import Vue from 'vue'
import Constant from '@/assets/js/Constant'
import axios from '@/utils/request'

function logout() {

}

function exit(code) {

}

/**
 * 统一处理成功回应
 */
function handleSuccessResponse(holder, data) {
    var response = {
        body: data
    }
    holder.onResult(true, response);
}

/**
 * 统一处理失败回应
 */
function handleFailResponse(holder, xhr, textStatus, errorThrown) {
    var data = {
        xhr: xhr,
        textStatus: textStatus,
        error: errorThrown
    }
    holder.onResult(false, data);
}

export var RequestHelper = {
    /**
     * 获取操作员列表
     */
    getControllerListFromWeb(holder) {
        let api = Constant.serverUrl + Constant.api.getControllerList + "?page=" + holder.page + "&size=" +
            holder.size + "&sort=opId,desc";
        let that = this;
        axios.get(api).then(success => {
            handleSuccessResponse(holder, success.data);
        }).catch(error => {
            handleFailResponse(holder, error);
        });
    },
    /**
     * 删除操作员接口
     */
    deleteOperator2Web(holder) {
        let api = Constant.serverUrl + Constant.api.deleteOperator;
        let that = this;
        axios.delete(api, {
            data: {
                opId: holder.opId,
            }
        }).then(success => {
            handleSuccessResponse(holder, success.data);
        }).catch(error => {
            handleFailResponse(holder, error);
        });
    },
    /**
     * 改变操作员的状态 激活 非激活
     */
    changeOperatorStatusFromWeb(holder) {
        let api = Constant.serverUrl + Constant.api.changeOperatorStatus;
        let that = this;
        axios.post(api, {
            opId: holder.opId,
            status: holder.status
        }).then(success => {
            handleSuccessResponse(holder, success.data);
        }).catch(error => {
            handleFailResponse(holder, error);
        });
    },
    /**
     * 添加操作员
     */
    addOperatorToWeb(holder) {
        let api = Constant.serverUrl + Constant.api.deleteOperator;
        let that = this;
        let mobile;
        if (holder.mobile) {
            mobile = holder.mobile;
        } else {
            mobile = "";
        }
        let opId
        if (holder.opId) {
            opId = holder.opId;
        } else {
            opId = "";
        }
        let file;
        if (holder.file) {
            file = holder.file;
        } else {
            // 构造一个空的文件
            file = new File([], "", {
                type: "file",
            });
        }
        const data = new FormData();
        data.append("opId", opId);
        data.append("loginName", holder.loginName);
        data.append("name", holder.name);
        data.append("email", holder.email);
        data.append("mobile", mobile);
        data.append("roles", holder.roles);
        data.append("orgId", holder.orgId);
        data.append("pictureFile", file);
        data.append("language", holder.language);
        data.append("password", holder.password);
        axios.post(api, data).then(success => {
            handleSuccessResponse(holder, success.data);
        }).catch(error => {
            handleFailResponse(holder, error);
        });
    },
    /**
     * 更改操作员密码
     */
    changeOperatorPwdFromWeb(holder) {
        let api = Constant.serverUrl + Constant.api.changeOperatorPwd;
        let that = this;
        axios.post(api, {
            opId: holder.opId,
            password: holder.password,
            passwordFirst: holder.passwordFirst
        }).then(success => {
            handleSuccessResponse(holder, success.data);
        }).catch(error => {
            handleFailResponse(holder, error);
        });
    },
    /**
     * 搜索操作员
     */
    searchOperatorFromWeb(holder) {
        let api = Constant.serverUrl + Constant.api.searchOperator + "?sort=opId,desc&size=" + holder.size +
            "&page=" + holder.page + "&loginName=" + holder.loginName;
        let that = this;
        axios.get(api).then(success => {
            handleSuccessResponse(holder, success.data);
        }).catch(error => {
            handleFailResponse(holder, error);
        });
    },
    /**
     * 获取角色列表
     */
    getRoleListFromWeb(holder) {
        let name;
        if (!holder.name) {
            name = ""
        } else {
            name = holder.name;
        }
        let api = `${Constant.serverUrl + Constant.api.getRolesList}?sort=id,desc&size=${holder.number}&page=${holder.page}&name=${name}`;
        let that = this;
        axios.get(api).then(success => {
            handleSuccessResponse(holder, success.data);
        }).catch(error => {
            handleFailResponse(holder, error);
        });
    },
    searchRoleByName(holder) {
        let api = `${Constant.serverUrl + Constant.api.getRolesList}?sort=id,desc&size=${holder.number}&page=${holder.page}&name=${holder.name}`;
        let that = this;
        axios.get(api).then(success => {
            handleSuccessResponse(holder, success.data);
        }).catch(error => {
            handleFailResponse(holder, error);
        });
    },
    /**
     * 验证操作员是否存在
     */
    isRoleExistFromWeb(holder) {
        let api = `${Constant.serverUrl + Constant.api.isRoleExit}?value=${holder.name}`;
        let that = this;
        axios.get(api).then(success => {
            handleSuccessResponse(holder, success.data);
        }).catch(error => {
            handleFailResponse(holder, error);
        });
    },
    /**
     * 增加角色
     * @desc 描述
     * @name 名称
     * @resIds 权限
     */
    editRole2Web(holder) {
        let api = Constant.serverUrl + Constant.api.addRole;
        let that = this;
        axios.post(api, {
            id: holder.id,
            desc: holder.desc,
            name: holder.name,
        }).then(success => {
            handleSuccessResponse(holder, success.data);
        }).catch(error => {
            handleFailResponse(holder, error);
        });
    },
    /**
     * 增加角色
     * @desc 描述
     * @name 名称
     * @resIds 权限
     */
    addRole2Web(holder) {
        let api = Constant.serverUrl + Constant.api.addRole;
        let that = this;
        axios.post(api, {
            id: holder.id,
            desc: holder.desc,
            name: holder.name,
            objRtpe: "role",
            resIds: holder.resIds,
            resType: "menu"
        }).then(success => {
            handleSuccessResponse(holder, success.data);
        }).catch(error => {
            handleFailResponse(holder, error);
        });
    },
    /**
     * 删除角色
     */
    deleteRole2Web(holder) {
        let api = Constant.serverUrl + Constant.api.addRole;
        let that = this;
        axios.delete(api, {
            data: {
                id: holder.id,
            }
        }).then(success => {
            handleSuccessResponse(holder, success.data);
        }).catch(error => {
            handleFailResponse(holder, error);
        });
    },
    /**
     * 获取权限列表
     */
    getPermissionListFromWeb(holder) {
        let api = `${Constant.serverUrl + Constant.api.getPermissionList}/${holder.roleId}.action?id=${holder.id}`;
        let that = this;
        axios.get(api).then(success => {
            handleSuccessResponse(holder, success.data);
        }).catch(error => {
            handleFailResponse(holder, error);
        });
    },
    /**
     * 保存权限树
     * @objId 角色id
     */
    savePermission2Web(holder) {
        let api = Constant.serverUrl + Constant.api.savePermission;
        let that = this;
        axios.post(api, {
            objId: holder.objId,
            objType: "role",
            resIds: holder.resIds,
            resType: "menu",
        }).then(success => {
            handleSuccessResponse(holder, success.data);
        }).catch(error => {
            handleFailResponse(holder, error);
        });
    },
    /**
     * 获取组织列表
     */
    getOrganizationListFromWeb(holder) {
        let api = Constant.serverUrl + Constant.api.getOrganizationTreeList + "?id=" + holder.id;
        let that = this;
        axios.get(api).then(success => {
            handleSuccessResponse(holder, success.data);
        }).catch(error => {
            handleFailResponse(holder, error);
        });
    },
    /**
     * 通过组织id获取该组织的子组织
     * @parentId 父id
     */
    getSonOrganizationFromWeb(holder) {
        let parentId;
        if (holder.parentId) {
            parentId = holder.parentId;
        } else {
            parentId = "";
        }
        let api = `${Constant.serverUrl + Constant.api.getOrganizationList}?page=${holder.page}&size=${holder.size}&parentId=${parentId}`;
        let that = this;
        axios.get(api).then(success => {
            handleSuccessResponse(holder, success.data);
        }).catch(error => {
            handleFailResponse(holder, error);
        });
    },
    /**
     * 编辑组织
     */
    editOrganizationFromWeb(holder) {
        let orgId;
        if (holder.orgId) {
            orgId = holder.orgId;
        } else {
            orgId = "";
        }
        let api = Constant.serverUrl + Constant.api.editOrganization;
        let that = this;
        axios.post(api, {
            code: holder.code,
            name: holder.name,
            orgId: orgId,
            parentId: holder.parentId,
        }).then(success => {
            handleSuccessResponse(holder, success.data);
        }).catch(error => {
            handleFailResponse(holder, error);
        });
    },
    /**
     * 增加组织
     */
    addOrganizationFromWeb(holder) {
        this.editOrganizationFromWeb(holder);
    },
    /**
     * 删除组织
     */
    deleteOrganizationById2Web(holder) {
        let api = Constant.serverUrl + Constant.api.editOrganization;
        let that = this;
        axios.delete(api, {
            data: {
                orgId: holder.orgId,
            }
        }).then(success => {
            handleSuccessResponse(holder, success.data);
        }).catch(error => {
            handleFailResponse(holder, error);
        });
    },
    /**
     * 从网络上获取菜单树
     */
    getMenuTreeFromWeb(holder) {
        let id;
        if (holder.id) {
            id = holder.id;
        } else {
            id = "";
        }
        let api = `${Constant.serverUrl + Constant.api.getMenuTreeList}?id=${id}`;
        let that = this;
        axios.get(api).then(success => {
            handleSuccessResponse(holder, success.data);
        }).catch(error => {
            handleFailResponse(holder, error);
        });
    },
    /**
     * 通过id获取当前menu
     */
    getMenuListByIdFromWeb(holder) {
        let parentId;
        if (holder.parentId) {
            parentId = holder.parentId;
        } else {
            parentId = "1";
        }
        let api = `${Constant.serverUrl + Constant.api.getMenuListByMenuId}?parentId=${parentId}&page=${holder.page}&size=${holder.size}&sort=orderNumber`;
        let that = this;
        axios.get(api).then(success => {
            handleSuccessResponse(holder, success.data);
        }).catch(error => {
            handleFailResponse(holder, error);
        });
    },
    /**
     * 编辑菜单
     */
    editMenuById2Web(holder) {
        let api = Constant.serverUrl + Constant.api.editMenu;
        let that = this;
        axios.post(api, {
            href: holder.href,
            icon: holder.icon,
            leaf: holder.leaf,
            menuId: holder.menuId,
            orderNumber: holder.orderNumber,
            parentId: holder.parentId,
            parentId_new: holder.parentId_new,
            title: holder.title
        }).then(success => {
            handleSuccessResponse(holder, success.data);
        }).catch(error => {
            handleFailResponse(holder, error);
        });
    },
    /**
     * 获取菜单 用于登录时 通过用户信息返回的菜单
     */
    getMenuFromWeb(holder) {
        let api = Constant.api.getMenu;
        let that = this;
        axios.get(api).then(success => {
            handleSuccessResponse(holder, success.data);
        }).catch(error => {
            handleFailResponse(holder, error);
        });
    },
    /**
     * 获取目录
     */
    getDirs(holder) {
        let api = holder.url;
        let that = this;
        axios.get(api).then(success => {
            handleSuccessResponse(holder, success.data);
        }).catch(error => {
            handleFailResponse(holder, error);
        });
    },
    /**
     * 获取文件
     */
    getFilesByDir(holder) {
        let api = holder.url;
        axios.get(api).then(success => {
            handleSuccessResponse(holder, success.data);
        }).catch(error => {
            handleFailResponse(holder, error);
        });
    }
};



function install(Vue) {
    Vue.prototype.$request = RequestHelper;
};

export default install;


