<template>
    <el-tabs class="main-container gray-bg" v-model="activeName" type="card" @tab-click="handleClick">
        <el-tab-pane style="" label="图" name="first">
            <chart />
        </el-tab-pane>
        <el-tab-pane label="表" name="second">
            <view-table/>
        </el-tab-pane>
        <el-tab-pane label="呼入绩效" name="third">
            <call-in/>
        </el-tab-pane>
    </el-tabs>
</template>
<script>
import ViewTable from "@/business/PerformanceCount/view/ViewTable";
import CallIn from "@/business/PerformanceCount/view/CallInPerformance";
import Chart from "@/business/PerformanceCount/view/Chart";

export default {
    components: {
        ViewTable,
        CallIn,
        Chart
    },
    data() {
        return {
            activeName: "first"
        };
    },
    methods: {
        handleClick(tab, event) {
            console.log(tab, event);
        }
    }
};
</script>


