<template>
    <div class="main-container gray-bg">
        <el-form ref="form" size="mini" :model="form" label-position="right" label-width="120px">
            <el-row :gutter="10">
                <el-col :span="8">
                    <el-form-item label="业务号：">
                        <el-input v-model="form.userId" placeholder="请输入"></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="8">
                    <el-form-item label="用户姓名：">
                        <el-input v-model="form.userName" placeholder="请输入"></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="8">
                    <el-form-item label="查分状态：">
                        <el-select v-model="form.status" placeholder="全部活动">
                            <el-option label="区域一" value="shanghai"></el-option>
                            <el-option label="区域二" value="beijing"></el-option>
                        </el-select>
                    </el-form-item>
                </el-col>
            </el-row>

            <el-row :gutter="10">
                <el-col :span="8">
                    <el-form-item label="电话：">
                        <el-input v-model="form.phone" placeholder="请输入"></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="8">
                    <el-form-item label="设备号：">
                        <el-input v-model="form.deviceId" placeholder="请输入"></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="8">
                    <el-form-item label="CRMID：">
                        <el-input v-model="form.crmId" placeholder="请输入"></el-input>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row :gutter="10">
                <el-col :span="8">
                    <el-form-item label="筛选失败原因：">
                        <el-input v-model="form.reason" placeholder="请输入"></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="8">
                    <el-form-item label="筛选状态：">
                        <el-input v-model="form.status" placeholder="请输入"></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="8">
                    <el-form-item>
                        <el-button type="primary">&nbsp;查询&nbsp;</el-button>
                    </el-form-item>
                </el-col>
            </el-row>
        </el-form>
        <div style="margin-bottom:8px;">
            <el-upload style="display:inline-block;" class="upload-demo" accept=".xlsx" action="https://jsonplaceholder.typicode.com/posts/">
                <el-button size="mini">导入用户</el-button>
            </el-upload>
            <el-button size="mini" type="primary" style="margin-left:5%;">积分查询</el-button>
        </div>

        <el-table :data="tableData" v-loading="isLoading" style="width: 100%">
            <el-table-column label="业务号" align="center" show-overflow-tooltip>
                <template slot-scope="scope">
                    <span>{{ scope.row.userId }}</span>
                </template>
            </el-table-column>
            <!--用户类型筛选-->
            <el-table-column label="用户类型" align="center" show-overflow-tooltip>
                <template slot-scope="scope">
                    <span>{{ scope.row.userType}}</span>
                </template>
            </el-table-column>
            <el-table-column label="用户姓名" align="center" show-overflow-tooltip>
                <template slot-scope="scope">
                    <span>{{ scope.row.userName}}</span>
                </template>
            </el-table-column>
            <el-table-column label="电话" align="center" show-overflow-tooltip>
                <template slot-scope="scope">
                    <span>{{ scope.row.phone}}</span>
                </template>
            </el-table-column>

            <el-table-column label="设备号" align="center" show-overflow-tooltip>
                <template slot-scope="scope">
                    <span>{{ scope.row.deviceId}}</span>
                </template>
            </el-table-column>
            <el-table-column label="积分" align="center" show-overflow-tooltip>
                <template slot-scope="scope">
                    <span>{{ scope.row.point }}</span>
                </template>
            </el-table-column>
            <el-table-column label="CRMID" align="center" show-overflow-tooltip>
                <template slot-scope="scope">
                    <span>{{ scope.row.smsContent }}</span>
                </template>
            </el-table-column>
            <el-table-column label="高标清" align="center" show-overflow-tooltip>
                <template slot-scope="scope">
                    <span>{{ scope.row.result }}</span>
                </template>
            </el-table-column>
            <!--筛选-->
            <el-table-column label="筛选" align="center" show-overflow-tooltip>
                <template slot-scope="scope">
                    <span>{{ scope.row.result }}</span>
                </template>
            </el-table-column>
            <!--筛选结果筛选-->
            <el-table-column label="筛选结果" align="center" show-overflow-tooltip>
                <template slot-scope="scope">
                    <span>{{ scope.row.result }}</span>
                </template>
            </el-table-column>
            <el-table-column label="筛选失败原因" align="center" show-overflow-tooltip>
                <template slot-scope="scope">
                    <span>{{ scope.row.result }}</span>
                </template>
            </el-table-column>
            <el-table-column label="联系地址" align="center" show-overflow-tooltip>
                <template slot-scope="scope">
                    <span>{{ scope.row.result }}</span>
                </template>
            </el-table-column>
            <el-table-column label="筛选时间" align="center" show-overflow-tooltip>
                <template slot-scope="scope">
                    <span>{{ scope.row.result }}</span>
                </template>
            </el-table-column>
            <el-table-column label="操作" align="center" show-overflow-tooltip>
                <template slot-scope="scope">
                    <el-button type="text">加入黑名单</el-button>
                </template>
            </el-table-column>
        </el-table>
        <div style="float:right;">
            <el-pagination class="layout-pagination" @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="pageInfo.number" :page-sizes="pageInfo.sizeArray" :page-size="pageInfo.currentSize" layout="total, sizes, prev, pager, next, jumper" :total="pageInfo.totalElements">
            </el-pagination>
        </div>
        <div style="clear:both;"></div>
    </div>
</template>

<script>
export default {
    components: {},
    props: {},
    data() {
        return {
            form: {},
            tableData: [],
            pageInfo: {
                currentSize: 10,
                sizeArray: [10, 20, 50, 100],
                number: 1,
                totalElements: 0
            },
            isLoading: false
        };
    },

    created() {},

    methods: {
        /**
         * 获取基础产品列表
         */
        getBaseProductListFromWeb() {
            this.isLoading = true;
            let that = this;
            this.$baseProductRequest.getBaseProductList({
                page: this.pageInfo.number - 1,
                size: this.pageInfo.currentSize,
                baseProduct: this.formInline,
                onResult(isSuccess, data) {
                    if (isSuccess) {
                        that.handleGetBaseProductListSuccessResponse(data);
                    } else {
                    }
                    that.isLoading = false;
                }
            });
        },
        handleGetBaseProductListSuccessResponse(data) {
            this.handlePageInfo(data);
            this.baseProductList = data.body;
        },
        /**
         * 将page 数据赋值
         */
        handlePageInfo(data) {
            this.pageInfo.totalElements = data.page.totalElements;
            console.log(this.pageInfo);
        },
        /**
         * 改变了每页数据
         */
        handleSizeChange(val) {
            console.log(`每页 ${val} 条`);
            this.pageInfo.currentSize = val;
            this.getBaseProductListFromWeb();
        },
        /**
         * 处理当前的改变
         */
        handleCurrentChange(val) {
            console.log(`当前页: ${val}`);
            this.pageInfo.number = val;
            this.getBaseProductListFromWeb();
        },
        /**
         * 重置 pageInfo
         */
        resetPageInfo() {
            this.pageInfo.currentSize = this.pageInfo.sizeArray[0];
            this.pageInfo.number = 1;
        },
        searchDialRecord() {}
    },

    watch: {}
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
</style>



