const baseProduct = {
    state: {
        showMutexProduct: false,
        showAddBaseProduct: false
    },
    mutations: {
        SET_SHOW_MUTEXPRODUCT(state, data) {
            state.showMutexProduct = data;
        },
        SET_SHOW_ADDBASEPRODUCT(state, data) {
            state.showAddBaseProduct = data;
        }
    },
    actions: {}
};
export default baseProduct;


