<template>
    <el-dialog :visible.sync="isCurrentShow" width="90%" :before-close="handleClose" :close-on-click-modal="false">
        <el-form ref="form" size="mini" :model="form" label-width="100px" label-position="right">
            <el-row :gutter="3">
                <el-col :span="8">
                    <el-form-item label="业务号:">
                        <el-input v-model="form.userId" placeholder="请输入"></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="8">
                    <el-form-item label="用户姓名:">
                        <el-input v-model="form.userName" placeholder="请输入"></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="8">
                    <el-form-item label="网龄申请:">
                        <el-select v-model="form.productName" placeholder="请选择">
                            <el-option v-for="product in productList" :label="product.title" :value="product.title" :key="product.productCode"></el-option>
                        </el-select>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row :gutter="3">
                <el-col :span="8">
                    <el-form-item label="电话/手机号:">
                        <el-input v-model="form.phone" placeholder="请输入"></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="8">
                    <el-form-item label="设备号:">
                        <el-input v-model="form.deviceId" placeholder="请输入"></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="8">
                    <el-form-item label="支付方式：">
                        <el-select v-model="form.payType" placeholder="请选择">
                            <el-option v-for="payType in payTypeFilters" :label="payType.text" :value="payType.value" :key="payType.productCode"></el-option>
                        </el-select>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row :gutter="3">
                <el-col :span="8">
                    <el-form-item label="客服姓名:">
                        <el-select v-model="form.payType" placeholder="请选择">
                            <el-option v-for="payType in payTypeFilters" :label="payType.text" :value="payType.value" :key="payType.productCode"></el-option>
                        </el-select>
                    </el-form-item>
                </el-col>
                <el-col :span="8">
                    <el-form-item>
                    </el-form-item>
                </el-col>
                <el-col :span="8">
                    <el-form-item>
                        <el-button type="primary" @click="searchOrderList">查询</el-button>
                    </el-form-item>
                </el-col>
            </el-row>
        </el-form>
        <el-button @click="batchOrder" size="mini" style="margin-bottom:8px;">批量订购</el-button>
        <el-table ref="multipleTable" :data="orderList" v-loading="isLoading" tooltip-effect="dark" style="width: 100%" @selection-change="handleSelectionChange">
            <el-table-column type="selection" width="55">
            </el-table-column>
            <el-table-column label="业务号">
                <template slot-scope="scope">{{ scope.row.userId }}</template>
            </el-table-column>
            <el-table-column prop="userName" label="用户姓名" align="center">
            </el-table-column>
            <el-table-column prop="phone" label="电话" align="center" show-overflow-tooltip>
            </el-table-column>
            <el-table-column prop="phone" label="手机号码" align="center">
            </el-table-column>
            <el-table-column prop="deviceId" label="设备号" align="center">
            </el-table-column>
            <el-table-column prop="crmId" label="CRMID" align="center">
            </el-table-column>
            <!--支付方式筛选-->
            <el-table-column label="支付方式" align="center" show-overflow-tooltip>
                <template slot-scope="scope">
                    <span>{{scope.row.payType | payTypeFilter}}</span>
                </template>
            </el-table-column>
            <el-table-column prop="point" label="用户积分" align="center">
            </el-table-column>
            <el-table-column prop="payPoint" label="支付积分" align="center">
            </el-table-column>
            <el-table-column prop="payPrice" label="出账" align="center">
            </el-table-column>
            <el-table-column prop="orderCycleCount" label="订购月数" align="center">
            </el-table-column>
            <!--客服姓名筛选-->
            <el-table-column prop="opName" label="客服姓名" align="center">
            </el-table-column>
            <el-table-column prop="name" label="网龄申请" align="center">
            </el-table-column>
            <el-table-column prop="failedReason" label="申请失败原因" align="center">
            </el-table-column>
            <el-table-column prop="productName" label="产品" align="center">
            </el-table-column>
            <el-table-column prop="spCode" label="用户按键" align="center">
            </el-table-column>
            <el-table-column prop="createTime" label="营销时间" align="center">
            </el-table-column>
            <el-table-column label="操作" align="center">
                <template slot-scope="scope">
                    <el-button type="text" style="color:#108EE9;">审核</el-button>
                </template>
            </el-table-column>
        </el-table>
        <div style="float:right;">
            <el-pagination class="layout-pagination" @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="pageInfo.number" :page-sizes="pageInfo.sizeArray" :page-size="pageInfo.currentSize" layout="total, sizes, prev, pager, next, jumper" :total="pageInfo.totalElements">
            </el-pagination>
        </div>
        <div style="clear:both;"></div>
    </el-dialog>
</template>
<script>
import Constant from "@/assets/js/Constant";
export default {
    data() {
        return {
            payTypeFilters: [],
            opNameFilters: [],
            form: {},
            activityId: "",
            orderList: [
                {
                    activityId: 0,
                    activityName: "string",
                    address: "string",
                    autoOpen: 0,
                    createTime: "2018-08-28T07:48:30.778Z",
                    crmId: "string",
                    deviceId: "string",
                    id: 0,
                    opId: 0,
                    opName: "string",
                    orderCycleCount: 0,
                    orderNo: "string",
                    payPoint: 0,
                    payPrice: 0,
                    payType: 0,
                    phone: "string",
                    point: 0,
                    productId: 0,
                    productName: "string",
                    spCode: "string",
                    status: 0,
                    unorderFlag: 0,
                    unorderSource: "string",
                    unorderTime: "2018-08-28T07:48:30.778Z",
                    userId: "string",
                    userName: "string",
                    userType: 0
                }
            ],
            multipleSelection: [],
            pageInfo: {
                currentSize: 10,
                sizeArray: [10, 20, 50, 100],
                number: 1,
                totalElements: 0
            },
            isLoading: false
        };
    },
    computed: {
        isCurrentShow() {
            return this.$store.state.reviewAndOrder.showReviewAndOrder;
        }
    },
    created() {
        this.activityId = this.$route.query.activityId;
        this.payTypeFilters = Constant.payTypeList;
        this.getOrderListFromWeb();
    },
    methods: {
        /**关闭弹窗 */
        handleClose() {
            this.$store.commit("SET_SHOW_REVIEWANDORDER", false);
        },
        /**查询订单列表 */
        getOrderListFromWeb() {
            //this.isLoading = true;
            let that = this;
            this.$orderRequest.orderList({
                activityId: this.activityId,
                userId: this.form.userId,
                userName: this.form.userName,
                // 网龄申请
                phone: this.form.phone,
                deviceId: this.form.deviceId,
                page: this.pageInfo.number,
                size: this.pageInfo.currentSize,
                onResult(isSuccess, data) {
                    if (isSuccess) {
                        that.handlePageInfo(data);
                        that.orderList = data.result;
                    } else {
                    }
                    that.isLoading = false;
                }
            });
        },
        // /**支付方式筛选 */
        // payTypeFilter(value, row) {
        //     return row.payType === value;
        // },
        // /**客服姓名筛选 */
        // opNameFilter(value, row) {
        //     return row.opName === value;
        // },
        // 表单查询订单列表
        searchOrderList() {
            this.resetPageInfo();
            this.getOrderListFromWeb();
        },
        // 批量订购
        batchOrder() {},
        // 列表勾选事件
        handleSelectionChange(val) {
            this.multipleSelection = val;
        },
        /**
         * 将page 数据赋值
         */
        handlePageInfo(data) {
            this.pageInfo.totalElements = data.page.totalElements;
            console.log(this.pageInfo);
        },
        /**
         * 改变了每页数据
         */
        handleSizeChange(val) {
            console.log(`每页 ${val} 条`);
            this.pageInfo.currentSize = val;
            this.getBaseProductListFromWeb();
        },
        /**
         * 处理当前的改变
         */
        handleCurrentChange(val) {
            console.log(`当前页: ${val}`);
            this.pageInfo.number = val;
            this.getBaseProductListFromWeb();
        },
        /**
         * 重置 pageInfo
         */
        resetPageInfo() {
            this.pageInfo.currentSize = this.pageInfo.sizeArray[0];
            this.pageInfo.number = 1;
        }
    },
    filters: {
        payTypeFilter(payType) {
            return Constant.payTypeMap[payType];
        }
    }
};
</script>




