const product = {
    state: {
        showAddProduct: false,
        showModifyProduct: false,
        productList: [],
        product: {},
        payTypeList: []
    },
    mutations: {
        SET_SHOW_ADDPRODUCT(state, data) {
            state.showAddProduct = data;
        },
        SET_SHOW_MODIFYPRODUCT(state, data) {
            state.showModifyProduct = data;
        },
        SET_PRODUCT_LIST(state, data) {
            state.productList = data;
        },
        SET_PRODUCT(state, data) {
            state.product = data;
        },
        SET_PAYTYPE_LIST(state, data) {
            state.payTypeList = data;
        }
    },
    actions: {}
};
export default product;


