import Vue from 'vue'
import axios from 'axios'
import Constant from '@/assets/js/Constant'

function logout() {

}

function exit(code) {

}

/**
 * 统一处理成功回应
 */
function handleSuccessResponse(holder, data) {
  holder.onResult(true, data);
}

/**
 * 统一处理失败回应 
 */
function handleFailResponse(holder, xhr, textStatus, errorThrown) {
  var data = {
    xhr: xhr,
    textStatus: textStatus,
    error: errorThrown
  }
  holder.onResult(false, data);
}

export var RequestHelper = {
  /**
   * 查询营销活动列表
   */
  getActivityList(holder) {
    let api = Constant.serverUrl + Constant.api.activity.getActivityList +
      "?page=" + holder.page + "&size=" + holder.size + "&payType=" + holder.payType + "&productName=" + holder.productName;
    let that = this;
    axios.get(api).then(success => {
      handleSuccessResponse(holder, success.data);
    }).catch(error => {
      handleFailResponse(holder, error);
    });
  },
  /**
   *查询营销活动信息
   */
  getActivity(holder) {
    let api = Constant.serverUrl + Constant.api.activity.getActivity +
      holder.activityId;
    let that = this;
    axios.get(api).then(success => {
      handleSuccessResponse(holder, success.data);
    }).catch(error => {
      handleFailResponse(holder, error);
    });
  },
  /**
   *删除营销活动
   */
  deleteActivity(holder) {
    if (Constant.api.activity.mock)
      return
    let api = Constant.serverUrl + Constant.api.activity.deleteActivity +
      holder.activityId;
    let that = this;
    axios.post(api).then(success => {
      handleSuccessResponse(holder, success.data);
    }).catch(error => {
      handleFailResponse(holder, error);
    });
  },
  /**
   *添加营销活动
   */
  addActivity(holder) {
    let api = Constant.serverUrl + Constant.api.activity.addActivity;
    let that = this;
    axios.post(api, {
      activity: holder.activity,
      products: holder.products
    }).then(success => {
      handleSuccessResponse(holder, success.data);
    }).catch(error => {
      handleFailResponse(holder, error);
    });
  },
  /**
   *领取营销任务
   */
  getMission(holder) {
    let api = Constant.serverUrl + Constant.api.activity.getMission +
      holder.activityId + "?opId=" + holder.opId;
    let that = this;
    axios.post(api).then(success => {
      handleSuccessResponse(holder, success.data);
    }).catch(error => {
      handleFailResponse(holder, error);
    });
  },
  /**
   *给营销活动分配坐席
   */
  sendUser(holder) {
    let api = Constant.serverUrl + Constant.api.activity.sendUser +
      holder.activityId;
    let that = this;
    axios.post(api, holder.users).then(success => {
      handleSuccessResponse(holder, success.data);
    }).catch(error => {
      handleFailResponse(holder, error);
    });
  },
  /**
   *删除营销活动已经分配的部分/全部坐席
   */
  deleteOp(holder) {
    let api = Constant.serverUrl + Constant.api.activity.deleteOp +
      holder.activityId;
    let that = this;
    axios.post(api, holder.users).then(success => {
      handleSuccessResponse(holder, success.data);
    }).catch(error => {
      handleFailResponse(holder, error);
    });
  },
  /**
   *为营销活动导入用户
   */
  importUser(holder) {
    if (Constant.api.activity.mock)
      return
    let api = Constant.serverUrl + Constant.api.activity.importUser +
      holder.activityId + "?file=" + holder.file;
    let that = this;
    axios.post(api).then(success => {
      handleSuccessResponse(holder, success.data);
    }).catch(error => {
      handleFailResponse(holder, error);
    });
  },
  /**
   *查询坐席的营销活动
   */
  myActivities(holder) {
    let api = Constant.serverUrl + Constant.api.activity.myActivities +
      "?activityName=" + holder.activityName + "&opId=" + holder.opId;
    let that = this;
    axios.get(api).then(success => {
      handleSuccessResponse(holder, success.data);
    }).catch(error => {
      handleFailResponse(holder, error);
    });
  },
  /**
   *获取下一个营销用户
   */
  nextUser(holder) {
    let api = Constant.serverUrl + Constant.api.activity.nextUser +
      holder.activityId + "?opId=" + holder.opId;
    let that = this;
    axios.post(api).then(success => {
      handleSuccessResponse(holder, success.data);
    }).catch(error => {
      handleFailResponse(holder, error);
    });
  },
  /**
   *查询营销活动已分配坐席
   */
  op(holder) {
    let api = Constant.serverUrl + Constant.api.activity.op +
      holder.activityId;
    let that = this;
    axios.get(api).then(success => {
      handleSuccessResponse(holder, success.data);
    }).catch(error => {
      handleFailResponse(holder, error);
    });
  },
  /**
   *查询营销活动已分配坐席
   */
  waitOp(holder) {
    let api = Constant.serverUrl + Constant.api.activity.waitOp +
      holder.activityId;
    let that = this;
    axios.get(api).then(success => {
      handleSuccessResponse(holder, success.data);
    }).catch(error => {
      handleFailResponse(holder, error);
    });
  },

  /**
   *更新营销活动
   */
  update(holder) {
    let api = Constant.serverUrl + Constant.api.activity.update +
      holder.activityId;
    let that = this;
    axios.post(api, {
      activity: holder.activity,
      products: holder.products
    }).then(success => {
      handleSuccessResponse(holder, success.data);
    }).catch(error => {
      handleFailResponse(holder, error);
    });
  },

  /**
   *更新营销活动自动更新状态
   */
  updateAutoOpen(holder) {
    let api = Constant.serverUrl + Constant.api.activity.updateAutoOpen +
      holder.activityId + "?autoOpen=" + holder.autoOpen;
    let that = this;
    axios.get(api).then(success => {
      handleSuccessResponse(holder, success.data);
    }).catch(error => {
      handleFailResponse(holder, error);
    });
  },

  /**
   *更新营销活动上线状态
   */
  updateOnlineStatus(holder) {
    let api = Constant.serverUrl + Constant.api.activity.updateOnlineStatus +
      holder.activityId + "?onlineStatus=" + holder.onlineStatus;
    let that = this;
    axios.get(api).then(success => {
      handleSuccessResponse(holder, success.data);
    }).catch(error => {
      handleFailResponse(holder, error);
    });
  },

  /**
   *获取有效的营销活动
   */
  validList(holder) {
    let api = Constant.serverUrl + Constant.api.activity.validList;
    let that = this;
    axios.get(api).then(success => {
      handleSuccessResponse(holder, success.data);
    }).catch(error => {
      handleFailResponse(holder, error);
    });
  },
  /**
   * 获取某活动绑定的产品
   * @param {*} holder 
   */
  getProductsByActivityId(holder) {
    let api = Constant.serverUrl + Constant.api.activity.getProductsByActivityId +
      holder.activityId;
    let that = this;
    axios.get(api).then(success => {
      handleSuccessResponse(holder, success.data);
    }).catch(error => {
      handleFailResponse(holder, error);
    });
  }
};



function install(Vue) {
  Vue.prototype.$activityRequest = RequestHelper;
};

export default install;


