import Vue from 'vue'
import axios from 'axios'
import Constant from '@/assets/js/Constant'

function logout() {

}

function exit(code) {

}

/**
 * 统一处理成功回应
 */
function handleSuccessResponse(holder, data) {
  holder.onResult(true, data);
}

/**
 * 统一处理失败回应
 */
function handleFailResponse(holder, xhr, textStatus, errorThrown) {
  var data = {
    xhr: xhr,
    textStatus: textStatus,
    error: errorThrown
  }
  holder.onResult(false, data);
}

export var RequestHelper = {
  /**
   * 查询呼叫记录列表
   */
  callRecordList(holder) {
    if (Constant.api.callRecord.mock) return
    let api = Constant.serverUrl + Constant.api.callRecord.callRecordList +
      "?page=" + holder.page + "&size=" + holder.size;
    let that = this;
    axios.get(api).then(success => {
      handleSuccessResponse(holder, success.data);
    }).catch(error => {
      handleFailResponse(holder, error);
    });
  },
  /**
   * 创建呼叫记录
   */
  addCallRecord(holder) {
    let api = Constant.serverUrl + Constant.api.callRecord.addCallRecord;
    let that = this;
    axios.post(api, holder.callRecord).then(success => {
      handleSuccessResponse(holder, success.data);
    }).catch(error => {
      handleFailResponse(holder, error);
    });
  },
  /**
   * 查询坐席的某个营销活动的有意向拨打记录
   * @param {*} holder 
   */
  willCallRecordList(holder) {
    let api = Constant.serverUrl + Constant.api.callRecord.willCallRecordList +
      "?opId=" + holder.opId + "&activityId=" + holder.activityId;
    let that = this;
    axios.get(api).then(success => {
      handleSuccessResponse(holder, success.data);
    }).catch(error => {
      handleFailResponse(holder, error);
    });
  },
  /**
   * 查询最近的预约营销对象
   */
  getCurrentAppo(holder) {
    let api = Constant.serverUrl + Constant.api.callRecord.getCurrentAppo +
      "?opId=" + holder.opId;
    let that = this;
    axios.get(api).then(success => {
      handleSuccessResponse(holder, success.data);
    }).catch(error => {
      handleFailResponse(holder, error);
    });
  }
};



function install(Vue) {
  Vue.prototype.$callRecordRequest = RequestHelper;
};

export default install;


