import Vue from 'vue'
import axios from 'axios'

import Constant from '@/assets/js/Constant'

function logout() {

}

function exit(code) {

}

/**
 * 统一处理成功回应
 */
function handleSuccessResponse(holder, data) {
  holder.onResult(true, data);
}

/**
 * 统一处理失败回应
 */
function handleFailResponse(holder, xhr, textStatus, errorThrown) {
  var data = {
    xhr: xhr,
    textStatus: textStatus,
    error: errorThrown
  }
  holder.onResult(false, data);
}

export var RequestHelper = {
  /**
   * 查询基础产品列表
   */
  getBaseProductList(holder) {
    let api = Constant.serverUrl + Constant.api.baseProduct.getBaseProductList +
      "?number=" + holder.page + "&size=" + holder.size + "&name=" + holder.name + "&code=" + holder.code;
    let that = this;
    axios.get(api).then(success => {
      handleSuccessResponse(holder, success.data);
    }).catch(error => {
      handleFailResponse(holder, error);
    });
  },

  /**
   * 获取基础产品信息/{productId}
   */
  getBaseProductById(holder) {
    let api = Constant.serverUrl + Constant.api.baseProduct.getBaseProduct +
      holder.productId;
    let that = this;
    axios.get(api).then(success => {
      handleSuccessResponse(holder, success.data);
    }).catch(error => {
      handleFailResponse(holder, error);
    });
  },
  /**
   * 获取基础产品信息/{productCode}
   */
  getBaseProductByCode(holder) {
    let api = Constant.serverUrl + Constant.api.baseProduct.getBaseProductByCode +
      holder.productCode;
    let that = this;
    console.log(api);
    axios.get(api).then(success => {
      handleSuccessResponse(holder, success.data);
    }).catch(error => {
      handleFailResponse(holder, error);
    });
  },
  /**
   * 保存基础产品
   */
  saveBaseProduct(holder) {
    let api = Constant.serverUrl + Constant.api.baseProduct.saveBaseProduct;
    let that = this;
    axios.post(api, holder.baseProduct).then(success => {
      handleSuccessResponse(holder, success.data);
    }).catch(error => {
      handleFailResponse(holder, error);
    });
  },
  /**
   * 在线查询基础产品信息
   */
  searchBaseProduct(holder) {
    let api = Constant.serverUrl + Constant.api.baseProduct.searchBaseProduct +
      '?productId=' + holder.productId;
    let that = this;
    axios.get(api).then(success => {
      handleSuccessResponse(holder, success.data);
    }).catch(error => {
      handleFailResponse(holder, error);
    });
  },
  /**
   * 更新产品上线状态/{productId}
   */
  updateOnlineStatus(holder) {
    let api = Constant.serverUrl + Constant.api.baseProduct.updateOnlineStatus +
      holder.productId + "?onlineStatus=" + holder.onlineStatus;
    let that = this;
    axios.post(api).then(success => {
      handleSuccessResponse(holder, success.data);
    }).catch(error => {
      handleFailResponse(holder, error);
    });
  },
  /**
   * 查询所有生效的基础产品
   */
  getValidList(holder) {
    let api = Constant.serverUrl + Constant.api.baseProduct.getValidList;
    let that = this;
    axios.get(api).then(success => {
      handleSuccessResponse(holder, success.data);
    }).catch(error => {
      handleFailResponse(holder, error);
    });
  },
};



function install(Vue) {
  Vue.prototype.$baseProductRequest = RequestHelper;
};

export default install;


