<template>
    <div class="main-container gray-bg">
        <div style="margin-bottom:12px;">
            <el-button size="mini" @click="newTeam">新建小组
            </el-button>
        </div>
        <el-row :gutter="20">
            <el-col :span="6">
                <div class="ibox-content">
                    <el-table :data="teamList" style="width: 94%">
                        <el-table-column label="小组" align="center" show-overflow-tooltip>
                            <template slot-scope="scope">
                                <el-button type="text" @click="teamChange(scope.row)">
                                    {{scope.row.name}}
                                </el-button>
                            </template>
                        </el-table-column>
                        <el-table-column label="操作" align="center" show-overflow-tooltip>
                            <template slot-scope="scope">
                                <el-button size="mini" @click="sendTeam(scope.row)">分派小组
                                </el-button>
                            </template>
                        </el-table-column>
                    </el-table>
                </div>
            </el-col>

            <el-col :span="18">
                <div class="ibox-content">
                    <el-table :data="currentTeamMembers" style="width: 100%" row-key="userTeamMember.opId" :expand-row-keys="expands" @row-click="rowClick">
                        <el-table-column type="expand">
                            <template slot-scope="scope" v-if="scope.row.willCount==0">
                                <span style="height:100px;">该活动暂时没有意向用户</span>
                            </template>
                            <template slot-scope="scope" v-if="scope.row.willCount>0">
                                <el-table :data="callRecords[scope.row.userTeamMember.opId]" style="width: 90%">
                                    <el-table-column prop="userId" label="业务号" align="center" show-overflow-tooltip>
                                    </el-table-column>
                                    <el-table-column prop="userName" label="用户姓名" align="center" show-overflow-tooltip>
                                    </el-table-column>
                                    <el-table-column prop="userPhone" label="电话" align="center" show-overflow-tooltip>
                                    </el-table-column>
                                    <el-table-column prop="callTime" label="拨打日期" align="center" show-overflow-tooltip>
                                    </el-table-column>
                                    <el-table-column prop="remark" label="拨打备注" align="center" show-overflow-tooltip>
                                    </el-table-column>
                                    <el-table-column label="操作" align="center">
                                        <template slot-scope="scope">
                                            <el-button type="text" @click="callBackPage(scope.row)">呼叫</el-button>
                                        </template>
                                    </el-table-column>
                                </el-table>
                            </template>
                        </el-table-column>
                        <el-table-column label="客服姓名" align="center" show-overflow-tooltip>
                            <template slot-scope="scope">
                                <span>{{ scope.row.userTeamMember.opName }}</span>
                            </template>
                        </el-table-column>
                        <el-table-column label="待回呼用户（总）" align="center" show-overflow-tooltip>
                            <template slot-scope="scope">
                                <span>{{ scope.row.callBackCount }}</span>
                            </template>
                        </el-table-column>
                        <el-table-column label="操作" align="center" show-overflow-tooltip>
                            <template slot-scope="scope">
                                <el-button type="text" @click="setToLeave(scope.row)">设为离职</el-button>
                                <el-button type="text" @click="deleteMember(scope.row)" style="color:red">移出小组</el-button>
                            </template>
                        </el-table-column>
                    </el-table>
                    <div style="float:right;">
                        <el-pagination class="layout-pagination" @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="pageInfo.number" :page-sizes="pageInfo.sizeArray" :page-size="pageInfo.currentSize" layout="total, sizes, prev, pager, next, jumper" :total="pageInfo.totalElements">
                        </el-pagination>
                    </div>
                    <div style="clear:both"></div>
                </div>
            </el-col>
        </el-row>
        <add-team :users="addUsers" :leaders="leaders" />
        <!-- <send-team /> -->
        <call-back-page :callRecord="currentCallRecord" />
    </div>
</template>

<script>
import AddTeam from "@/business/teammanage/view/AddTeam";
// import SendTeam from "@/business/teammanage/view/SendTeam";
import CallBackPage from "@/business/customerServiceMission/view/CallWillPage";

export default {
    components: {
        AddTeam,
        // SendTeam,
        CallBackPage
    },
    props: {},
    data() {
        return {
            // 坐席待回呼拨打记录
            callRecords: {
                33: [
                    {
                        activityId: 0,
                        activityName: "string",
                        callTime: "2018-08-28T06:15:31.564Z",
                        failedReason: "string",
                        id: 0,
                        opId: 0,
                        opName: "string",
                        orderCycleCount: 0,
                        payPoint: 0,
                        payPrice: 0,
                        payType: 0,
                        productId: 0,
                        productName: "string",
                        remark: "string",
                        result: 0,
                        userId: "string",
                        userName: "string",
                        userPhone: "string"
                    }
                ]
            },
            teamList: [
                // 小组信息列表
                { id: 1, name: "张三组" },
                { id: 2, name: "李四组" },
                { id: 3, name: "王五组" }
            ],
            currentTeam: null, // 当前选中的小组
            currentTeamMembers: [
                //{userTeamMember:{opId:1,opName:""},callBackCount:1}
            ], // 当前展示的小组成员信息
            pageInfo: {
                currentSize: 10,
                sizeArray: [10, 20, 50, 100],
                number: 1,
                totalElements: 0
            },
            expands: [], // 展开的行
            currentCallRecord: {},
            addUsers: [], // 待分配的坐席
            leaders: [] // 组长人选
        };
    },
    computed: {},

    created() {
        this.getTeamListFromWeb();
    },

    methods: {
        /**获取小组信息列表 */
        getTeamListFromWeb() {
            let that = this;
            this.$teamManageRequest.getTeamList({
                onResult(isSuccess, data) {
                    if (isSuccess) {
                        if (data.status == 200) {
                            that.teamList = data.result;
                            that.currentTeam = data.result[0];
                        }
                    } else {
                    }
                }
            });
        },
        /**获取小组成员信息列表 */
        getTeamMembersFromWeb(team) {
            let that = this;
            this.$teamManageRequest.getTeamMembers({
                page: this.pageInfo.number - 1,
                size: this.pageInfo.currentSize,
                teamId: team.id,
                onResult(isSuccess, data) {
                    if (isSuccess) {
                        if (data.status == 200) {
                            that.currentTeamMembers = data.result;
                            that.handlePageInfo(data);
                        }
                    } else {
                    }
                }
            });
        },
        /**
         * 单击行展开
         */
        //在<table>里，我们已经设置row的key值设置为每行数据id：row-key="id"
        rowClick(row, event, column) {
            let that = this;
            // 判断是否有待回呼记录
            if (row.callBackCount === 0) {
                return;
            } else {
                if (this.expands.indexOf(row.userTeamMember.opId) < 0) {
                    // 有,获取待回呼记录
                    this.$teamManageRequest.getCallBackByOpId({
                        opId: row.userTeamMember.opId,
                        onResult(isSuccess, data) {
                            if (isSuccess) {
                                if (data.status == 200) {
                                    that.callRecords[row.userTeamMember.opId] =
                                        data.result;
                                }
                            } else {
                            }
                        }
                    });
                    this.expands.push(row.userTeamMember.opId);
                } else {
                    this.expands.remove(row.userTeamMember.opId);
                }
                Array.prototype.remove = function(val) {
                    let index = this.indexOf(val);
                    if (index > -1) {
                        this.splice(index, 1);
                    }
                };
            }
        },
        /**帮助客服回呼 */
        callBackPage(callRecord) {
            this.currentCallRecord = callRecord;
            this.$store.commit("SET_SHOW_CALLWILLPAGE", true);
        },
        /**查询可用的小组人选 */
        getLeadersFromWeb() {
            let that = this;
            this.leaders = [];
            this.addUsers = [];
            this.$teamManageRequest.getLeaders({
                onResult(isSuccess, data) {
                    if (isSuccess) {
                        if (data.status == 200 && data.result) {
                            that.leaders = data.result;
                        } else if (data.status == 200 && !data.result) {
                            that.$message({
                                type: "warning",
                                message: "当前没有可用的组长人选！"
                            });
                            return;
                        } else {
                            that.$message({
                                type: "error",
                                message: "服务器异常！"
                            });
                            return;
                        }
                    } else {
                        that.$message({
                            type: "error",
                            message: "当前网络异常！"
                        });
                    }
                }
            });
        },
        /**点击新建小组 */
        newTeam() {
            let that = this;
            this.getLeadersFromWeb();
            this.$teamManageRequest.getUnAssignedOp({
                onResult(isSuccess, data) {
                    if (isSuccess) {
                        if (data.status == 200) {
                            that.addUsers = data.result;
                            that.$store.commit("SET_SHOW_ADDTEAM", true);
                        } else {
                            that.$message({
                                type: "error",
                                message: "服务器异常！"
                            });
                        }
                    } else {
                        that.$message({
                            type: "error",
                            message: "当前网络异常！"
                        });
                    }
                }
            });
        },
        /**点击小组名称 */
        teamChange(team) {
            this.currentTeam = team;
        },
        /**派发小组 */
        sendTeam(team) {
            this.assignedTeam = team;
            this.$store.commit("SET_SHOW_SENDTEAM", true);
        },
        /**
         * 设为离职
         */
        setToLeave(row) {
            event.cancelBubble = false;
            event.stopPropagation();
        },
        /**
         * 删除成员
         */
        deleteMember(row) {
            event.cancelBubble = false;
            event.stopPropagation();
        },

        /**
         * 将page 数据赋值
         */
        handlePageInfo(data) {
            this.pageInfo.totalElements = data.page.totalElements;
        },
        /**
         * 改变了每页数据
         */
        handleSizeChange(val) {
            console.log(`每页 ${val} 条`);
            this.pageInfo.currentSize = val;
            this.getBlackListFromWeb();
        },
        /**
         * 处理当前的改变
         */
        handleCurrentChange(val) {
            console.log(`当前页: ${val}`);
            this.pageInfo.number = val;
            this.getBlackListFromWeb();
        },
        /**
         * 重置 pageInfo
         */
        resetPageInfo() {
            this.pageInfo.currentSize = this.pageInfo.sizeArray[0];
            this.pageInfo.number = 1;
        },
        /**弹出拨打页面 */
        callPage(callRecord) {
            this.$store.commit("SET_SHOW_CALLPAGE", true);
        }
    },

    watch: {
        currentTeam() {
            this.getTeamMembersFromWeb(this.currentTeam);
        }
    }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
</style>


