const activity = {
    state: {
        showAddActivity: false,
        showModifyActivity: false,
        showWillingUser: false,
        showActivityDetail: false
    },
    mutations: {
        SET_SHOW_ADDACTIVITY(state, data) {
            state.showAddActivity = data;
        },
        SET_SHOW_MODIFYACTIVITY(state, data) {
            state.showModifyActivity = data;
        },
        SET_SHOW_WILLINGUSER(state, data) {
            state.showWillingUser = data;
        },
        SET_SHOW_ACTIVITYDETAIL(state, data) {
            state.showActivityDetail = data;
        }
    },
    actions: {}
};
export default activity;


