<template>
    <div class="tree">
        <table style="border:solid 1px rgb(233,233,233); width:100%;">
            <tr>
                <td class="colum">开始时间</td>
                <td class="colum2">{{activity.validTime}}</td>
            </tr>
            <tr>
                <td class="colum">支付方式</td>
                <td class="colum2">{{activity.payType}}</td>
            </tr>
            <tr>
                <td class="colum">总营销人数</td>
                <td class="colum2">{{activity.deviceId}}</td>
            </tr>
        </table>
        <br>
        <table style="border:solid 1px rgb(233,233,233);width:100%;">
            <tr>
                <td></td>
                <td class="colum1">总统计</td>
                <td class="colum1">当天统计</td>
            </tr>
            <tr>
                <td class="colum">已领取</td>
                <td>{{subscriber.userName}}</td>
                <td>{{subscriber.userName}}</td>
            </tr>
            <tr>
                <td class="colum">已拨打</td>
                <td>{{subscriber.userName}}</td>
                <td>{{subscriber.userName}}</td>
            </tr>
            <tr>
                <td class="colum">预订购</td>
                <td>{{subscriber.userName}}</td>
                <td>{{subscriber.userName}}</td>
            </tr>
            <tr>
                <td class="colum">实际开通</td>
                <td>{{subscriber.userName}}</td>
                <td>{{subscriber.userName}}</td>
            </tr>
        </table>
        <br>
        <table style="border:solid 1px rgb(233,233,233);width:100%;">
            <tr>
                <td class="colum">未拨打数</td>
                <td>50</td>
                <td>
                    <i class="el-icon-phone-outline" style="color:#108EE9; font-size:18px;"></i>
                </td>
            </tr>
            <tr>
                <td class="colum">有意向</td>
                <td>{{subscriber.userId}}</td>
                <td>{{subscriber.userId}}</td>
            </tr>
            <tr>
                <td class="colum">无人应答</td>
                <td>{{subscriber.userId}}</td>
                <td>{{subscriber.userId}}</td>
            </tr>
            <tr>
                <td class="colum">二次回呼</td>
                <td>{{subscriber.userId}}</td>
                <td>{{subscriber.userId}}</td>
            </tr>
            <tr>
                <td class="colum">审核不通过</td>
                <td>{{subscriber.userId}}</td>
                <td>{{subscriber.userId}}</td>
            </tr>
            <tr>
                <td class="colum">营销失败</td>
                <td>{{subscriber.userId}}</td>
                <td>{{subscriber.userId}}</td>
            </tr>
            <tr>
                <td class="colum">回呼成功</td>
                <td>{{subscriber.userId}}</td>
                <td>{{subscriber.userId}}</td>
            </tr>
        </table>
        <br>
        <el-button size="small" @click="$store.commit('SET_SHOW_CALLPAGE',false)">返回</el-button>
        <el-button size="small" type="primary" @click="assignUser">领取</el-button>
        <el-button size="small">刷新</el-button>
    </div>
</template>

<script>
export default {
    props: {
        activity: { type: Object }
    },
    data() {
        return {
            subscriber: {},
            userId: "",
            activityId: ""
        };
    },
    created() {
        this.activityId = this.$route.query.id;
        // this.getActivityFromWeb();
    },
    methods: {
        /**领取用户 */
        assignUser(activity) {
            let that = this;
            this.$activityRequest.getMission({
                activityId: this.activity.id,
                opId: 1,
                // opId: window.parent.userInfo.userId,
                onResult(isSuccess, data) {
                    if (isSuccess) {
                        if (data.status == 200 && data.result > 0) {
                            that.$message({
                                type: "success",
                                message: "成功领取" + data.result + "个用户"
                            });
                        } else if (data.status == 200 && data.result == 0) {
                            that.$message({
                                type: "warning",
                                message: "该营销活动暂时没有营销对象"
                            });
                        } else if (data.status == 500 && data.code != 1000) {
                            that.$message({
                                type: "error",
                                message: data.msg
                            });
                        }
                    } else {
                        that.$message({
                            type: "error",
                            message: "当前网络异常"
                        });
                    }
                }
            });
        }
    },
    filters: {
        // 处理用户手机号和住址
    },
    watch: {}
};
</script>

<!-- Add "scoped" attribute to limit  CSS to this component only -->
<style scoped>
td {
    text-align: center;
    border: 1px solid #cccccc;
    height: auto;
    width: 30%;
}
.colum {
    padding: 6px;
    font: 1.2em sans-serif;
    font-weight: 500;
    color: black;
    width: 40%;
}
.colum1 {
    padding: 6px;
    font: 1.2em sans-serif;
    font-weight: 500;
    color: black;
    width: 30%;
}
.colum2 {
    width: 60%;
}
.tree {
    width: 100%;
    margin: auto;
}
</style>



