<template>
    <div>
        <div>
            <h3 style="float:left;">
                <span style="color:#f00;font-weight:700;margin-right:6px;">●</span>每日查询
            </h3>
            <div style="width:60%;background-color:#E6F3FC; float:right; height:30px;">
                <span style="line-height:30px;margin-left:10px;">任务名称：
                    <span style="color:#108EE9;">{{activity.name}}</span>
                </span>
                <el-button size="mini" style="height:28px;float:right;margin:1px 10px;">返回</el-button>
            </div>
        </div>
        <div style="clear:left;"></div>
        <br/>
        <el-form size="mini" class="demo-form-inline">
            <el-col :span="6">
                <el-form-item>
                </el-form-item>
            </el-col>
            <el-col :span="14">
                <el-form-item label="开始结束时间：">
                    <el-date-picker style="width:70%;" v-model="time" type="daterange" align="right" unlink-panels range-separator="-" start-placeholder="开始日期" end-placeholder="结束日期">
                    </el-date-picker>
                </el-form-item>
            </el-col>
            <el-col :span="4">
                <el-form-item>
                    <el-button size="mini" type="primary">&nbsp;&nbsp;查询&nbsp;&nbsp;</el-button>
                </el-form-item>
            </el-col>
        </el-form>
        <div style="clear:left; margin-bottom:10px;">
            <el-button size="mini">&nbsp;&nbsp;导出&nbsp;&nbsp;</el-button>
        </div>
        <div>
            <div id="info" style="clear:both;height:68px;background-color:#E6F3FC; line-height:34px; ">
            <el-col :span="4">
                <i class="el-icon-info" style="display:inline-block;vertical-align:middle;color:#108EE9;margin-left:10px;font-size:20px;line-height:68px;"></i><span style="display:inline-block;vertical-align:middle;font-weight:600;">【查询总计】</span>
            </el-col>
             <el-col :span="20">
                 <el-row>
                    <el-col :span="8">已拨打(拨打次数)：<span>1234</span></el-col>
                    <el-col :span="5">接通数：<span>1234</span></el-col>
                    <el-col :span="5">预订购：<span>1234</span></el-col>
                    <el-col :span="6">实际开通：<span>1234</span></el-col>
                </el-row>
                <el-row>
                    <el-col :span="4">待开通：<span>1234</span></el-col>
                    <el-col :span="5">投诉退单：<span>1234</span></el-col>
                    <el-col :span="5">接通率：<span>1234</span></el-col>
                    <el-col :span="5">预订购率：<span>1234</span></el-col>
                    <el-col :span="5">实际开通率：<span>1234</span></el-col>
                </el-row>
             </el-col>
        </div>
            <el-table :data="productList" style="width: 100%">
                <el-table-column label="日期" align="center" show-overflow-tooltip>
                    <template slot-scope="scope">
                        <span>{{ scope.row.title }}</span>
                    </template>
                </el-table-column>
                <el-table-column label="已拨打(拨打次数)" width="130" align="center" show-overflow-tooltip>
                    <template slot-scope="scope">
                        <span>{{ scope.row.payType}}</span>
                    </template>
                </el-table-column>
                <el-table-column label="接通数" align="center" show-overflow-tooltip>
                    <template slot-scope="scope">
                        <span>{{ scope.row.payPiont}}</span>
                    </template>
                </el-table-column>
                <el-table-column label="预订购" align="center" show-overflow-tooltip>
                    <template slot-scope="scope">
                        <span>{{ scope.row.payPrice }}</span>
                    </template>
                </el-table-column>

                <el-table-column label="实际开通" align="center" show-overflow-tooltip>
                    <template slot-scope="scope">
                        <span>{{ scope.row.orderCycleCount }}</span>
                    </template>
                </el-table-column>
                <el-table-column label="待开通" align="center" show-overflow-tooltip>
                    <template slot-scope="scope">
                        <span>{{ scope.row.orderCycleCount }}</span>
                    </template>
                </el-table-column>
                <el-table-column label="投诉退单" align="center" show-overflow-tooltip>
                    <template slot-scope="scope">
                        <span>{{ scope.row.orderCycleCount }}</span>
                    </template>
                </el-table-column>
                <el-table-column label="接通率" align="center" show-overflow-tooltip>
                    <template slot-scope="scope">
                        <span>{{ scope.row.orderCycleCount }}</span>
                    </template>
                </el-table-column>
                <el-table-column label="预订购率" align="center" show-overflow-tooltip>
                    <template slot-scope="scope">
                        <span>{{ scope.row.orderCycleCount }}</span>
                    </template>
                </el-table-column>
                <el-table-column label="实际开通率" width="90" align="center" show-overflow-tooltip>
                    <template slot-scope="scope">
                        <span>{{ scope.row.orderCycleCount }}</span>
                    </template>
                </el-table-column>
            </el-table>
        </div>
        <div style="float:right;">
            <el-pagination class="layout-pagination" @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="pageInfo.number" :page-sizes="pageInfo.sizeArray" :page-size="pageInfo.currentSize" layout="total, sizes, prev, pager, next, jumper" :total="pageInfo.totalElements">
            </el-pagination>
        </div>
        <div style="clear:both;"></div>
        <div style="margin-top:30px;">
            <p>
                <span style="color:#108EE9;">*备注：</span> <br/> &nbsp;&nbsp;&nbsp;（1）预订购率=预订购/已拨打，开通率和接通率以同样的方式计算
                <br/> &nbsp;&nbsp;&nbsp;（2）这里的已拨打数仅代表已拨打的“次数”，与其他的已拨打数不同
                <br/> &nbsp;&nbsp;&nbsp;（3）每天晚上11点30分准时更新数据
            </p>
        </div>
    </div>
</template>

<script>
export default {
    components: {},
    props: {},
    data() {
        return {
            time: "",
            activity: { type: Object },
            productList: [],
            pageInfo: {
                currentSize: 10,
                sizeArray: [10, 20, 50, 100],
                number: 1,
                totalElements: 0
            },
            isLoading: false
        };
    },
    created() {
    },
    filters: {
        // 对手机号、地址处理
    },
    methods: {
        /**
         * 将page 数据赋值
         */
        handlePageInfo(data) {
            this.pageInfo.totalElements = data.page.totalElements;
            console.log(this.pageInfo);
        },
        /**
         * 改变了每页数据
         */
        handleSizeChange(val) {
            console.log(`每页 ${val} 条`);
            this.pageInfo.currentSize = val;
            this.getActivityListFromWeb();
        },
        /**
         * 处理当前的改变
         */
        handleCurrentChange(val) {
            console.log(`当前页: ${val}`);
            this.pageInfo.number = val;
            this.getActivityListFromWeb();
        },
        /**
         * 重置 pageInfo
         */
        resetPageInfo() {
            this.pageInfo.currentSize = this.pageInfo.sizeArray[0];
            this.pageInfo.number = 1;
        }
    },

    watch: {}
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
#info span{
    color:#108EE9;
}
</style>



