import Vue from 'vue'
import axios from 'axios'

import Constant from '@/assets/js/Constant'

function logout() {

}

function exit(code) {

}

/**
 * 统一处理成功回应
 */
function handleSuccessResponse(holder, data) {
  holder.onResult(true, data);
}

/**
 * 统一处理失败回应
 */
function handleFailResponse(holder, xhr, textStatus, errorThrown) {
  var data = {
    xhr: xhr,
    textStatus: textStatus,
    error: errorThrown
  }
  holder.onResult(false, data);
}

export var RequestHelper = {
  /**
   * 查询产品列表
   */
  getProductList(holder) {
    let product = {
      title: holder.title,
      productCode: holder.productCode,
      payType: holder.payType
    };
    let api = Constant.serverUrl + Constant.api.product.getProductList +
      "?page=" + holder.page + "&size=" + holder.size + "&name=" + holder.name + "&productCode=" + holder.productCode + "&payType=" + holder.payType;
    let that = this;
    axios.get(api).then(success => {
      handleSuccessResponse(holder, success.data);
    }).catch(error => {
      handleFailResponse(holder, error);
    });
  },

  /**
   * 获取产品信息/{productId}
   */
  getProductById(holder) {
    let api = Constant.serverUrl + Constant.api.product.getProduct +
      holder.productId;
    let that = this;
    axios.get(api).then(success => {
      handleSuccessResponse(holder, success.data);
    }).catch(error => {
      handleFailResponse(holder, error);
    });
  },
  /**
   * 获取产品信息/{productCode}
   */
  getProductByCode(holder) {
    let api = Constant.serverUrl + Constant.api.product.getProductByCode +
      holder.productCode;
    let that = this;
    axios.get(api).then(success => {
      handleSuccessResponse(holder, success.data);
    }).catch(error => {
      handleFailResponse(holder, error);
    });
  },
  /**
   * 添加产品
   */
  addProduct(holder) {
    let api = Constant.serverUrl + Constant.api.product.addProduct;
    let that = this;
    axios.post(api, holder.product).then(success => {
      handleSuccessResponse(holder, success.data);
    }).catch(error => {
      handleFailResponse(holder, error);
    });
  },
  /**
   * 更新产品/{productId}
   */
  updateProduct(holder) {
    let api = Constant.serverUrl + Constant.api.product.updateProduct +
      holder.productId;
    let that = this;
    axios.post(api, holder.product).then(success => {
      handleSuccessResponse(holder, success.data);
    }).catch(error => {
      handleFailResponse(holder, error);
    });
  },
  /**
   * 更新产品上线状态/{productId}
   */
  updateOnlineStatus(holder) {
    let api = Constant.serverUrl + Constant.api.product.updateOnlineStatus +
      holder.product.id + "?onlineStatus=" + holder.product.status;
    let that = this;
    axios.post(api).then(success => {
      handleSuccessResponse(holder, success.data);
    }).catch(error => {
      handleFailResponse(holder, error);
    });
  },
  /**
   * 获取有效产品
   */
  getProductValidList(holder) {
    let api = Constant.serverUrl + Constant.api.product.getProductValidList + '?activityId =' + holder.activityId;
    let that = this;
    axios.get(api).then(success => {
      handleSuccessResponse(holder, success.data);
    }).catch(error => {
      handleFailResponse(holder, error);
    });
  },
  /**
   * 根据支付类型获取产品
   */
  getProductListByPayType(holder) {
    let api = Constant.serverUrl + Constant.api.product.getProductListByPayType +
      holder.payType;
    let that = this;
    axios.get(api).then(success => {
      handleSuccessResponse(holder, success.data);
    }).catch(error => {
      handleFailResponse(holder, error);
    });
  },
  /**
   * 在线查询产品
   */
  searchProduct(holder) {
    let api = Constant.serverUrl + Constant.api.product.searchProduct +
      "productId=" + holder.productId;
    let that = this;
    axios.get(api).then(success => {
      handleSuccessResponse(holder, success.data);
    }).catch(error => {
      handleFailResponse(holder, error);
    });
  },
};



function install(Vue) {
  Vue.prototype.$productRequest = RequestHelper;
};

export default install;


