<template>
    <!-- 分配任务 -->
    <div class="main-container gray-bg">
        <el-row :gutter="40">
            <el-col :span="7">
                <div id="activityList" class="ibox-content">
                    <el-button style="display:block;height:34px; width:80%; border-bottom: 1px solid #cccccc; margin-left:10%;" type="text">
                        <i class="el-icon-caret-bottom" style="font-size:30px;color:#ccc;float:left;margin-lift:10px;margin-top:-10px;"></i>
                        <span style="float:left; color:#666;">任务</span>
                    </el-button>
                    <el-button @click="selectActivity(activity)" class="activity" type="text" v-for="activity in activityList" :key="activity.id">
                        {{activity.name}}
                    </el-button>
                </div>
            </el-col>

            <el-col :span="17">
                <div class="ibox-content">
                    <h3 style="color:#666; text-align:center;">任务名称：
                        <span>{{activity.name}}</span>
                    </h3>
                    <div style="height:34px;background-color:#E6F3FC;margin-bottom:10px;">
                        <i class="el-icon-info" style="display:inline-block;vertical-align:middle;color:#108EE9;margin-left:10px;font-size:20px;"></i>
                        <span style="display:inline-block;vertical-align:middle;line-height:34px; margin-left:20px;font-weight:700;">已分配客服</span>
                        <el-button size="small" v-if="assignedTeams" @click="clearAll()" style="display:inline-block;vertical-align:middle;margin-left:20px;">清空全部</el-button>
                    </div>

                    <div id="checkedTeams">
                        <div v-if="!assignedTeams">
                            <h4 style="text-align:center; color:red;">该任务尚未分配坐席</h4>
                        </div>
                        <div v-for="team in assignedTeams" :key="team.name">
                            <div style="background-color:#F3F3F3; height:36px;">
                                &nbsp;
                                <span style="line-height:36px;">{{team.teamName}}（{{team.members | totalNum}}）</span>
                                <el-button @click="removeAll(team)" size="mini" style="float:right; margin-top:2px;margin-right:10px;">&nbsp;&nbsp;&nbsp;&nbsp;清空&nbsp;&nbsp;&nbsp;&nbsp;</el-button>
                            </div>
                            <div style="clear:both;"></div>
                            <div style="display:inline-block; width:16%; height:26px;padding-left: 10px; background-color:#F4F4F4; margin:8px 10px;" v-for="member in team.members" :key="member.opId">
                                <span style="line-height:26px;">{{member.opName}}</span>
                                <el-button @click="deleteUser(member,team)" type="text" style="float:right; margin:-7px 10px;">
                                    <i :id="member.id" class='el-icon-close' style="font-weight:700;color:#999;"></i>
                                </el-button>
                            </div>
                            <hr style="margin-top:-2px;" />
                        </div>
                    </div>

                    <div style="height:34px;background-color:#E6F3FC;margin-bottom:10px;">
                        <i class="el-icon-info" style="color:#108EE9;margin-left:10px;font-size:20px;line-height:34px;"></i>
                        <span style="margin-left:20px;font-weight:700;line-height:34px;">待分配客服</span>
                    </div>
                    <div>
                        <div v-for="team in teams" :key="team.name">
                            <div style="background-color:#F3F3F3; height:36px;">
                                &nbsp;
                                <span style="line-height:36px;">{{team.teamName}}（{{team.members | totalNum}}）</span>
                                <el-button @click="checkedAll(team)" size="mini" style="float:right; margin-top:2px;margin-right:10px;">全组加入</el-button>
                            </div>
                            <div style="clear:both;"></div>
                            <div style="display:inline-block; width:16%; height:26px;padding-left: 10px; background-color:#F4F4F4; margin:8px 10px;" v-for="member in team.members" :key="member.opId">
                                <span style="line-height:26px;">{{member.opName}}</span>
                                <el-button @click="selectedUser(member,team)" type="text" style="float:right; margin:-7px 10px;">
                                    <i :id="member.id" class='el-icon-plus' style="font-weight:700;color:#999; color: #3399ff;"></i>
                                </el-button>
                            </div>
                            <hr style="margin-top:-2px;" />
                        </div>
                    </div>
                    <!-- <el-button size="small" style="float:right;background-color:#108EE9;color:white;">保存记录</el-button>
                    <div style="clear:both;"></div> -->
                </div>
            </el-col>
        </el-row>
    </div>
</template>
<script>
export default {
    components: {},
    props: {},
    data() {
        return {
            activity: {},
            activityList: [
                {
                    autoOpen: 0,
                    createTime: "2018-08-24T06:55:04.012Z",
                    expireTime: "2018-08-24T06:55:04.012Z",
                    id: 0,
                    ivr: "string",
                    maxPoint: 0,
                    minPoint: 0,
                    name: "活动001",
                    payType: 0,
                    script: "string",
                    smsParameter: "string",
                    smsTemplate: "string",
                    spCode: "string",
                    status: 0,
                    type: 0,
                    validTime: "2018-08-24T06:55:04.012Z"
                }
            ],
            assignedTeams: [
                // 待分配坐席
            ],
            teams: [
                // 待分配坐席
            ]
        };
    },

    created() {
        this.getActivityListFromWeb(); // 获取有效的营销活动列表
        // this.activity = this.activityList[0];
        // this.getOpFromWeb(); // 获取营销活动已分配坐席
    },

    methods: {
        /**从任务中移除某个成员 */
        deleteUser(member, team) {
            var deleteTeams = [
                {
                    teamName: team.teamName,
                    members: [member]
                }
            ];
            this.deleteOpFromWeb(deleteTeams);
        },
        /**清空小组 */
        removeAll(team) {
            var deleteTeams = [team];
            this.deleteOpFromWeb(deleteTeams);
        },
        /**清空全部 */
        clearAll() {
            let that = this;
            this.$confirm("你确定要进行客服清空吗？", {
                confirmButtonText: "确定",
                cancelButtonText: "取消",
                type: "warning"
            })
                .then(() => {
                    that.deleteOpFromWeb(that.assignedTeams);
                })
                .catch(() => {
                    that.$message({
                        type: "info",
                        message: "已取消清空客服！"
                    });
                });
        },
        /**将某成员加入任务 */
        selectedUser(member, team) {
            var sendTeam = {
                teamName: team.teamName,
                members: [member]
            };
            this.sendUserFromWeb(sendTeam);
        },
        /**全组加入 */
        checkedAll(team) {
            var that = this;
            this.$confirm("你确定要进行客服全组加入吗？", {
                confirmButtonText: "确定",
                cancelButtonText: "取消",
                type: "warning"
            })
                .then(() => {
                    that.sendUserFromWeb(team);
                })
                .catch(() => {
                    that.$message({
                        type: "info",
                        message: "已取消全组加入！"
                    });
                });
        },
        /**
         * 给营销活动分配坐席
         */
        sendUserFromWeb(team) {
            let that = this;
            let sendTeams = [];
            for (var i = 0; i < team.members.length; i++) {
                sendTeams.push({
                    activityId: this.activity.id,
                    opId: team.members[i].opId,
                    opName: team.members[i].opName,
                    teamId: team.members[i].teamId,
                    teamName: team.teamName
                });
            }
            this.$activityRequest.sendUser({
                activityId: that.activity.id,
                users: sendTeams,
                onResult(isSuccess, data) {
                    if (isSuccess) {
                        if (data.status == 200) {
                            // 重新获取坐席分配情况
                            that.getOpFromWeb();
                            that.getWaitOpFromWeb();
                        } else if (data.status == 500 && data.code != 1000) {
                            that.$message({ type: "error", message: data.msg });
                        }
                    } else {
                        that.$message({
                            type: "error",
                            message: "当前网络异常"
                        });
                    }
                }
            });
        },
        /**
         * 删除营销活动已经分配的坐席
         */
        deleteOpFromWeb(teams) {
            let that = this;
            let sendTeams = [];
            for (var j = 0; j < teams.length; j++) {
                for (var i = 0; i < teams[j].members.length; i++) {
                    sendTeams.push({
                        activityId: this.activity.id,
                        opId: teams[j].members[i].opId,
                        opName: teams[j].members[i].opName,
                        teamId: teams[j].members[i].teamId,
                        teamName: teams[j].teamName
                    });
                }
            }
            this.$activityRequest.deleteOp({
                activityId: that.activity.id,
                users: sendTeams,
                onResult(isSuccess, data) {
                    if (isSuccess) {
                        if (data.status == 200) {
                            // 重新获取坐席分配情况
                            that.getOpFromWeb();
                            that.getWaitOpFromWeb();
                        } else if (data.status == 500 && data.code != 1000) {
                            that.$message({ type: "error", message: data.msg });
                        }
                    } else {
                        that.$message({
                            type: "error",
                            message: "当前网络异常"
                        });
                    }
                }
            });
        },
        /**
         * 获取有效的营销活动
         */
        getActivityListFromWeb() {
            let that = this;
            this.$activityRequest.validList({
                onResult(isSuccess, data) {
                    if (isSuccess) {
                        if (data.status == 200) {
                            that.activityList = data.result;
                            that.activity = that.activityList[0];
                            that.getOpFromWeb();
                            that.getWaitOpFromWeb();
                        } else if (data.status == 500 && data.code != 1000) {
                            that.$message({
                                type: "warning",
                                message: data.msg
                            });
                        }
                    } else {
                        that.$message({
                            type: "error",
                            message: "当前网络异常"
                        });
                    }
                }
            });
        },
        /**
         * 获取营销活动已分配坐席
         */
        getOpFromWeb() {
            let that = this;
            this.$activityRequest.op({
                activityId: this.activity.id,
                onResult(isSuccess, data) {
                    if (isSuccess) {
                        if (data.status == 200)
                            that.assignedTeams = data.result;
                    } else {
                    }
                }
            });
        },
        /**
         * 获取营销活动待分配坐席
         */
        getWaitOpFromWeb() {
            let that = this;
            this.$activityRequest.waitOp({
                activityId: this.activity.id,
                onResult(isSuccess, data) {
                    if (isSuccess) {
                        if (data.status == 200) that.teams = data.result;
                    } else {
                    }
                }
            });
        },

        /**点击选择活动 */
        selectActivity(activity) {
            this.activity = activity;
            this.getOpFromWeb();
            this.getWaitOpFromWeb();
        }
    },
    filters: {
        totalNum(members) {
            return members.length;
        }
    },
    watch: {}
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.activity {
    display: block;
    height: 30px;
    width: 80%;
    border-bottom: 1px solid #cccccc;
    margin-left: 10%;
    color: #666666;
}
#activityList button:hover {
    background-color: #e8f4fd;
    color: #108ee9;
    border-bottom: 1px solid #108ed9;
}
</style>



