<template>
    <div class="main-container gray-bg">
        <el-form ref="setting" :model="form" label-width="30%" size="mini" label-position="right">
            <el-form-item label="每次领取用户上限：">
                <el-input v-model="form.maxSubscriber" placeholder="请输入"></el-input>
                <span>个（默认值：200）</span>
            </el-form-item>
            <el-form-item label="拨打最高次数：">
                <el-input v-model="form.maxCallCount" placeholder="请输入"></el-input>
                <span>次（默认值：5）尝试拨打用户电话最高次数</span>
            </el-form-item>
            <el-form-item label="用户积分更新时间间隔：">
                <el-input v-model="form.pointRefreshInterval" placeholder="请输入"></el-input>
                <span>天（默认值：5）积分更新间隔天数</span>
            </el-form-item>
            <el-form-item label="拨打电话-实时积分查询：">
                <el-radio-group v-model="form.pointSearch">
                    <el-radio label="开"></el-radio>
                    <el-radio label="关"></el-radio>
                </el-radio-group>
                <span>( 如遇网络问题可以关闭 )</span>
            </el-form-item>
            <el-form-item label="自动发送短信（18:30）：">
                <el-radio-group v-model="form.sendMsgSwitch">
                    <el-radio label="开"></el-radio>
                    <el-radio label="关"></el-radio>
                </el-radio-group>
            </el-form-item>

            <el-form-item label="自动筛选订购关系（6:30）：">
                <el-radio-group v-model="form.filterOrderSwitch">
                    <el-radio label="开"></el-radio>
                    <el-radio label="关"></el-radio>
                </el-radio-group>
            </el-form-item>

            <el-form-item label="每日筛选量：" v-if="form.filterOrderSwitch==1">
                <el-input v-model="form.name" placeholder="请输入"></el-input>
                <span>个（开启筛选量大于0才能筛选）</span>
            </el-form-item>

            <el-form-item label="筛选间隔：" v-if="form.filterOrderSwitch==1">
                <el-input v-model="form.name" placeholder="请输入"></el-input>
                <span>小时（默认值：24）</span>
            </el-form-item>
            <el-form-item label="自动开通（18:00,21:00）：">
                <el-radio-group v-model="form.resource">
                    <el-radio label="开"></el-radio>
                    <el-radio label="关"></el-radio>
                </el-radio-group>
            </el-form-item>
            <el-form-item label="获取按键时间：">
                <el-input v-model="form.name" placeholder="请输入"></el-input>
                <span>秒（默认值：60）</span>
            </el-form-item>
            <el-form-item label="主动申请订购：">
                <el-radio-group v-model="form.resource">
                    <el-radio label="自动开"></el-radio>
                    <el-radio label="双休自动开"></el-radio>
                    <el-radio label="不自动开"></el-radio>
                </el-radio-group>
            </el-form-item>
            <el-form-item label="纯积分开通接口：">
                <el-radio-group v-model="form.resource">
                    <el-radio label="默认"></el-radio>
                    <el-radio label="sp渠道"></el-radio>
                </el-radio-group>
            </el-form-item>
            <el-form-item>
                <el-button type="primary" @click="onSubmit">保存</el-button>
                <el-button>恢复默认</el-button>
            </el-form-item>
        </el-form>
    </div>
</template>
<script>
export default {
    data() {
        return {
            form: {}
        };
    },
    methods: {
        onSubmit() {
            // 提交
        }
    }
};
</script>
 <style lang="scss" scoped>
.el-input {
    width: 20%;
}
</style>



