<template>
    <div>
        <el-form ref="form" size="mini" :model="formInline" label-width="110px">
            <el-row :gutter="70">
                <el-col :span="9">
                    <el-form-item label="客服：">
                        <el-input v-model="formInline.user" placeholder="审批人"></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="9">
                    <el-form-item label="开始结束时间：">
                        <el-date-picker v-model="formInline.time" type="daterange" range-separator="-" start-placeholder="开始日期" end-placeholder="结束日期">
                        </el-date-picker>
                    </el-form-item>
                </el-col>
                <el-col :span="6">
                    <el-form-item>
                        <el-button type="primary" @click="onSubmit">查询</el-button>
                    </el-form-item>
                </el-col>
            </el-row>
        </el-form>

        <div id="info" style="height:34px; background-color:#E6F3FC; line-height:34px;">
            <el-col :span="1"><i class="el-icon-info" style="display:inline-block;vertical-align:middle;color:#108EE9;margin-left:10px;font-size:20px;"></i></el-col>
            <el-col :span="4">呼入用户数：<span>1234</span></el-col>
            <el-col :span="3">收入/元：<span>1234</span></el-col>    
            <el-col :span="4">实际开通数：<span>1234</span></el-col>                <el-col :span="4"><span>开通失败数：</span><span>1234</span></el-col>
            <el-col :span="4">用户取消申请：<span>1234</span></el-col>
            <el-col :span="4">退单用户数：<span>1234</span></el-col>
        </div>
        <el-table :data="tableData" v-loading="isLoading" style="width: 100%" tooltip-effect="dark">
            <el-table-column label="用户姓名" align="center" show-overflow-tooltip>
                <template slot-scope="scope">
                    <span>{{ scope.row.userId }}</span>
                </template>
            </el-table-column>
            <el-table-column label="呼入时间" align="center" show-overflow-tooltip>
                <template slot-scope="scope">
                    <span>{{ scope.row.deviceId}}</span>
                </template>
            </el-table-column>
            <el-table-column label="厂商" align="center" show-overflow-tooltip>
                <template slot-scope="scope">
                    <span>{{ scope.row.userName}}</span>
                </template>
            </el-table-column>
            <el-table-column label="客服" align="center" show-overflow-tooltip>
                <template slot-scope="scope">
                    <span>{{ scope.row.phone}}</span>
                </template>
            </el-table-column>

            <el-table-column label="产品" align="center" show-overflow-tooltip>
                <template slot-scope="scope">
                    <span>{{ scope.row.phone}}</span>
                </template>
            </el-table-column>
            <!--状态筛选-->
            <el-table-column label="状态" align="center" show-overflow-tooltip>
                <template slot-scope="scope">
                    <span>{{ scope.row.point }}</span>
                </template>
            </el-table-column>
            <!--支付方式筛选-->
            <el-table-column label="支付方式" align="center" show-overflow-tooltip>
                <template slot-scope="scope">
                    <span>{{ scope.row.smsContent }}</span>
                </template>
            </el-table-column>
            <el-table-column label="实际支付" align="center" show-overflow-tooltip>
                <template slot-scope="scope">
                    <span>{{ scope.row.result }}</span>
                </template>
            </el-table-column>
        </el-table>
        <div style="float:right;">
            <el-pagination class="layout-pagination" @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="pageInfo.number" :page-sizes="pageInfo.sizeArray" :page-size="pageInfo.currentSize" layout="total, sizes, prev, pager, next, jumper" :total="pageInfo.totalElements">
            </el-pagination>
        </div>
        <div style="clear:both;"></div>
    </div>
</template>

<script>
export default {
    components: {},
    props: {},
    data() {
        return {
            formInline: {},
            tableData: [],
            pageInfo: {
                currentSize: 10,
                sizeArray: [10, 20, 50, 100],
                number: 1,
                totalElements: 0
            },
            isLoading: false
        };
    },

    created() {},

    methods: {
        onSubmit() {},
        /**
         * 获取基础产品列表
         */
        getBaseProductListFromWeb() {
            this.isLoading = true;
            let that = this;
            this.$baseProductRequest.getBaseProductList({
                page: this.pageInfo.number - 1,
                size: this.pageInfo.currentSize,
                baseProduct: this.formInline,
                onResult(isSuccess, data) {
                    if (isSuccess) {
                        that.handleGetBaseProductListSuccessResponse(data);
                    } else {
                    }
                    that.isLoading = false;
                }
            });
        },
        handleGetBaseProductListSuccessResponse(data) {
            this.handlePageInfo(data);
            this.baseProductList = data.body;
        },
        /**
         * 将page 数据赋值
         */
        handlePageInfo(data) {
            this.pageInfo.totalElements = data.page.totalElements;
            console.log(this.pageInfo);
        },
        /**
         * 改变了每页数据
         */
        handleSizeChange(val) {
            console.log(`每页 ${val} 条`);
            this.pageInfo.currentSize = val;
            this.getBaseProductListFromWeb();
        },
        /**
         * 处理当前的改变
         */
        handleCurrentChange(val) {
            console.log(`当前页: ${val}`);
            this.pageInfo.number = val;
            this.getBaseProductListFromWeb();
        },
        /**
         * 重置 pageInfo
         */
        resetPageInfo() {
            this.pageInfo.currentSize = this.pageInfo.sizeArray[0];
            this.pageInfo.number = 1;
        }
    },

    watch: {}
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
#info span{
    color:#108EE9;
}
</style>



