<template>
    <div class="main-container gray-bg">
        <el-form ref="form" size="mini" :model="form" label-position="right" label-width="100px">
            <el-row :gutter="10">
                <el-col :span="8">
                    <el-form-item label="订单号：">
                        <el-input v-model="form.orderNo" placeholder="请输入"></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="8">
                    <el-form-item label="姓名：">
                        <el-input v-model="form.userName" placeholder="请输入"></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="8">
                    <el-form-item label="业务号：">
                        <el-input v-model="form.userId" placeholder="请输入"></el-input>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row :gutter="10">
                <el-col :span="8">
                    <el-form-item label="手机号：">
                        <el-input v-model="form.phone" placeholder="请输入"></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="8">
                    <el-form-item label="设备号：">
                        <el-input v-model="form.deviceId" placeholder="请输入"></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="8">
                    <el-form-item label="产品名称：">
                        <el-input v-model="form.productName" placeholder="请输入"></el-input>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row :gutter="10">
                <el-col :span="8">
                    <el-form-item label="处理时间：">
                        <el-date-picker v-model="form.refundTime" type="datetimerange" range-separator="-" start-placeholder="开始日期" end-placeholder="结束日期">
                        </el-date-picker>
                    </el-form-item>
                </el-col>
                <el-col :span="8">
                    <el-form-item label="投诉时间：">
                        <el-date-picker v-model="form.tousuTime" type="datetimerange" range-separator="-" start-placeholder="开始日期" end-placeholder="结束日期">
                        </el-date-picker>
                    </el-form-item>
                </el-col>
                <el-col :span="8">
                    <el-form-item label="支付方式：">
                        <el-select v-model="form.payType" placeholder="请选择">
                            <el-option v-for="payType in payTypeFilters" :label="payType.text" :value="payType.value" :key="payType.value"></el-option>
                        </el-select>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row :gutter="10">
                <el-col :span="8">
                    <el-form-item label="投诉来源：">
                        <el-select v-model="form.unorderSource" placeholder="请选择">
                            <el-option v-for="unorderSource in unorderSourceFilters" :label="unorderSource.text" :value="unorderSource.value" :key="unorderSource.value"></el-option>
                        </el-select>
                    </el-form-item>
                </el-col>
                <el-col :span="8">
                    <el-form-item label="处理进度：">
                        <el-select v-model="form.status" placeholder="请选择">
                            <el-option v-for="status in orderStatusFilters" :label="status.text" :value="status.value" :key="status.value"></el-option>
                        </el-select>
                    </el-form-item>
                </el-col>
                <el-col :span="8">
                    <el-form-item label="">
                        <el-button type="primary" style="float:right;">&nbsp;查询&nbsp;</el-button>
                    </el-form-item>
                </el-col>
            </el-row>
        </el-form>

        <div>
            <el-button type="primary" @click="$store.commit('SET_SHOW_SEARCHUSERDLG',true)" size="small">查找用户</el-button>
            <!-- <el-button style="margin-left:5%;" type="small">&nbsp;导出&nbsp;</el-button> -->

        </div>
        <br/>

        <el-table :data="refundList" v-loading="isLoading" style="width: 100%">
            <el-table-column label="订单号" align="center" show-overflow-tooltip>
                <template slot-scope="scope">
                    <span>{{ scope.row.orderNo }}</span>
                </template>
            </el-table-column>
            <el-table-column label="业务号" align="center" show-overflow-tooltip>
                <template slot-scope="scope">
                    <span>{{ scope.row.userId}}</span>
                </template>
            </el-table-column>
            <el-table-column label="姓名" align="center" show-overflow-tooltip>
                <template slot-scope="scope">
                    <span>{{ scope.row.userName}}</span>
                </template>
            </el-table-column>
            <el-table-column label="产品名称" align="center" show-overflow-tooltip>
                <template slot-scope="scope">
                    <span>{{ scope.row.productName }}</span>
                </template>
            </el-table-column>
            <!--支付方式筛选-->
            <el-table-column label="支付方式" align="center" show-overflow-tooltip>
                <template slot-scope="scope">
                    <span>{{ scope.row.payType | payTypeFilter }}</span>
                </template>
            </el-table-column>
            <!--投诉来源筛选-->
            <el-table-column label="投诉来源" align="center" show-overflow-tooltip>
                <template slot-scope="scope">
                    <span>{{ scope.row.unorderSource | unorderSourceFilter}}</span>
                </template>
            </el-table-column>
            <el-table-column label="投诉时间" width="100" sortable align="center" show-overflow-tooltip>
                <template slot-scope="scope">
                    <span>{{ scope.row.touSuTime }}</span>
                </template>
            </el-table-column>
            <el-table-column label="处理时间" width="100" sortable align="center" show-overflow-tooltip>
                <template slot-scope="scope">
                    <span>{{ scope.row.chuLiTime }}</span>
                </template>
            </el-table-column>
            <!--处理进度筛选-->
            <el-table-column label="处理进度" align="center" show-overflow-tooltip>
                <template slot-scope="scope">
                    <span>{{ scope.row.status | orderStatusFilter}}</span>
                </template>
            </el-table-column>
            <el-table-column label="处理结果" align="center" show-overflow-tooltip>
                <template slot-scope="scope">
                    <span>{{ scope.row.result }}</span>
                </template>
            </el-table-column>
            <el-table-column label="建议" align="center" show-overflow-tooltip>
                <template slot-scope="scope">
                    <span>{{ scope.row.jianYi }}</span>
                </template>
            </el-table-column>
            <el-table-column label="操作" align="center" show-overflow-tooltip>
                <template slot-scope="scope">
                    <el-button @click="viewRefundDetail(scope.row)" type="text">详情</el-button>
                    <el-button type="text">处理</el-button>
                    <el-button type="text">删除</el-button>
                </template>
            </el-table-column>
        </el-table>
        <div style="float:right;">
            <el-pagination class="layout-pagination" @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="pageInfo.number" :page-sizes="pageInfo.sizeArray" :page-size="pageInfo.currentSize" layout="total, sizes, prev, pager, next, jumper" :total="pageInfo.totalElements">
            </el-pagination>
        </div>
        <search-user />
        <refund-detail :refund="refund" />
    </div>
</template>

<script>
import Constant from "@/assets/js/Constant";
import SearchUser from "./SearchUser";
import RefundDetail from "./RefundDetail";
export default {
    components: {
        SearchUser,
        RefundDetail
    },
    props: {},
    data() {
        return {
            payTypeFilters: [],
            orderStatusFilters: [],
            unorderSourceFilters: [],
            refund: {},
            form: {},
            refundList: [],
            pageInfo: {
                currentSize: 10,
                sizeArray: [10, 20, 50, 100],
                number: 1,
                totalElements: 0
            },
            isLoading: false
        };
    },

    created() {
        this.unorderSourceFilters = Constant.unorderSourceList;
        this.orderStatusFilters = Constant.orderStatusList;
        this.payTypeFilters = Constant.payTypeList;
        this.getRefundListFromWeb();
    },

    methods: {
        /**查看退单处理详情 */
        viewRefundDetail(refund) {
            this.refund = refund;
            this.$store.commit("SET_SHOW_REFUNDDETAILDLG", true);
        },
        // /**支付方式筛选 */
        // payTypeFilter(value, row, column) {
        //     alert(column["property"]);
        //     const property = column["property"];
        //     return row[property] === value;
        // },
        // /**投诉来源筛选 */
        // unorderSourceFilter(value, row, column) {
        //     const property = column["property"];
        //     return row[property] === value;
        // },
        // /**处理进度筛选 */
        // payTypeFilter(value, row, column) {
        //     const property = column["property"];
        //     return row[property] === value;
        // },
        /**
         * 获取退单列表
         */
        getRefundListFromWeb() {
            this.isLoading = true;
            let that = this;
            this.$baseProductRequest.getBaseProductList({
                page: this.pageInfo.number - 1,
                size: this.pageInfo.currentSize,
                baseProduct: this.formInline,
                onResult(isSuccess, data) {
                    if (isSuccess) {
                        that.handleGetRefundListSuccessResponse(data);
                    } else {
                    }
                    that.isLoading = false;
                }
            });
        },
        handleGetRefundListSuccessResponse(data) {
            this.handlePageInfo(data);
            this.baseProductList = data.result;
        },
        /**
         * 将page 数据赋值
         */
        handlePageInfo(data) {
            this.pageInfo.totalElements = data.page.totalElements;
            console.log(this.pageInfo);
        },
        /**
         * 改变了每页数据
         */
        handleSizeChange(val) {
            console.log(`每页 ${val} 条`);
            this.pageInfo.currentSize = val;
            this.getBaseProductListFromWeb();
        },
        /**
         * 处理当前的改变
         */
        handleCurrentChange(val) {
            console.log(`当前页: ${val}`);
            this.pageInfo.number = val;
            this.getBaseProductListFromWeb();
        },
        /**
         * 重置 pageInfo
         */
        resetPageInfo() {
            this.pageInfo.currentSize = this.pageInfo.sizeArray[0];
            this.pageInfo.number = 1;
        },
        searchDialRecord() {}
    },
    filters: {
        payTypeFilter(payType) {
            return Constant.payTypeMap[payType];
        },
        unorderSourceFilter(unorderSource) {
            return Constant.unorderSourceMap[unorderSource];
        },
        orderStatusFilter(status) {
            return Constant.orderStatusMap[status];
        }
    },

    watch: {}
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
</style>



