<template>
    <el-dialog title="" :visible.sync="isCurrentShow" width="100%" :before-close="handleClose" :close-on-click-modal="false">
        <el-row :gutter="20" style="margin-top:-10px;">
            <el-col :span="6">
                <div class="ibox-content">
                    <left-table />
                </div>
            </el-col>
            <el-col :span="18">
                <div class="ibox-content">
                    <right-content />
                </div>
            </el-col>
        </el-row>
    </el-dialog>
</template>

<script>
import LeftTable from "@/business/newMission/view/LeftTable";
import RightContent from "@/business/newMission/view/RightContent";

export default {
    components: {
        LeftTable,
        RightContent
    },

    props: {
        activity: {}
    },

    data() {
        return {};
    },

    created() {
        this.getActivityFromWeb;
    },
    computed: {
        isCurrentShow() {
            return this.$store.state.activity.showActivityDetail;
        }
    },
    methods: {
        handleClose() {
            this.$store.commit("SET_SHOW_ACTIVITYDETAIL", false);
        },
        /**
         * 获取营销活动信息
         */
        getActivityFromWeb() {
            let that = this;
            this.$activityRequest.getActivity({
                activityId: this.activity.id,
                onResult(isSuccess, data) {
                    if (isSuccess) {
                        if (data.status == 200) {
                            that.activity = data.result;
                        }
                    } else {
                    }
                }
            });
        }
    },

    watch: {}
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
</style>



