<template>
    <div style="width:100%; border:solid 1px #CCC">
        <div style="margin-top: 20px">
            <div style="border-bottom:solid 1px #ccc">
                <h3 style="display:inline-block;vertical-alige:middle; margin-left:3%;">客服总体情况</h3>
                <div style="display:inline-block;vertical-alige:middle; margin-left:5%;">
                    <el-radio-group v-model=" currentTeam " size="mini ">
                        <el-radio-button v-for="team in teams " :key="team.teamName " :label="team ">{{team.teamName}}</el-radio-button>
                    </el-radio-group>
                </div>
                <div style="float:right; margin-right:20px;">
                    <el-button type="text" style="display:inline-block;vertical-align:middle;line-height:1px;margin-right:3%;" @click="date=new Date();">今天</el-button>
                    <el-date-picker v-model="date" align="right" size="mini" type="date" style="display:inline-block;vertical-align:middle;width:70%;" placeholder="选择日期" :picker-options="pickerOptions1">
                    </el-date-picker>
                </div>
            </div>
            <!-- <div>
                 <el-radio-group v-model="currentMember " size="mini ">
                    <el-radio-button v-for="member in currentTeam.members " :key="member.opId " :label="member ">{{member.opName}}</el-radio-button>
                </el-radio-group> 
            </div> -->
            <el-tabs v-model="currentMemberIndex " @tab-click="memberChange ">
                <el-tab-pane v-for="member in currentTeam.members " :key="member.opId " :label="member.opName "></el-tab-pane>
            </el-tabs>
        </div>
        <div id="average " style="float:left; margin-left:10%;margin-bottom:-30px; ">
            <span style="display:inline-block;vertical-align:middle;font-size:18px; ">
                <span style="font-size:24px; ">
                    {{average}}个/
                </span>30分钟
            </span>&nbsp;&nbsp;
            <span style="display:inline-block;vertical-align:middle;color:#989898; ">
                外呼平均数
            </span>
        </div>
        <div id="bar" style="width:100%;height:300px;"></div>

    </div>
</template>
<script>
var echarts = require("echarts");
// 引入 ECharts 主模块
var echarts = require("echarts/lib/echarts");
// 引入柱状图
require("echarts/lib/chart/bar");
// 引入提示框和标题组件
require("echarts/lib/component/tooltip");
require("echarts/lib/component/title");
export default {
    name: "Bank",
    data() {
        return {
            pickerOptions1: {
                disabledDate(time) {
                    return time.getTime() > Date.now();
                }
            },
            teams: [
                {
                    teamName: "张三组",
                    members: [
                        { opId: 3001, opName: "张三1" },
                        { opId: 3002, opName: "张三2" }
                    ]
                },
                {
                    teamName: "李四组",
                    members: [
                        { opId: 4001, opName: "李四1" },
                        { opId: 4002, opName: "李四2" }
                    ]
                },
                {
                    teamName: "王五组",
                    members: [
                        { opId: 5001, opName: "王五1" },
                        { opId: 5002, opName: "王五2" }
                    ]
                }
            ],
            currentTeam: {},
            currentMember: {},
            currentMemberIndex: "0",
            date: "",
            option: {
                legend: {
                    // 图例
                    show: true,
                    zlevel: 0,
                    z: 4,
                    orient: "horizontal",
                    x: "75%",
                    y: "top",
                    backgroundColor: "rgba(0,0,0,0)",
                    borderColor: "#ccc",
                    borderWidth: 0,
                    padding: 5,
                    itemGap: 10,
                    itemWidth: 20,
                    itemHeight: 14,
                    textStyle: {
                        fontFamily: "Arial, Verdana, sans...",
                        fontSize: 12,
                        fontStyle: "normal",
                        fontWeight: "normal"
                    },
                    formatter: null,
                    selectedMode: true,
                    selected: null,
                    data: [{ name: "外呼量" }, { name: "" }, { name: "成功量" }]
                },
                xAxis: [
                    {
                        scale: true,
                        position: "bottom",
                        boundaryGap: false,
                        axisLabel: {
                            interval: 1,
                            rotate: 0
                        },
                        data: [
                            "9:00", " ","9:30", " ",
                            "10:00"," ","10:30"," ",
                            "11:00"," ","11:30"," ",
                            "12:00"," ","12:30"," ",
                            "13:00"," ","13:30"," ",
                            "14:00"," ","14:30"," ",
                            "15:00"," ","15:30"," ",
                            "16:00"," ","16:30"," ",
                            "17:00"
                        ]
                    }
                ],
                yAxis: { splitLine: { show: false } },
                series: [
                    {
                        name: "外呼量",
                        type: "bar",
                        color: "#49A9EE",
                        barWidth: "60%",
                        barGap: "-100%",
                        itemStyle: {
                            // 图形样式
                            emphasis: {
                                // 强调样式（悬浮时样式）
                                borderColor: "orange",
                                borderWidth: 2,
                                label: {
                                    show: true,
                                    position: "top",
                                    textStyle: { color: "black" }
                                }
                            }
                        },
                        data: [
                            null,12,null,20,null,36,null,21,null,11,null,22,
                            null,24,null,42,null,45,null,21,null,16,
                            null,22,null,43,null,12,null,46,null,31
                        ]
                    },
                    {
                        name: "成功量",
                        type: "bar",
                        color: "#00CCFF",
                        barWidth: "60%",
                        itemStyle: {
                            emphasis: {
                                borderColor: "orange",
                                borderWidth: 2,
                                label: {
                                    show: true,
                                    position: "top",
                                    textStyle: { color: "black" }
                                }
                            }
                        },
                        z: 10,
                        data: [
                            ,6,,9,,11,,7,,3,,3,
                            ,5,,12,,3,,6,,2,
                            ,4,,7,,6,,17,,10
                        ]
                    }
                ]
            }
        };
    },
    components: {},
    computed: {
        average() {
            var count = 0;
            for (var i = 0; i < this.option.series[0].data.length; i++) {
                count += this.option.series[0].data[i];
            }
            return Math.round(count / 16);
        }
    },
    created() {
        this.currentTeam = this.teams[0];
        this.currentMember = this.currentTeam.members[0];
    },
    methods: {
        /**切换客服 */
        memberChange(tab) {
            this.currentMember = this.currentTeam.members[tab.index];
        },
        /**初始化柱状图 */
        echartsBarInit(){
            var myChart = echarts.init(document.getElementById("bar"));
            myChart.setOption(this.option);
        }
    },
    watch: {
        currentTeam(newTeam, old) {
            this.currentMemberIndex = "0";
            this.currentMember = newTeam.members[0];
        },
        currentMember(newMember, old) {
            for(var i = 0 ;i<this.option.series[0].data.length;i++){
                if(i%2==1){
                    this.option.series[0].data[i]=(60*Math.random()).toFixed();
                    this.option.series[1].data[i]=(60*Math.random()).toFixed();
                }
            }
        },
        
    },
    mounted() {
        /*ECharts图表*/
        this.echartsBarInit();
        var ss = {
            // 当前点击的图形元素所属的组件名称，
            // 其值如 'series'、'markLine'、'markPoint'、'timeLine' 等。
            componentType: "string",
            // 系列类型。值可能为：'line'、'bar'、'pie' 等。当 componentType 为 'series' 时有意义。
            seriesType: "string",
            // 系列在传入的 option.series 中的 index。当 componentType 为 'series' 时有意义。
            seriesIndex: "number",
            // 系列名称。当 componentType 为 'series' 时有意义。
            seriesName: "string",
            // 数据名，类目名
            name: "string",
            // 数据在传入的 data 数组中的 index
            dataIndex: "number",
            // 传入的原始数据项
            data: "Object",
            // sankey、graph 等图表同时含有 nodeData 和 edgeData 两种 data，
            // dataType 的值会是 'node' 或者 'edge'，表示当前点击在 node 还是 edge 上。
            // 其他大部分图表中只有一种 data，dataType 无意义。
            dataType: "string",
            // 传入的数据值
            value: "number | Array",
            // 数据图形的颜色。当 componentType 为 'series' 时有意义。
            color: "string"
        };
        // myChart.on("mouseover", function(parmas) {
        //     console.log(parmas.value);
        // });
    }
};
</script>


