<template>
    <el-dialog title="查找用户" :visible.sync="isCurrentShow" width="80%" :before-close="handleClose" :close-on-click-modal="false">
        <el-form ref="form" size="mini" :model="form" label-position="right" label-width="30%">
            <el-row :gutter="10">
                <el-col :span="8">
                    <el-form-item label="业务号：">
                        <el-input v-model="form.userId" placeholder="请输入"></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="8">
                    <el-form-item label="设备号：">
                        <el-input v-model="form.deviceId" placeholder="请输入"></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="8">
                    <el-form-item label="订单号：">
                        <el-input v-model="form.orderNo" placeholder="请输入"></el-input>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row :gutter="10">
                <el-col :span="8">
                    <el-form-item label="CRMID：">
                        <el-input v-model="form.crmId" placeholder="请输入"></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="8">
                    <el-form-item label="手机号：">
                        <el-input v-model="form.phone" placeholder="请输入"></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="8">
                    <el-form-item>
                        <el-button>查询</el-button>
                    </el-form-item>
                </el-col>
            </el-row>
        </el-form>

        <el-table :data="tableData" v-loading="isLoading" style="width: 100%">
            <el-table-column label="订单号" align="center" show-overflow-tooltip>
                <template slot-scope="scope">
                    <span>{{ scope.row.orderNo }}</span>
                </template>
            </el-table-column>
            <el-table-column label="业务号" align="center" show-overflow-tooltip>
                <template slot-scope="scope">
                    <span>{{ scope.row.userId}}</span>
                </template>
            </el-table-column>
            <el-table-column label="设备号" align="center" show-overflow-tooltip>
                <template slot-scope="scope">
                    <span>{{ scope.row.deviceId}}</span>
                </template>
            </el-table-column>
            <el-table-column label="CRMID" align="center" show-overflow-tooltip>
                <template slot-scope="scope">
                    <span>{{ scope.row.crmId }}</span>
                </template>
            </el-table-column>

            <el-table-column label="营销日期" align="center" show-overflow-tooltip>
                <template slot-scope="scope">
                    <!--/////////////////////-->
                    <span>{{ scope.row.productCode }}</span>
                </template>
            </el-table-column>
            <el-table-column label="产品名称" align="center" show-overflow-tooltip>
                <template slot-scope="scope">
                    <!--/////////////////////-->
                    <span>{{ scope.row.productName }}</span>
                </template>
            </el-table-column>
            <el-table-column label="产品价格" align="center" show-overflow-tooltip>
                <template slot-scope="scope">
                    <!--/////////////////////-->
                    <span>{{ scope.row.price }}</span>
                </template>
            </el-table-column>
            <el-table-column label="支付方式" align="center" show-overflow-tooltip>
                <template slot-scope="scope">
                    <!--/////////////////////-->
                    <span>{{ scope.row.payType }}</span>
                </template>
            </el-table-column>
            <el-table-column label="用户姓名" align="center" show-overflow-tooltip>
                <template slot-scope="scope">
                    <span>{{ scope.row.userName }}</span>
                </template>
            </el-table-column>
            <el-table-column label="状态" align="center" show-overflow-tooltip>
                <template slot-scope="scope">
                    <span>{{ scope.row.status }}</span>
                </template>
            </el-table-column>
            <!--/////////////////////-->
            <el-table-column label="操作" type="selection" width="55" align="center">
            </el-table-column>
        </el-table>
        <div style="float:right;">
            <el-pagination class="layout-pagination" @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="pageInfo.number" :page-sizes="pageInfo.sizeArray" :page-size="pageInfo.currentSize" layout="total, sizes, prev, pager, next, jumper" :total="pageInfo.totalElements">
            </el-pagination>
        </div>
        <div style="clear:both; margin-top:70px;">
            <el-form ref="form" :model="refund" size="mini" label-width="120px" label-position="right">
                <el-form-item label="退单来源：">
                    <el-select v-model="refund.unorderSource" placeholder="请选择退单来源">
                        <el-option label="区域一" value="source1"></el-option>
                        <el-option label="区域二" value="beijing"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="退单原因：">
                    <el-input type="textarea" v-model="refund.unorderReason" placeholder="请输入退单原因"></el-input>
                </el-form-item>
                <el-form-item style="float:right;">
                    <el-button type="primary">提交</el-button>
                </el-form-item>
                <div style="clear:both;"></div>
            </el-form>
        </div>
    </el-dialog>
</template>
<script>
export default {
    props: {},
    data() {
        return {
            form: {},
            tableData: [],
            pageInfo: {
                currentSize: 10,
                sizeArray: [10, 20, 50, 100],
                number: 1,
                totalElements: 0
            },
            isLoading: false,
            refund: {}
        };
    },
    created() {},
    computed: {
        isCurrentShow() {
            return this.$store.state.orderAndRefund.showSearchUserDlg;
        }
    },
    methods: {
        handleClose() {
            this.$store.commit("SET_SHOW_SEARCHUSERDLG", false);
        },
        /**
         * 将page 数据赋值
         */
        handlePageInfo(data) {
            this.pageInfo.totalElements = data.page.totalElements;
            console.log(this.pageInfo);
        },
        /**
         * 改变了每页数据
         */
        handleSizeChange(val) {
            console.log(`每页 ${val} 条`);
            this.pageInfo.currentSize = val;
            this.getBaseProductListFromWeb();
        },
        /**
         * 处理当前的改变
         */
        handleCurrentChange(val) {
            console.log(`当前页: ${val}`);
            this.pageInfo.number = val;
            this.getBaseProductListFromWeb();
        },
        /**
         * 重置 pageInfo
         */
        resetPageInfo() {
            this.pageInfo.currentSize = this.pageInfo.sizeArray[0];
            this.pageInfo.number = 1;
        }
    }
};
</script>
<style scoped>
</style>





