<template>
    <div>
        <h3>
            <span style="color:red;font-weight:700;margin-right:6px;">●</span>已记录的黑名单查询
        </h3>
        <el-form :inline="true" size="mini" :model="formInline" class="demo-form-inline">
            <el-row :gutter="20">
                <el-col :span="10">
                    <el-form-item label="业务号:">
                        <el-input v-model="formInline.userId" placeholder="请输入"></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="10">
                    <el-form-item label="姓名:">
                        <el-input v-model="formInline.userName" placeholder="请输入"></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="4">
                    <el-form-item>
                        <el-button type="primary" style="margin-top:-5px;" @click="searchBlackList">查询</el-button>
                    </el-form-item>
                </el-col>
            </el-row>
        </el-form>
        <el-table :data="blackList" v-loading="isLoading" style="width: 100%">
            <el-table-column label="业务号" align="center" show-overflow-tooltip>
                <template slot-scope="scope">
                    <span>{{ scope.row.userId }}</span>
                </template>
            </el-table-column>
            <el-table-column label="姓名" align="center" show-overflow-tooltip>
                <template slot-scope="scope">
                    <span>{{ scope.row.userName}}</span>
                </template>
            </el-table-column>
            <el-table-column label="电话" align="center" show-overflow-tooltip>
                <template slot-scope="scope">
                    <span>{{ scope.row.phone}}</span>
                </template>
            </el-table-column>
            <el-table-column label="地址" align="center" show-overflow-tooltip>
                <template slot-scope="scope">
                    <span>{{ scope.row.address }}</span>
                </template>
            </el-table-column>

            <el-table-column label="加入黑名单时间" align="center" show-overflow-tooltip>
                <template slot-scope="scope">
                    <span>{{ scope.row.createTime }}</span>
                </template>
            </el-table-column>
        </el-table>
        <div style="float:right;">
            <el-pagination class="layout-pagination" @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="pageInfo.number" :page-sizes="pageInfo.sizeArray" :page-size="pageInfo.currentSize" layout="total, sizes, prev, pager, next, jumper" :total="pageInfo.totalElements">
            </el-pagination>
        </div>
        <div style="clear:both;"></div>
    </div>
</template>

<script>
export default {
    components: {},
    props: {},
    data() {
        return {
            formInline: {
                userId: "",
                orderNo: "",
                userName: ""
            },
            blackList: [
                {
                    address: "string",
                    createTime: "2018-08-28T07:07:59.344Z",
                    crmId: "string",
                    deviceId: "string",
                    id: 0,
                    opId: 0,
                    opName: "string",
                    phone: "string",
                    point: 0,
                    userId: "string",
                    userName: "string",
                    userType: 0
                }
            ],
            pageInfo: {
                currentSize: 10,
                sizeArray: [10, 20, 50, 100],
                number: 1,
                totalElements: 0
            },
            isLoading: false
        };
    },

    created() {
        this.getBlackListFromWeb();
    },
    filters: {
        // 对手机号、地址处理
    },
    methods: {
        /**
         * 获取黑名单列表
         */
        getBlackListFromWeb() {
            // this.isLoading = true;
            let that = this;
            this.$blackListRequest.getBlackList({
                page: this.pageInfo.number - 1,
                size: this.pageInfo.currentSize,
                userId: this.formInline.userId,
                userName: this.formInline.userName,
                onResult(isSuccess, data) {
                    if (isSuccess) {
                        that.handleGetBlackListSuccessResponse(data);
                    } else {
                    }
                    that.isLoading = false;
                }
            });
        },
        handleGetBlackListSuccessResponse(data) {
            this.handlePageInfo(data);
            this.blackList = data.result;
        },
        /**
         * 将page 数据赋值
         */
        handlePageInfo(data) {
            this.pageInfo.totalElements = data.page.totalElements;
        },
        /**
         * 改变了每页数据
         */
        handleSizeChange(val) {
            this.pageInfo.currentSize = val;
            this.getBlackListFromWeb();
        },
        /**
         * 处理当前的改变
         */
        handleCurrentChange(val) {
            this.pageInfo.number = val;
            this.getBlackListFromWeb();
        },
        /**
         * 重置 pageInfo
         */
        resetPageInfo() {
            this.pageInfo.currentSize = this.pageInfo.sizeArray[0];
            this.pageInfo.number = 1;
        },
        /**
         * 表单查找黑名单
         */
        searchBlackList() {
            this.resetPageInfo();
            this.getBlackListFromWeb();
        }
    },

    watch: {}
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
</style>



