<template>
    <div class="main-container gray-bg">
        <div v-if="currentAppo" style="width:100%; background-color:red; height:30px; color:white; text-align:center; font-weight:700; line-height:30px;">
            您【 {{currentAppo.appoDate}} 】有预约客户,请准时联系！
        </div><br>
        <el-form style="float:right;" :inline="true" size="mini" class="demo-form-inline">
            <el-form-item>
                <el-input v-model="name" placeholder="请输入任务名称"></el-input>
            </el-form-item>
            <el-form-item>
                <el-button type="primary" style="margin-top:-5px;" @click="searchActivityList">搜索</el-button>
            </el-form-item>
        </el-form>
        <el-table v-loading="isLoading" :data="myActivities" style="width: 100%" row-key="activity.id" :expand-row-keys="expands" @row-click="rowClick">
            <el-table-column type="expand">
                <template slot-scope="scope" v-if="scope.row.willCount==0">
                    <span style="height:100px;">该活动暂时没有意向用户</span>
                </template>
                <template slot-scope="scope" v-if="scope.row.willCount>0">
                    <el-table :data="callRecords[scope.row.activity.id]" v-loading="isLoading" style="width: 90%">
                        <el-table-column prop="userId" label="业务号" align="center" show-overflow-tooltip>
                        </el-table-column>
                        <el-table-column prop="userName" label="用户姓名" align="center" show-overflow-tooltip>
                        </el-table-column>
                        <el-table-column prop="userPhone" label="电话" align="center" show-overflow-tooltip>
                        </el-table-column>
                        <el-table-column prop="callTime" label="拨打日期" align="center" show-overflow-tooltip>
                        </el-table-column>
                        <el-table-column prop="remark" label="拨打备注" align="center" show-overflow-tooltip>
                        </el-table-column>
                        <el-table-column label="操作" align="center">
                            <template slot-scope="scope">
                                <el-button type="text" @click="callwillPage(scope.row)">呼叫</el-button>
                            </template>
                        </el-table-column>
                    </el-table>
                </template>
            </el-table-column>
            <el-table-column label="任务名称" align="center" show-overflow-tooltip>
                <template slot-scope="scope">
                    <span>{{ scope.row.activity.name }}</span>
                </template>
            </el-table-column>
            <el-table-column label="活动类型" align="center" show-overflow-tooltip>
                <template slot-scope="scope">
                    <span>{{ scope.row.activity.type | activityTypeFilter}}</span>
                </template>
            </el-table-column>
            <el-table-column label="开始时间" align="center" show-overflow-tooltip>
                <template slot-scope="scope">
                    <span>{{ scope.row.activity.validTime}}</span>
                </template>
            </el-table-column>
            <el-table-column label="您已营销人数" align="center" show-overflow-tooltip>
                <template slot-scope="scope">
                    <span>{{ scope.row.calledCount }}</span>
                </template>
            </el-table-column>
            <el-table-column label="您成功订单数" align="center" show-overflow-tooltip>
                <template slot-scope="scope">
                    <span>{{ scope.row.orderedCount }}</span>
                </template>
            </el-table-column>
            <el-table-column label="支付方式" align="center" show-overflow-tooltip>
                <template slot-scope="scope">
                    <span>{{ scope.row.activity.payType | payTypeFilter }}</span>
                </template>
            </el-table-column>
            <el-table-column label="有意向用户" align="center" show-overflow-tooltip>
                <template slot-scope="scope">
                    <span v-if="scope.row.willCount==0">0</span>
                    <el-button type="text" v-else-if="scope.row.appoCount==0" style="color:#0E7CCC;font-weight:600;">{{scope.row.willCount}}</el-button>
                    <el-button type="text" v-else style="color:red;font-weight:600;">{{ scope.row.willCount }}</el-button>
                </template>
            </el-table-column>
            <el-table-column label="操作" align="center" show-overflow-tooltip>
                <template slot-scope="scope">
                    <el-button type="text" @click="callPage(scope.row.activity)">进入</el-button>
                    <el-button type="text" @click="assignUser(scope.row.activity)">领取用户</el-button>
                </template>
            </el-table-column>
        </el-table>
        <div style="float:right;">
            <el-pagination class="layout-pagination" @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="pageInfo.number" :page-sizes="pageInfo.sizeArray" :page-size="pageInfo.currentSize" layout="total, sizes, prev, pager, next, jumper" :total="pageInfo.totalElements">
            </el-pagination>
        </div>
        <div style="clear:both;"></div>
        <call-page :activity="currentActivity" @refreshMyActivity="getMyActivitiesFromWeb" />
        <call-will-page :callRecord="currentCallRecord" @refreshMyActivity="getMyActivitiesFromWeb" />
        <!-- <willing-user /> -->
    </div>
</template>

<script>
import Constant from "@/assets/js/Constant";
import CallPage from "./CallPage";
import CallWillPage from "./CallWillPage";
export default {
    components: {
        CallPage,
        CallWillPage
    },
    props: {},
    data() {
        return {
            name: "", //表单查询
            currentActivity: {}, // 进入外呼的营销活动
            currentCallRecord: {}, // 进入外呼的有意向用户的拨打记录
            callRecords: {}, // 有意向用户的拨打记录
            myActivities: [
                {
                    activity: {
                        autoOpen: 0,
                        createTime: "2018-08-24T06:07:17.167Z",
                        expireTime: "2018-08-24T06:07:17.167Z",
                        id: 0,
                        ivr: "string",
                        maxPoint: 0,
                        minPoint: 0,
                        name: "string",
                        payType: 0,
                        script: "string",
                        smsParameter: "string",
                        smsTemplate: "string",
                        spCode: "string",
                        status: 0,
                        type: 0,
                        validTime: "2018-08-24T06:07:17.167Z"
                    },
                    appoCount: 0, // 当天预约数
                    calledCount: 0, //已营销用户数
                    willCount: 1, // 意向用户数
                    orderedCount: 0 // 成功订购用户数
                }
            ],
            pageInfo: {
                currentSize: 10,
                sizeArray: [10, 20, 50, 100],
                number: 1,
                totalElements: 0
            },
            isLoading: false,
            // 要展开的行，数值的元素是row的key值
            expands: [],
            currentAppo: null // 最近的预约营销对象拨打记录
        };
    },
    created() {
        this.getMyActivitiesFromWeb();
        this.getCurrentAppo();
    },
    mounted() {
        if (this.timer) {
            clearInterval(this.timer);
        } else {
            this.timer = setInterval(() => {
                this.getCurrentAppo();
            }, 300000);
        }
    },
    destroyed() {
        clearInterval(this.timer);
    },
    methods: {
        /**查询最近的预约营销对象 */
        getCurrentAppo() {
            let that = this;
            this.$callRecordRequest.getCurrentAppo({
                opId: 1,
                // opId: window.parent.userInfo.userId,
                onResult(isSuccess, data) {
                    if (isSuccess) {
                        that.currentAppo = data.result;
                    }
                    that.currentAppo = null;
                }
            });
        },
        /**
         * 单击行展开
         */
        //在<table>里，我们已经设置row的key值设置为每行数据id：row-key="id"
        rowClick(row, event, column) {
            let that = this;
            // 判断是否有意向用户
            if (row.willCount == 0) {
                return;
            } else {
                if (this.expands.indexOf(row.activity.id) < 0) {
                    // 有,获取意向用户对应的拨打记录
                    this.$callRecordRequest.willCallRecordList({
                        activityId: row.activity.id,
                        opId: 101,
                        //opId: window.parent.userInfo.userId,
                        onResult(isSuccess, data) {
                            if (isSuccess) {
                                if (data.status == 200) {
                                    that.callRecords[row.activity.id] =
                                        data.result;
                                }
                            } else {
                            }
                        }
                    });
                    this.expands.push(row.activity.id);
                } else {
                    this.expands.remove(row.activity.id);
                }
                Array.prototype.remove = function(val) {
                    let index = this.indexOf(val);
                    if (index > -1) {
                        this.splice(index, 1);
                    }
                };
            }
        },
        /**
         * 根据当前坐席获取营销活动列表
         */
        getMyActivitiesFromWeb() {
            this.isLoading = true;
            let that = this;
            this.$activityRequest.myActivities({
                page: this.pageInfo.number - 1,
                size: this.pageInfo.currentSize,
                activityName: this.name,
                opId: 101,
                // opId: window.parent.userInfo.userId,
                onResult(isSuccess, data) {
                    if (isSuccess) {
                        that.handleGetMyActivitiesSuccessResponse(data);
                    } else {
                        that.$message({
                            type: "error",
                            message: "当前网络异常"
                        });
                    }
                    that.isLoading = false;
                }
            });
        },
        handleGetMyActivitiesSuccessResponse(data) {
            if (data.status == 200) {
                this.handlePageInfo(data);
                this.myActivities = data.result;
            } else {
                this.$message({ type: "error", message: "服务器异常" });
            }
        },
        /**
         * 将page 数据赋值
         */
        handlePageInfo(data) {
            this.pageInfo.totalElements = data.page.totalElements;
        },
        /**
         * 改变了每页数据
         */
        handleSizeChange(val) {
            this.pageInfo.currentSize = val;
            this.getMyActivitiesFromWeb();
        },
        /**
         * 处理当前的改变
         */
        handleCurrentChange(val) {
            this.pageInfo.number = val;
            this.getMyActivitiesFromWeb();
        },
        /**
         * 重置 pageInfo
         */
        resetPageInfo() {
            this.pageInfo.currentSize = this.pageInfo.sizeArray[0];
            this.pageInfo.number = 1;
        },
        /**
         * 点击按条件查找
         */
        searchActivityList() {
            this.resetPageInfo();
            this.getMyActivitiesFromWeb();
        },
        /**领取用户 */
        assignUser(activity) {
            //js阻止事件冒泡
            event.cancelBubble = false;
            event.stopPropagation();
            let that = this;
            this.$activityRequest.getMission({
                activityId: activity.id,
                opId: 101,
                //opId: window.parent.userInfo.userId,
                onResult(isSuccess, data) {
                    if (isSuccess) {
                        if (data.status == 200 && data.result > 0) {
                            that.$message({
                                type: "success",
                                message: "成功领取" + data.result + "个用户"
                            });
                        } else if (data.status == 200 && data.result == 0) {
                            that.$message({
                                type: "warning",
                                message: "该营销活动暂时没有营销对象"
                            });
                        } else if (data.status == 500 && data.code != 1000) {
                            that.$message({
                                type: "error",
                                message: data.msg
                            });
                        }
                    } else {
                        that.$message({
                            type: "error",
                            message: "当前网络异常"
                        });
                    }
                }
            });
        },
        /**点击进入拨打页面 */
        callPage(activity) {
            //js阻止事件冒泡
            event.cancelBubble = false;
            event.stopPropagation();
            this.currentActivity = activity;
            this.$store.commit("SET_SHOW_CALLPAGE", true);
        },
        /**点击拨打按钮，拨打意向用户 */
        callwillPage(callRecord) {
            this.currentCallRecord = callRecord;
            this.$store.commit("SET_SHOW_CALLWILLPAGE", true);
        },
        /**查询当前坐席的有意向用户记录 */
        getWillCallRecord() {
            let that = this;
            this.$callRecordRequest.getWillCallRecord({
                opId: 101,
                //opId: window.parent.userInfo.userId,
                onResult(isSuccess, data) {
                    if (isSuccess) {
                        that.callRecords = data.result;
                    } else {
                    }
                }
            });
        }
    },
    filters: {
        activityTypeFilter(type) {
            return Constant.activityTypeMap[type];
        },
        payTypeFilter(payType) {
            return Constant.payTypeMap[payType];
        }
    },
    watch: {
        myActivities(newActivitys, old) {
            if (newActivitys) {
                for (var i = 0; i < newActivitys.length; i++) {
                    this.callRecords[newActivitys[i].activity.id] = [];
                }
            }
        }
    }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.bg {
    background-color: white;
    height: 100%;
    padding: 10px;
}
</style>



