<template>
    <el-dialog title="" :visible.sync="isCurrentShow" width="61.8%" :before-close="handleClose" :close-on-click-modal="false">
        <div style="margin-top:-30px;margin-bottom:10px;">
            <i class="el-icon-warning" style="color:red;font-size:36px; display:inline-block; vertical-align:middle;"></i>
            <span style="margin-left:12px;font-size:20px;display:inline-block; vertical-align:middle;">详情</span>
        </div>
        <el-form style="padding-left:30%;" ref="form" size="mini" :model="form" label-position="left" label-width="20%">
            <el-form-item label="用户姓名：">
                <span>{{form.userName}}</span>
            </el-form-item>
            <el-form-item label="产品名称：">
                <span>{{form.productName}}</span>
            </el-form-item>
            <el-form-item label="产品ID：">
                <span>{{form.productCode}}</span>
            </el-form-item>
            <el-form-item label="开通月数：">
                <span>{{form.cycleCount}}</span>
            </el-form-item>
            <el-form-item label="所需分数：">
                <span>{{form.point}}</span>
            </el-form-item>
            <el-form-item label="手机号：">
                <span>{{form.userPhone}}</span>
            </el-form-item>
            <el-form-item label="电话：">
                <span>{{form.orderPhone}}</span>
            </el-form-item>
            <el-form-item label="结果为：">
                <span v-if="form.status===1" class="success">{{form.status | orderStatusFilter}}</span>
                <span v-else class="failed">{{form.status | orderStatusFilter}}</span>
            </el-form-item>
        </el-form>
    </el-dialog>
</template>
<script>
import Constant from "@/assets/js/Constant";
export default {
    props: { form: {} },
    data() {
        return {};
    },
    computed: {
        isCurrentShow() {
            return this.$store.state.orderAndRefund.showOrderApplyDetail;
        }
    },
    methods: {
        handleClose() {
            this.$store.commit("SET_SHOW_ORDERAPPLYDETAIL", false);
        }
    },
    filters: {
        orderStatusFilter(status) {
            return Constant.orderStatusMap[status];
        }
    }
};
</script>
<style scoped>
.el-input {
    width: 50%;
}
span {
    color: #929292;
}
.success {
    font-weight: 700;
    color: #008000;
}
.failed {
    font-weight: 700;
    color: red;
}
</style>





