const user = {
    state: {
        opName: "",
        opId: "",
        roles: []
    },
    mutations: {
        SET_OPNAME: (state, opName) => {
            state.opName = opName
        },
        SET_OPID: (state, opId) => {
            state.opId = opId
        },
        SET_ROLES: (state, roles) => {
            state.roles = roles
        }
    },
    actions: {
        // 获取用户信息
        GetUserInfo({
            commit,
            state
        }) {
            return new Promise((resolve, reject) => {
                console.log("store GetUserInfo");
                let res = {
                    data: {
                        role: ["admin"]
                    }
                }
                commit('SET_ROLES', res.data.role)
                resolve(res);
            })
        },
        // 前端 登出
        FedLogOut({
            commit
        }) {
            return new Promise(resolve => {
                // 将token 清空
                // commit('SET_TOKEN', '')
                // removeToken()
                resolve()
            })
        },
    }
}

export default user;


