<template>
    <el-dialog title="" :visible.sync="isCurrentShow" width="96%" :before-close="handleClose" :close-on-click-modal="false">
        <el-row :gutter="4">
            <el-col :span="6">
                <div class="ibox-content">
                    <left-table :activity="activity" />
                </div>
            </el-col>
            <el-col :span="18">
                <div class="ibox-content">
                    <right-content :activity="activity" @refreshMyActivity="refreshMyActivity" />
                </div>
            </el-col>
        </el-row>
    </el-dialog>
</template>

<script>
import LeftTable from "@/business/customerServiceMission/view/LeftTable";
import RightContent from "@/business/customerServiceMission/view/RightContent";
export default {
    components: {
        LeftTable,
        RightContent
    },
    props: {
        activity: {}
    },
    data() {
        return {
            isLoading: false,
            loadingMsg: ""
        };
    },
    computed: {
        isCurrentShow() {
            return this.$store.state.customerMission.showCallPage;
        }
    },
    created() {},

    methods: {
        /**关闭弹窗 */
        handleClose() {
            this.$store.commit("SET_SHOW_CALLPAGE", false);
        },
        /**刷新该坐席的营销活动列表 */
        refreshMyActivity() {
            this.$emit("refreshMyActivity");
        }
    },

    watch: {}
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
</style>



