<template>
    <div class="tree">
        <h3>
            <span style="color:#7EC856;font-weight:700;margin-right:6px;">●</span>在线查询</h3>
        <el-form :inline="true" class="demo-form-inline" size="mini">
            <el-row :gutter="2">
                <el-col :span="18">
                    <el-form-item style="margin-top:3;">
                        <el-input v-model="userId" placeholder="请输入业务号"></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="6">
                    <el-form-item>
                        <el-button type="primary" @click="searchSubscriber">搜索</el-button>
                    </el-form-item>
                </el-col>
            </el-row>
        </el-form>
        <table style="border:solid 1px rgb(233,233,233);">
            <tr>
                <td class="colum">业务号</td>
                <td>{{subscriber.userId}}</td>
            </tr>
            <tr>
                <td class="colum">用户姓名</td>
                <td>{{subscriber.userName}}</td>
            </tr>
            <tr>
                <td class="colum">设备号</td>
                <td>{{subscriber.deviceId}}</td>
            </tr>
            <tr>
                <td class="colum">电话</td>
                <td>{{subscriber.phone}}</td>
            </tr>
            <tr>
                <td class="colum">地址</td>
                <td>{{subscriber.address}}</td>
            </tr>
            <tr>
                <td class="colum">设置</td>
                <td>
                    <el-button type="text" @click="addBlackList">添加到黑名单</el-button>
                </td>
            </tr>
        </table>
    </div>
</template>

<script>
export default {
    data() {
        return {
            subscriber: {
                address: "string",
                crmId: "string",
                deviceId: "string",
                phone: "string",
                point: 0,
                userId: "string",
                userName: "string",
                userType: 0
            },
            userId: ""
        };
    },
    methods: {
        searchSubscriber() {
            if (!this.userId) {
                return;
            }
            let that = this;
            this.$userManageRequest.searchSubscriber({
                userId: this.userId,
                onResult(isSuccess, data) {
                    if (isSuccess && data.code == 200) {
                        that.subscriber = data.result;
                    } else {
                    }
                }
            });
        },
        /**添加到黑名单 */
        addBlackList() {
            this.$blackListRequest.addBlackList({
                subscriber: this.subscriber,
                onResult(isSuccess, data) {
                    if (isSuccess && data.code == 200) {
                        that.$message({
                            type: "success",
                            message: data.msg
                        });
                    } else {
                    }
                }
            });
        }
    },
    filters: {
        // 处理用户手机号和住址
    },
    watch: {}
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
td {
    text-align: center;
    border: 1px solid #cccccc;
    height: auto;
}
.colum {
    padding: 6px;
    font: 1.4em sans-serif;
    font-weight: 500;
    color: black;
    width: 40%;
}
.tree {
    width: 80%;
    margin: auto;
}
</style>



