<template>
    <div style="width:100%; border:solid 1px #CCC">
        <div style="margin-top: 20px">
            <div style="border-bottom:solid 1px #ccc">
                <h3 style="display:inline-block;vertical-alige:middle; margin-left:3%;">时间段客服情况</h3>
                <el-radio-group v-model="currentTeam" size="mini" style="margin-left:3%;">
                    <el-radio-button v-for="team in teams" :key="team.teamName " :label="team ">{{team.teamName}}</el-radio-button>
                </el-radio-group>
                <el-button type="text" style="display:inline-block;vertical-align:middle;line-height:1px; margin-left:3%" @click="today">今日</el-button>
                <el-button type="text" style="display:inline-block;vertical-align:middle;line-height:1px;" @click="currentWeek">本周</el-button>
                <el-button type="text" style="display:inline-block;vertical-align:middle;line-height:1px;" @click="currentMonth">本月</el-button>
                <el-date-picker v-model="dateTime" size="mini" style="display:inline-block;vertical-align:middle;margin-bottom:5px;width:40%;" type="datetimerange" range-separator="-" start-placeholder="开始日期" end-placeholder="结束日期">
                </el-date-picker>
            </div>
        </div>
        <div id="line" style="width:100%;height:300px;"></div>
        <div>
            <table style="border:solid 1px #ccc;width:94%;margin-left:3%;">
                <tr>
                    <td style="font-weight: 700;">代号</td>
                    <td v-for="(member,key) in currentTeam.members" :key="key" v-if="key<13">{{key+1 | keyFilter}}</td>
                </tr>
                <tr>
                    <td style="font-weight: 700;">客服名 </td>
                    <td v-for="(member,key) in currentTeam.members" :key="key" v-if="key<13">{{member.opName}}</td>
                </tr>
                <tr v-if="currentTeam.members.length>13">
                    <td style="font-weight: 700;">代号</td>
                    <td v-for="(member,key) in currentTeam.members" :key="key" v-if="key>=13">{{key+1}}</td>
                </tr>
                <tr v-if="currentTeam.members.length>13">
                    <td style="font-weight: 700;">客服名 </td>
                    <td v-for="(member,key) in currentTeam.members" :key="key" v-if="key>=13">{{member.opName}}</td>
                </tr>
            </table>
        </div>
    </div>
</template>
<script>
var echarts = require("echarts");
// 引入 ECharts 主模块
var echarts = require("echarts/lib/echarts");
// 引入柱状图
require("echarts/lib/chart/bar");
export default {
    name: "Bank",
    data() {
        return {
            pickerOptions1: {
                disabledDate(time) {
                    return time.getTime() > Date.now();
                }
            },
            teams: [
                {
                    teamName: "张三组",
                    members: [
                        { opId: 3001, opName: "张三1" },
                        { opId: 3002, opName: "张三2" },
                        { opId: 3001, opName: "张三3" },
                        { opId: 3002, opName: "张三4" },
                        { opId: 3001, opName: "张三5" },
                        { opId: 3002, opName: "张三6" },
                        { opId: 3001, opName: "张三7" },
                        { opId: 3002, opName: "张三8" },
                        { opId: 3001, opName: "张三9" },
                        { opId: 3002, opName: "张三10" },
                        { opId: 3001, opName: "张三11" },
                        { opId: 3002, opName: "张三12" },
                        { opId: 3001, opName: "张三13" },
                        { opId: 3002, opName: "张三14" },
                        { opId: 3001, opName: "张三15" },
                        { opId: 3002, opName: "张三16" },
                        { opId: 3001, opName: "张三17" },
                        { opId: 3002, opName: "张三18" },
                        { opId: 3001, opName: "张三19" },
                        { opId: 3002, opName: "张三20" }
                    ]
                },
                {
                    teamName: "李四组",
                    members: [
                        { opId: 4001, opName: "李四1" },
                        { opId: 4002, opName: "李四2" }
                    ]
                }
            ],
            currentTeam: {},
            dateTime: [],
            option: {
                tooltip: {
                    trigger: "axis"
                    //formatter: "{b}<br/>{a0}:{c0}<br/>{a1}:{c1}"
                },
                legend: {
                    x: "30%",
                    data: [
                        { name: "外呼量", icon: "bar" },
                        { name: "成功量", icon: "bar" }
                    ]
                },
                xAxis: [
                    {
                        scale: true,
                        position: "bottom",
                        boundaryGap: false,
                        axisLabel: {
                            interval: 0,
                            rotate: 0
                        },
                        data: []
                    }
                ],
                yAxis: { splitLine: { show: false } },
                series: [
                    {
                        name: "外呼量",
                        type: "line",
                        color: "#49A9EE",
                        data: []
                    },
                    {
                        name: "成功量",
                        type: "line",
                        color: "#98D87D",
                        data: []
                    }
                ]
            }
        };
    },
    components: {},
    computed: {
        average() {
            var count = 0;
            for (var i = 0; i < this.option.series[0].data.length; i++) {
                count += this.option.series[0].data[i];
            }
            return Math.round(count / 16);
        }
    },
    created() {
        this.currentTeam = this.teams[0];
        for (var i = 1; i <= this.currentTeam.members.length; i++) {
            var num = (100 * Math.random()).toFixed();
            this.option.series[0].data.push(num);
            this.option.series[1].data.push((num / 2).toFixed());
            this.option.xAxis[0].data.push(i < 10 ? "0" + i : "" + i);
        }
    },
    methods: {
        /**今天 */
        today() {
            var start = new Date(new Date().toLocaleDateString()); // yyyy-MM-dd 00:00:00
            this.dateTime = [
                start,
                new Date(start.getTime() + 24 * 3600 * 1000 - 1000)
            ];
        },
        /**本周 */
        currentWeek() {
            var today = new Date(new Date().toLocaleDateString()); // yyyy-MM-dd 00:00:00
            var start =
                today.getTime() - new Date().getDay() * 24 * 3600 * 1000;
            this.dateTime = [
                new Date(start),
                new Date(start + 7 * 24 * 3600 * 1000 - 1000)
            ];
        },
        /**本月 */
        currentMonth() {
            var today = new Date(new Date().toLocaleDateString()); // yyyy-MM-dd 00:00:00
            var start =
                today.getTime() - (new Date().getDate() - 1) * 24 * 3600 * 1000;
            var dayCount = new Date( // 当月的天数
                new Date().getFullYear(),
                new Date().getMonth() + 1,
                0
            ).getDate();
            this.dateTime = [
                new Date(start),
                new Date(start + dayCount * 24 * 3600 * 1000 - 1000)
            ];
        },
        /**折线图初始化 */
        echartsLineInit() {
            var myChart = echarts.init(document.getElementById("line"));
            myChart.setOption(this.option);
        }
    },
    filters: {
        keyFilter(key) {
            return key > 9 ? key : "0" + key;
        }
    },
    watch: {
        currentTeam(newTeam, old) {
            this.option.series[0].data.splice(0);
            this.option.series[1].data.splice(0);
            this.option.xAxis[0].data.splice(0);
            for (var i = 1; i <= newTeam.members.length; i++) {
                var num = (100 * Math.random()).toFixed();
                this.option.series[0].data.push(num);
                this.option.series[1].data.push((num / 2).toFixed());
                this.option.xAxis[0].data.push(i < 10 ? "0" + i : "" + i);
            }
            this.echartsLineInit();
        }
    },

    mounted() {
        /*ECharts图表*/
        this.echartsLineInit();
        // var myChart = echarts.init(document.getElementById("line"));
        // myChart.setOption(this.option);
    }
};
</script>
<style scoped>
td {
    border: solid 1px #ccc;
    text-align: center;
    color: black;
}
</style>

 


