const baseProduct = {
    state: {
        showCallPage: false,
        showCallWillPage: false
    },
    mutations: {
        SET_SHOW_CALLPAGE(state, data) {
            state.showCallPage = data;
        },
        SET_SHOW_CALLWILLPAGE(state, data) {
            state.showCallWillPage = data;
        }
    },
    actions: {}
};
export default baseProduct;


