import Vue from 'vue'
import axios from 'axios'
import Constant from '@/assets/js/Constant'

function logout() {

}

function exit(code) {

}

/**
 * 统一处理成功回应
 */
function handleSuccessResponse(holder, data) {
  holder.onResult(true, data);
}

/**
 * 统一处理失败回应
 */
function handleFailResponse(holder, xhr, textStatus, errorThrown) {
  var data = {
    xhr: xhr,
    textStatus: textStatus,
    error: errorThrown
  }
  holder.onResult(false, data);
}

export var RequestHelper = {
  /**
   * 查询订购申请列表
   */
  orderApplyList(holder) {
    if (Constant.api.orderApply.mock) return
    let api = Constant.serverUrl + Constant.api.orderApply.orderApplyList;
    let that = this;
    axios.get(api).then(success => {
      handleSuccessResponse(holder, success.data);
    }).catch(error => {
      handleFailResponse(holder, error);
    });
  },
  /**
   * 添加订购申请
   */
  orderApplyList(holder) {
    if (Constant.api.orderApply.mock) return
    let api = Constant.serverUrl + Constant.api.orderApply.addOrderApply;
    let that = this;
    axios.post(api, {
      order: holder.order
    }).then(success => {
      handleSuccessResponse(holder, success.data);
    }).catch(error => {
      handleFailResponse(holder, error);
    });
  },
  /**
   * 重置订购申请状态
   */
  resetOrderApply(holder) {
    if (Constant.api.orderApply.mock) return
    let api = Constant.serverUrl + Constant.api.orderApply.resetOrderApply +
      holder.recordId;
    let that = this;
    axios.post(api).then(success => {
      handleSuccessResponse(holder, success.data);
    }).catch(error => {
      handleFailResponse(holder, error);
    });
  },
};

function install(Vue) {
  Vue.prototype.$orderApplyRequest = RequestHelper;
};

export default install;


