const recordAndCount = {
    state: {
        showSendMsgOffLine: false,
    },
    mutations: {
        SET_SHOW_SENDMSGOFFLINE(state, data) {
            state.showSendMsgOffLine = data;
        }
    },
    actions: {}
};
export default recordAndCount;


