<template>
    <div class="tree">
        <h3>
            <span style="color:#7EC856;font-weight:700;margin-right:6px;">●</span>任务进度</h3>
        <table style="border:solid 1px rgb(233,233,233); width:100%;">
            <tr>
                <td class="colum">总营销人数</td>
                <td>{{activity.validTime}}</td>
            </tr>
            <tr>
                <td class="colum">未领取数</td>
                <td>{{activity.payType}}</td>
            </tr>
            <tr>
                <td class="colum">总领取数</td>
                <td>{{activity.deviceId}}</td>
            </tr>
            <tr>
                <td class="colum">预订购成功率(预订购/已拨打)</td>
                <td>{{activity.validTime}}</td>
            </tr>
            <tr>
                <td class="colum">实际开通率</td>
                <td>{{activity.payType}}</td>
            </tr>
            <tr>
                <td class="colum">未拨打</td>
                <td>{{activity.deviceId}}</td>
            </tr>
            <tr>
                <td class="colum">营销前筛选</td>
                <td>{{activity.validTime}}</td>
            </tr>
            <tr>
                <td class="colum">已拨打数(不含无人接听)</td>
                <td>{{activity.payType}}</td>
            </tr>
            <tr style="background-color:#FFCDFF;">
                <td class="colum">营销失败</td>
                <td>{{activity.deviceId}}</td>
            </tr>
            <tr>
                <td class="colum">接通数</td>
                <td>{{activity.validTime}}</td>
            </tr>
            <tr style="background-color:#BED392;">
                <td class="colum">预订购</td>
                <td>{{activity.payType}}</td>
            </tr>
            <tr style="background-color:#BED392;">
                <td class="colum">实际开通(去除投诉)</td>
                <td>{{activity.deviceId}}</td>
            </tr>
            <tr style="background-color:#BED392;">
                <td class="colum">待开通</td>
                <td>{{activity.validTime}}</td>
            </tr>
            <tr>
                <td class="colum">未完成投诉退单</td>
                <td>{{activity.payType}}</td>
            </tr>
            <tr>
                <td class="colum">投诉结单</td>
                <td>{{activity.deviceId}}</td>
            </tr>
            <tr>
                <td class="colum">今日预订购</td>
                <td>{{activity.validTime}}</td>
            </tr>
            <tr>
                <td class="colum">今日实际开通</td>
                <td>{{activity.payType}}</td>
            </tr>
        </table>
    </div>
</template>

<script>
export default {
    props: {},
    data() {
        return {
            activity: {}
        };
    },
    created() {},
    methods: {},
    filters: {},
    watch: {}
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
td {
    text-align: center;
    border: 1px solid #cccccc;
    height: auto;
}
.colum {
    padding: 6px;
    font: 1.2em sans-serif;
    font-weight: 500;
    color: black;
    width: 40%;
}
.tree {
    width: 90%;
    margin: auto;
}
</style>



