<template>
    <div class="main-container gray-bg">
        <el-form ref="form" size="mini" :model="form" label-position="right" label-width="100px">
            <el-row :gutter="10">
                <el-col :span="8">
                    <el-form-item label="业务号：">
                        <el-input v-model="form.userId" placeholder="请输入"></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="8">
                    <el-form-item label="手机号：">
                        <el-input v-model="form.phone" placeholder="请输入"></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="8">
                    <el-form-item label="开通时间：">
                        <el-date-picker v-model="form.openTime" type="datetimerange" range-separator="-" start-placeholder="开始日期" end-placeholder="结束日期">
                        </el-date-picker>
                    </el-form-item>
                </el-col>
            </el-row>

            <el-row :gutter="10">
                <el-col :span="8">
                    <el-form-item label="发送时间：">
                        <el-date-picker v-model="form.refundTime" type="datetimerange" range-separator="-" start-placeholder="开始日期" end-placeholder="结束日期">
                        </el-date-picker>
                    </el-form-item>
                </el-col>
                <el-col :span="8">
                    <el-form-item label="用户类型：">
                        <el-select v-model="form.userType" placeholder="全部活动">
                            <el-option label="区域一" value="shanghai"></el-option>
                            <el-option label="区域二" value="beijing"></el-option>
                        </el-select>
                    </el-form-item>
                </el-col>
                <el-col :span="8">
                    <el-form-item label="发送状态：">
                        <el-select v-model="form.sendStatus" placeholder="请选择产品">
                            <el-option label="区域一" value="shanghai"></el-option>
                            <el-option label="区域二" value="beijing"></el-option>
                        </el-select>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row :gutter="10">
                <el-col :span="8">
                    <el-form-item label="短信内容：">
                        <el-input v-model="form.sms" placeholder="请输入"></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="8">
                    <el-form-item label="反馈结果：">
                        <el-input v-model="form.result" placeholder="请输入"></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="8">
                    <el-form-item>
                        <el-button @click="$store.commit('SET_SHOW_SENDMSGOFFLINE',true)">发送线下短信</el-button>
                        <el-button style="float:right;" type="primary">&nbsp;查询&nbsp;</el-button>
                    </el-form-item>
                </el-col>
            </el-row>
        </el-form>
        <div style="background-color:#E6F3FC; height:36px; line-height:36px; margin-top:5px;margin-bottom:5px; font-weight:600;">
            &nbsp;&nbsp;&nbsp;
            <i class="el-icon-info" style="color:#108EE9;font-size:18px; line-height:36px;"></i>
            <span style="margin-left:4%;">未发送：</span>
            <span style="color:#108EE9;">{{1234}}</span>
            <span style="margin-left:5%;">其中过期两天以上：</span>
            <span style="color:#108EE9;">{{12}}</span>
        </div>

        <el-table :data="tableData" v-loading="isLoading" style="width: 100%">
            <el-table-column label="业务号" align="center" show-overflow-tooltip>
                <template slot-scope="scope">
                    <span>{{ scope.row.userId }}</span>
                </template>
            </el-table-column>
            <el-table-column label="类型" align="center" show-overflow-tooltip>
                <template slot-scope="scope">
                    <span>{{ scope.row.type}}</span>
                </template>
            </el-table-column>
            <el-table-column label="用户姓名" align="center" show-overflow-tooltip>
                <template slot-scope="scope">
                    <span>{{ scope.row.userName}}</span>
                </template>
            </el-table-column>
            <el-table-column label="开通时间" align="center" show-overflow-tooltip>
                <template slot-scope="scope">
                    <span>{{ scope.row.openTime}}</span>
                </template>
            </el-table-column>

            <el-table-column label="发送时间" align="center" show-overflow-tooltip>
                <template slot-scope="scope">
                    <span>{{ scope.row.sendTime}}</span>
                </template>
            </el-table-column>
            <el-table-column label="发送状态" align="center" show-overflow-tooltip>
                <template slot-scope="scope">
                    <span>{{ scope.row.sendStatus }}</span>
                </template>
            </el-table-column>
            <el-table-column label="短信内容" align="center" show-overflow-tooltip>
                <template slot-scope="scope">
                    <span>{{ scope.row.smsContent }}</span>
                </template>
            </el-table-column>
            <el-table-column label="反馈结果" align="center" show-overflow-tooltip>
                <template slot-scope="scope">
                    <span>{{ scope.row.result }}</span>
                </template>
            </el-table-column>
        </el-table>
        <div style="float:right;">
            <el-pagination class="layout-pagination" @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="pageInfo.number" :page-sizes="pageInfo.sizeArray" :page-size="pageInfo.currentSize" layout="total, sizes, prev, pager, next, jumper" :total="pageInfo.totalElements">
            </el-pagination>
        </div>
        <send-msg-off-line></send-msg-off-line>
    </div>
</template>

<script>
import SendMsgOffLine from "@/business/smsPreview/view/SendMsgOffLine";
export default {
    components: { SendMsgOffLine },
    props: {},
    data() {
        return {
            form: {},
            tableData: [],
            pageInfo: {
                currentSize: 20,
                sizeArray: [10, 20, 50, 100],
                number: 1,
                totalElements: 0
            },
            isLoading: false
        };
    },

    created() {
        this.getBaseProductListFromWeb();
    },

    methods: {
        /**
         * 获取基础产品列表
         */
        getBaseProductListFromWeb() {
            this.isLoading = true;
            let that = this;
            this.$baseProductRequest.getBaseProductList({
                page: this.pageInfo.number - 1,
                size: this.pageInfo.currentSize,
                baseProduct: this.formInline,
                onResult(isSuccess, data) {
                    if (isSuccess) {
                        that.handleGetBaseProductListSuccessResponse(data);
                    } else {
                    }
                    that.isLoading = false;
                }
            });
        },
        handleGetBaseProductListSuccessResponse(data) {
            this.handlePageInfo(data);
            this.baseProductList = data.body;
        },
        /**
         * 将page 数据赋值
         */
        handlePageInfo(data) {
            this.pageInfo.totalElements = data.page.totalElements;
            console.log(this.pageInfo);
        },
        /**
         * 改变了每页数据
         */
        handleSizeChange(val) {
            console.log(`每页 ${val} 条`);
            this.pageInfo.currentSize = val;
            this.getBaseProductListFromWeb();
        },
        /**
         * 处理当前的改变
         */
        handleCurrentChange(val) {
            console.log(`当前页: ${val}`);
            this.pageInfo.number = val;
            this.getBaseProductListFromWeb();
        },
        /**
         * 重置 pageInfo
         */
        resetPageInfo() {
            this.pageInfo.currentSize = this.pageInfo.sizeArray[0];
            this.pageInfo.number = 1;
        },
        searchDialRecord() {}
    },

    watch: {}
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
</style>



