<template>
    <el-dialog title="" :visible.sync="isCurrentShow" width="61.8%" :before-close="handleClose" :close-on-click-modal="false">
        <div style="float:left;">
            <i class='el-icon-warning' style="color:red;"></i>详情
        </div>
        <div style="clear:both;"></div>
        <el-form ref="refund" size="mini" :model="refund" label-position="right" label-width="30%">
            <el-col :span="12">
                <el-form-item label="订单号：">
                    {{refund.orderNo}}
                </el-form-item>
                <el-form-item label="姓名：">
                    {{refund.userName}}
                </el-form-item>
                <el-form-item label="支付方式：">
                    {{refund.payType | payTypeFilter}}
                </el-form-item>
                <el-form-item label="投诉时间：">
                    {{refund}}
                </el-form-item>
                <el-form-item label="处理进度：">
                    {{refund}}
                </el-form-item>
                <el-form-item label="建议：">

                </el-form-item>
                <el-form-item label="设备号：">
                    {{refund.deviceId}}
                </el-form-item>
                <el-form-item label="手机号码：">
                    {{refund.phone}}
                </el-form-item>
                <el-form-item label="电话：">
                    {{refund.phone}}
                </el-form-item>
            </el-col>
            <el-col :span="12">
                <el-form-item label="业务号：">
                    {{refund.userId}}
                </el-form-item>
                <el-form-item label="产品名称：">
                    {{refund.productName}}
                </el-form-item>
                <el-form-item label="投诉来源：">
                    {{refund.unorderSource | unorderSourceFilter}}
                </el-form-item>
                <el-form-item label="处理时间：">
                    {{refund}}
                </el-form-item>
                <el-form-item label="处理结果：">
                    {{refund.result | resultFilter}}
                </el-form-item>
                <el-form-item label="厂商：">
                    {{refund}}
                </el-form-item>
            </el-col>
        </el-form>
    </el-dialog>
</template>
<script>
import Constant from "@/assets/js/Constant";
export default {
    props: {
        refund: { type: Object }
    },
    data() {
        return {};
    },
    computed: {
        isCurrentShow() {
            return this.$store.state.orderAndRefund.showRefundDetailDlg;
        }
    },
    methods: {
        handleClose() {
            this.$store.commit("SET_SHOW_REFUNDDETAILDLG", false);
        }
    },
    filters: {
        unorderSourceFilter(unorderSource) {
            return Constant.unorderSourceMap[unorderSource];
        },
        resultFilter(result) {
            return Constant.resultMap[result];
        }
    }
};
</script>
<style scoped>
</style>





