import Vue from 'vue'
import axios from 'axios'
import Constant from '@/assets/js/Constant'

function logout() {

}

function exit(code) {

}

/**
 * 统一处理成功回应
 */
function handleSuccessResponse(holder, data) {
  holder.onResult(true, data);
}

/**
 * 统一处理失败回应
 */
function handleFailResponse(holder, xhr, textStatus, errorThrown) {
  var data = {
    xhr: xhr,
    textStatus: textStatus,
    error: errorThrown
  }
  holder.onResult(false, data);
}

export var RequestHelper = {
  /**
   * 查询黑名单
   * @param {*} holder 
   */
  getBlackList(holder) {
    if (Constant.api.blackList.mock) return
    let api = Constant.serverUrl + Constant.api.blackList.getBlackList +
      "?userId=" + holder.userId + "&userName=" + holder.userName;
    let that = this;
    axios.get(api).then(success => {
      handleSuccessResponse(holder, success.data);
    }).catch(error => {
      handleFailResponse(holder, error);
    });
  },
  /**
   * 删除黑名单
   * @param {*} holder 
   */
  deleteBlackList(holder) {
    if (Constant.api.blackList.mock) return
    let api = Constant.serverUrl + Constant.api.blackList.deleteBlackList +
      "/" + holder.userId;
    let that = this;
    axios.post(api).then(success => {
      handleSuccessResponse(holder, success.data);
    }).catch(error => {
      handleFailResponse(holder, error);
    });
  },
  /**
   * 添加黑名单
   * @param {*} holder 
   */
  addBlackList(holder) {
    if (Constant.api.blackList.mock) return
    let api = Constant.serverUrl + Constant.api.blackList.addBlackList;
    let that = this;
    axios.post(api, {
      subscriber: holder.subscriber
    }).then(success => {
      handleSuccessResponse(holder, success.data);
    }).catch(error => {
      handleFailResponse(holder, error);
    });
  },

};



function install(Vue) {
  Vue.prototype.$blackListRequest = RequestHelper;
};

export default install;


