<template>
    <el-menu mode="vertical" :default-active="$route.path" :collapse="isCollapse" background-color="#304156" text-color="#fff" active-text-color="#409EFF">
        <el-collapse-transition>
            <div class="layout-user-head" v-show="!isCollapse">
                <strong>{{user.opName}}</strong>
            </div>
        </el-collapse-transition>
        <sidebar-item :routes="permission_routers"></sidebar-item>
    </el-menu>
</template>

<script>
import SidebarItem from "./SidebarItem";

export default {
    components: { SidebarItem },
    computed: {
        isCollapse() {
            return !this.$store.state.app.sidebar.opened;
        },
        permission_routers() {
            //this.$store.state.permission.routers;
            return this.$store.state.permission.routers;
        },
        user() {
            return this.$store.state.user;
        }
    }
};
</script>

<style lang="scss" scoped>
.layout-user-head {
    color: white;
    padding: 33px 25px;
}
</style>



