<template>
    <el-dialog :visible.sync="isCurrentShow" width="61.8%" :before-close="handleClose" :close-on-click-modal="false">
        <div style="margin-top:-30px;">
            <i class="el-icon-circle-plus" style="color:#108EE9;font-size:36px; display:inline-block; vertical-align:middle;"></i>
            <span style="margin-left:12px;font-size:20px;display:inline-block; vertical-align:middle">新增小组</span>
        </div>
        <br/>

        <el-form style="padding-left:20px;" ref="team" :model="team" label-width="90px" size="mini" label-position="right">
            <el-form-item label="*小组名称：" prop="name">
                <el-input v-model="team.name" placeholder="请输入"></el-input>
            </el-form-item>
            <el-form-item label="*组长：" class="item" prop="leaderName">
                <el-select v-model="leader" value-key="opId" placeholder="请选择">
                    <el-option v-for="leader in leaders" :key="leader.opId" :label="leader.opName" :value="leader">
                    </el-option>
                </el-select>
            </el-form-item>
        </el-form>

        <div style="margin-left:20px;">
            <div style="background-color:#F3F3F3; height:36px;">
                &nbsp;
                <span style="line-height:36px;">未分组（{{checkedUser | totalNum}}/{{users | totalNum}}）</span>
                <el-button @click="checkedAll()" size="mini" style="float:right; margin-top:2px;margin-right:10px;">全部加入</el-button>
            </div>
            <div style="clear:both;"></div>
            <div style="display:inline-block; width:16%; height:26px;padding-left: 10px; background-color:#F4F4F4; margin:8px 10px;" v-for="user in users" :key="user.opId">
                <span style="line-height:26px;">{{user.opName}}</span>
                <el-button @click="selectedUser(user)" type="text" style="float:right; margin:-7px 10px;">
                    <i :id="user.opId" class='el-icon-plus plus' style="font-weight:700;"></i>
                </el-button>
            </div>
        </div>

        <div>
            <span slot="footer" class="dialog-footer" style="float:right;">
                <el-button size="small" type="primary" @click="addTeam">保存</el-button>
                <el-button size="small" @click="handleClose">取消</el-button>
            </span>
        </div>
        <div style="clear:both;"></div>
    </el-dialog>
</template>

<script>
export default {
    components: {},
    props: {
        users: { type: Array },
        leaders: { type: Array }
    },
    data() {
        return {
            team: {
                teamName: "",
                leaderName: "",
                leaderId: ""
            },
            leader: {},
            checkedUser: []
        };
    },
    computed: {
        isCurrentShow() {
            return this.$store.state.teamManage.showAddTeam;
        }
    },
    created() {},
    methods: {
        /**保存小组信息 */
        addTeam() {
            let that = this;
            this.$teamManageRequest.addTeam({
                userTeam: this.team,
                members: this.checkedUser,
                onResult(isSuccess, data) {
                    if (isSuccess) {
                        if (data.status == 200) {
                            that.$message({
                                type: "success",
                                message: "小组新增成功！"
                            });
                            that.handleClose();
                        }
                    } else {
                        that.$message({
                            type: "error",
                            message: "服务器异常！"
                        });
                    }
                }
            });
        },
        /**关闭弹窗 */
        handleClose() {
            this.$refs["team"].resetFields();
            this.$store.commit("SET_SHOW_ADDTEAM", false);
        },
        selectedUser(user) {
            var str = document.getElementById(user.opId).getAttribute("class");
            if (str.split(" ")[1] == "plus") {
                this.checkedUser.push(user);
                document
                    .getElementById(user.opId)
                    .setAttribute("class", "el-icon-minus minus");
            } else {
                for (var i = 0; i < this.checkedUser.length; i++) {
                    if (this.checkedUser[i].opId == user.opId) {
                        this.checkedUser.splice(i, 1);
                        break;
                    }
                }
                document
                    .getElementById(user.opId)
                    .setAttribute("class", "el-icon-plus plus");
            }
        },
        /**全部选中 */
        checkedAll() {
            this.checkedUser = [];
            for (var i = 0; i < this.users.length; i++) {
                document
                    .getElementById(this.users[i].opId)
                    .setAttribute("class", "el-icon-minus minus");
                this.checkedUser.push(this.users[i]);
            }
            console.log(this.checkedUser);
        }
    },
    filters: {
        totalNum(arr) {
            return arr.length;
        }
    },
    watch: {
        leader(newLeader, old) {
            this.team.leaderName = newLeader.opName;
            this.team.leaderId = newLeader.opId;
        }
    }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.el-input {
    width: 40%;
}
.plus {
    color: #3399ff;
}
.minus {
    color: #ff0000;
}
</style>


