<template>
    <el-dialog title="线下发短信" :visible.sync="isCurrentShow" width="61.8%" :before-close="handleClose" :close-on-click-modal="false">

        <el-form ref="form" size="mini" :model="form" label-position="right" label-width="30%">
            <el-form-item label="手机号码：">
                <el-input v-model="form.phone" placeholder="请输入手机号码"></el-input>
            </el-form-item>
            <el-form-item label="短信内容：">
                <el-input style="width:50%;" type="textarea" v-model="form.msgContent" placeholder="请输入内容"></el-input>
            </el-form-item>
        </el-form>
        <el-button size="small" type="primary" style="float:right;margin-right:15%;">确认发送</el-button>
        <div style="clear:both;"></div>
    </el-dialog>
</template>
<script>
export default {
    props: {},
    data() {
        return {
            form: {}
        };
    },
    computed: {
        isCurrentShow() {
            return this.$store.state.recordAndCount.showSendMsgOffLine;
        }
    },
    methods: {
        handleClose() {
            this.$store.commit("SET_SHOW_SENDMSGOFFLINE", false);
        }
    }
};
</script>
<style scoped>
.el-input {
    width: 50%;
}
</style>





