<template>
    <el-dialog title="修改产品" :visible.sync="isCurrentShow" width="61.8%" :before-close="handleClose" :close-on-click-modal="false">
        <el-form ref="modProduct" :model="product" :rules="rules1" label-width="180px" size="mini" label-position="right" class="demo-dynamic">
            <el-row type="flex" class="row-bg">
                <el-col :span="12">
                    <el-form-item label="产品ID：" class="item" prop="productCode">
                        <el-input v-model="product.productCode" disabled placeholder="请输入"></el-input>
                    </el-form-item>
                    <el-form-item label="产品名称：" prop="name">
                        <el-input v-model="product.name" placeholder="请输入"></el-input>
                    </el-form-item>
                    <el-form-item label="积分筛选上限（分）：" class="item" prop="maxPoint">
                        <el-input v-model="product.maxPoint" placeholder="请输入"></el-input>
                    </el-form-item>
                    <el-form-item label="出账/补扣金额（分）：" prop="payPrice" class="item">
                        <el-input v-model="product.payPrice" placeholder="请输入"></el-input>
                    </el-form-item>
                    <!-- 产品总价值（分） -->
                    <!-- <el-form-item label="产品总价值（分）：">
                        <el-input v-model="product.origPrice" placeholder="请输入"></el-input>
                    </el-form-item> -->
                    <el-form-item label="订购月数(订购参数)：" prop="orderCycleCount">
                        <el-input v-model="product.orderCycleCount" placeholder="请输入"></el-input>
                    </el-form-item>
                    <el-form-item label="积分礼品编号：" prop="giftId">
                        <el-input v-model="product.giftId" placeholder="H11341"></el-input>
                    </el-form-item>
                </el-col>

                <el-col :span="12">
                    <el-form-item label="显示名称：" class="item" prop="title">
                        <el-input v-model="product.title" placeholder="请输入"></el-input>
                    </el-form-item>
                    <el-form-item label="支付方式：" prop="payType">
                        <el-select v-model="product.payType" placeholder="请选择支付方式">
                            <el-option v-for="payType in payTypeList" :label="payType.text" :value="payType.value" :key="payType.value"></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="产品类型：" prop="type">
                        <el-select v-model="product.type" placeholder="请选择产品类型">
                            <el-option v-for="type in productTypeList" :label="type.text" :value="type.value" :key="type.value"></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="产品最低积分（分）：" prop="minPoint">
                        <el-input v-model="product.minPoint" placeholder="请输入"></el-input>
                    </el-form-item>
                    <el-form-item label="单月积分：" class="item" prop="payPoint">
                        <el-input v-model="product.payPoint" placeholder="请输入"></el-input>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-form-item label="互斥产品：">
                <el-input v-model="product.exclusionPID" placeholder="请输入"></el-input>
            </el-form-item>
            <el-form-item label="可伸缩输入框：">
                <el-input type="textarea" :rows="2" placeholder="请输入备注：" v-model="product.desc">
                </el-input>
            </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
            <span id="tishi">*友情提示：填写所需参考的产品信息可在《基础产品包》菜单内查看【价格、名称、互斥ID、产品属性等】
            </span>
            <el-button type="primary" size="mini" @click="doModifyProduct">保存</el-button>
        </span>
    </el-dialog>
</template>
<script>
import Constant from "@/assets/js/Constant";
export default {
    name: "ModifyProduct",
    props: {
        product: {}
    },
    data() {
        var checkProdectCode = (rule, value, callback) => {
            if (!value) {
                return callback(new Error("请输入产品ID"));
            }
            setTimeout(() => {
                if (isNaN(value) || value.substr(0, 1) != 1) {
                    callback(new Error("请输入正确的产品ID"));
                } else if (value.length > 20) {
                    callback(new Error("产品ID不能超过20个字符"));
                } else {
                    this.$productRequest.getProductByCode({
                        productCode: this.product.productCode,
                        onResult(isSuccess, data) {
                            if (isSuccess && data.code == 200) {
                            } else {
                                callback(new Error("请输入正确的产品ID"));
                            }
                        }
                    });
                    callback();
                }
            }, 1000);
        };
        return {
            payTypeList: [],
            productTypeList: [],
            rules1: {
                productCode: [
                    {
                        required: true,
                        validator: checkProdectCode,
                        trigger: "blur"
                    }
                ],
                name: [
                    {
                        required: true,
                        message: "请输入正确的产品名称",
                        max: 20,
                        trigger: "blur"
                    }
                ],
                title: [
                    {
                        required: true,
                        message: "请输入合适的产品显示名称",
                        max: 20,
                        trigger: "blur"
                    }
                ],

                payType: [
                    {
                        required: true,
                        message: "请选择支付方式",
                        trigger: "change",
                        pattern: /^[1-5]$/
                    }
                ],
                minPoint: [
                    {
                        required: false,
                        message: "请输入最低积分",
                        trigger: "blur"
                    },
                    { pattern: /^[0-9]{0,}$/, message: "请输入有效数字" }
                ],
                maxPoint: [
                    {
                        required: true,
                        message: "请输入积分上限",
                        trigger: "blur"
                    },
                    { pattern: /^[0-9]{0,}$/, message: "请输入有效数字" }
                ],
                payPoint: [
                    {
                        required: false,
                        trigger: "blur",
                        pattern: /^[0-9]{0,}$/,
                        message: "请输入有效数字"
                    }
                ],
                payPrice: [
                    {
                        required: false,
                        trigger: "blur",
                        pattern: /^[0-9]{0,}$/,
                        message: "请输入有效数字"
                    }
                ],
                orderCycleCount: [
                    {
                        required: false,
                        trigger: "blur",
                        pattern: /^[0-9]{0,}$/,
                        message: "请输入有效数字"
                    }
                ]
            }
        };
    },
    computed: {
        isCurrentShow() {
            return this.$store.state.product.showModifyProduct;
        }
    },
    created() {
        this.payTypeList = Constant.payTypeList;
        this.productTypeList = Constant.productTypeList;
    },
    methods: {
        handleClose() {
            this.$refs["modProduct"].resetFields();
            this.$store.commit("SET_SHOW_MODIFYPRODUCT", false);
        },
        doModifyProduct() {
            var that = this;
            this.$refs["modProduct"].validate(valid => {
                if (valid) {
                    this.$emit("updateProduct", this.product);
                } else {
                    that.$message({
                        type: "error",
                        message: "请确保产品信息正确"
                    });
                    return false;
                }
            });
        }
    }
};
</script>
<style>
#tishi {
    float: left;
    color: rgb(50, 142, 234);
}
</style>




