<template>
    <div class="gray-bg app-wrapper" :class="{hideSidebar:!sidebar.opened}">
        <!-- <router-view/> -->
        <el-container>
            <el-aside class="aside" :width="sidebar.opened?'180px':'36px'">
                <side-bar class="sidebar-container" />
            </el-aside>
            <el-container>
                <el-header class="layout-header" height="85px">
                    <nav-bar />
                    <tags-view />
                </el-header>
                <el-main>
                    <!--style="background-color:white;" -->
                    <div class="main-container gray-bg">
                        <transition name="fade" mode="out-in">
                            <router-view />
                        </transition>
                    </div>
                </el-main>
            </el-container>
        </el-container>
    </div>
</template>

<script>
import SideBar from "@/components/sidebar/SideBar";
import NavBar from "@/components/nav/NavBar";
import TagsView from "@/components/tags/TagsView";
import Constant from "@/assets/js/Constant";

export default {
    components: {
        SideBar,
        NavBar,
        TagsView
    },

    data() {
        return {};
    },

    created() {
        // this.$store.commit("SET_IMAGE_SERVER_URL", Constant.imageServerUrl);
    },

    computed: {
        sidebar() {
            // 获取边框
            return this.$store.state.app.sidebar;
        }
    }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
.app-wrapper {
    position: relative;
    height: 100%;
    width: 100%;
}

.aside {
    transition: 0.5s;
}

.fade-enter-active,
.fade-leave-active {
    transition: opacity 0.5s;
}

.fade-enter,
.fade-leave-to {
    opacity: 0;
}

.back-to-ceiling .Icon {
    fill: #9aaabf;
    background: none;
}
</style>



