<template>
    <div class="tree">
        <h3>
            <span style="color:#7EC856;font-weight:700;margin-right:6px;">●</span>在线查询
        </h3>

        <el-form :inline="true" class="demo-form-inline" size="mini">
            <el-row :gutter="2">
                <el-col :span="18">
                    <el-form-item style="margin-top:3;">
                        <el-input v-model="productId" placeholder="请输入产品ID"></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="6">
                    <el-form-item>
                        <el-button size="mini" type="primary" @click="searchProductOnline">搜索</el-button>
                    </el-form-item>
                </el-col>
            </el-row>
        </el-form>
        <table style="border:solid 1px rgb(233,233,233);">
            <tr>
                <td class="colum">产品ID</td>
                <td>{{baseProduct.code}}</td>
            </tr>
            <tr>
                <td class="colum">产品名称</td>
                <td>{{baseProduct.name}}</td>
            </tr>
            <tr>
                <td class="colum">价格(积分)</td>
                <td>{{baseProduct.origPrice}}</td>
            </tr>
            <tr>
                <td class="colum">产品属性</td>
                <td>{{baseProduct.type | productTypeFilter}}</td>
            </tr>
            <tr>
                <td class="colum">互斥产品</td>
                <td>
                    <el-button type="text" @click="viewMutexProduct">共{{baseProduct.exclusionPID | countFilter}}个互斥</el-button>
                </td>
            </tr>
            <tr>
                <td class="colum">产品介绍</td>
                <td>{{baseProduct.desc}}</td>
            </tr>
            <tr>
                <td class="colum">操作</td>
                <td>
                    <el-button type="text" @click="addBaseProduct">添加到产品</el-button>
                </td>
            </tr>
        </table>
        <add-base-product :baseProduct="baseProduct" />
    </div>
</template>

<script>
import Constant from "@/assets/js/Constant";
import AddBaseProduct from "./AddBaseProduct";
export default {
    components: {
        AddBaseProduct
    },
    props: {},
    data() {
        return {
            baseProduct: {},
            productId: ""
        };
    },
    computed: {},
    created() {},
    methods: {
        /**在线查询基础产品信息 */
        searchProductOnline() {
            if (!this.productId) {
                return;
            }
            let that = this;
            this.$baseProductRequest.searchBaseProduct({
                productId: this.productId,
                onResult(isSuccess, data) {
                    if (isSuccess && data.status == 200) {
                        that.baseProduct = data.result;
                    } else {
                    }
                }
            });
        },
        /**查看互斥产品ID */
        viewMutexProduct() {
            if (this.baseProduct.exclusionPID) {
                this.$alert(
                    "<div style='text-align:center;'>" +
                        this.baseProduct.exclusionPID +
                        "</div>",
                    {
                        dangerouslyUseHTMLString: true
                    }
                );
            } else {
                this.$alert(
                    "<div style='text-align:center;'>没有互斥产品</div>",
                    {
                        dangerouslyUseHTMLString: true
                    }
                );
            }
        },
        /**点击添加到基础产品 */
        addBaseProduct() {
            let that = this;
            if (!this.baseProduct.code) {
                that.$message({
                    type: "warning",
                    message: "请先选择产品"
                });
                return;
            }
            this.$baseProductRequest.getBaseProductByCode({
                productCode: that.baseProduct.code,
                onResult(isSuccess, data) {
                    if (isSuccess) {
                        if (data.status == 200 && !data.result) {
                            that.$store.commit("SET_SHOW_ADDBASEPRODUCT", true);
                        } else {
                            that.$message({
                                type: "warning",
                                message: "该基础产品在已经添加过"
                            });
                        }
                    } else {
                        that.$message({
                            type: "error",
                            message: "网络异常"
                        });
                    }
                }
            });
        }
    },
    filters: {
        countFilter(exclusionPID) {
            if (exclusionPID) {
                return exclusionPID.split(",").length;
            } else return 0;
        },
        productTypeFilter(type) {
            if (type) {
                return Constant.productTypeMap[type];
            }
        }
    },
    watch: {}
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
td {
    text-align: center;
    border: 1px solid #cccccc;
    height: auto;
}
.colum {
    padding: 6px;
    font: 1.4em sans-serif;
    font-weight: 500;
    color: black;
    width: 40%;
}
.tree {
    width: 80%;
    margin: auto;
}
</style>



