const teamManage = {
    state: {
        showAddTeam: false,
        showSendTeam: false
    },
    mutations: {
        SET_SHOW_ADDTEAM(state, data) {
            state.showAddTeam = data;
        },
        SET_SHOW_SENDTEAM(state, data) {
            state.showSendTeam = data;
        }
    },
    actions: {}
};
export default teamManage;


