<template>
    <div class="main-container gray-bg">
        <el-form ref="form" size="mini" :model="form" label-position="right" label-width="110px">
            <el-row :gutter="14">
                <el-col :span="8">
                    <el-form-item label="业务号：">
                        <el-input v-model="form.userId" placeholder="请输入"></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="8">
                    <el-form-item label="电话/手机号：">
                        <el-input v-model="form.userPhone" placeholder="请输入"></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="8">
                    <el-form-item label="用户姓名：">
                        <el-input v-model="form.userName" placeholder="请输入"></el-input>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row :gutter="14">
                <el-col :span="8">
                    <el-form-item label="拨打时间：">
                        <el-date-picker v-model="form.callTime" type="datetimerange" range-separator="-" start-placeholder="开始日期" end-placeholder="结束日期">
                        </el-date-picker>
                    </el-form-item>
                </el-col>
                <el-col :span="8">
                    <el-form-item label="营销结果：">
                        <el-select v-model="form.result" placeholder="请选择">
                            <el-option v-for="result in resultFilters" :label="result.text" :value="result.value" :key="result.value"></el-option>
                        </el-select>
                    </el-form-item>
                </el-col>
                <el-col :span="8">
                    <el-form-item label="客服姓名：">
                        <el-select v-model="form.opName" placeholder="请选择">
                            <el-option v-for="result in resultFilters" :label="result.text" :value="result.value" :key="result.value"></el-option>
                        </el-select>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row :gutter="14">
                <el-col :span="8">
                    <el-form-item></el-form-item>
                </el-col>
                <el-col :span="8">
                    <el-form-item></el-form-item>
                </el-col>
                <el-col :span="8">
                    <el-form-item>
                        <el-button @click="searchCallRecord" style="float:right; " type="primary ">查询</el-button>
                    </el-form-item>
                </el-col>
            </el-row>
        </el-form>

        <el-table :data="callRecordList" v-loading="isLoading" style="width: 100% ">
            <el-table-column prop="userId" label="业务号" align="center" show-overflow-tooltip>
            </el-table-column>
            <el-table-column prop="userName" label="用户姓名" align="center " show-overflow-tooltip>
            </el-table-column>
            <!--营销结果筛选-->
            <el-table-column prop="result" label="营销结果 " width="90 " align="center " show-overflow-tooltip>
                <template slot-scope="scope">{{scope.row.result | resultFilter}}</template>
            </el-table-column>
            <el-table-column prop="userPhone" label="电话 " align="center " show-overflow-tooltip>
            </el-table-column>
            <el-table-column prop="callTime" label="拨打日期 " align="center " show-overflow-tooltip>
            </el-table-column>
            <!--客服姓名筛选-->
            <el-table-column prop="opName" label="客服姓名" align="center" show-overflow-tooltip>
            </el-table-column>
            <el-table-column prop="productId" label="开通产品" align="center" show-overflow-tooltip>
            </el-table-column>
            <el-table-column prop="payPoint" label="扣分" align="center" show-overflow-tooltip>
            </el-table-column>
            <el-table-column prop="payPrice" label="出账" align="center" show-overflow-tooltip>
            </el-table-column>
            <el-table-column prop="orderCycleCount" label="月数" align="center " show-overflow-tooltip>
            </el-table-column>
            <el-table-column prop="tapes" label="录音" align="center" show-overflow-tooltip>
            </el-table-column>
            <el-table-column prop="remark" label="拨打备注" align="center" show-overflow-tooltip>
            </el-table-column>
            <el-table-column prop="failedReason" label="失败原因" align="center " show-overflow-tooltip>
            </el-table-column>
            <el-table-column label="操作" align="center ">
                <template slot-scope="scope ">
                    <el-button type="text" @click="callPage(scope.row) ">呼叫</el-button>
                </template>
            </el-table-column>
        </el-table>
        <div style="float:right; ">
            <el-pagination class="layout-pagination " @size-change="handleSizeChange " @current-change="handleCurrentChange " :current-page="pageInfo.number " :page-sizes="pageInfo.sizeArray " :page-size="pageInfo.currentSize " layout="total, sizes, prev, pager, next, jumper " :total="pageInfo.totalElements ">
            </el-pagination>
        </div>
        <call-will-page :callRecord="currentCallRecord" />
    </div>
</template>

<script>
import Constant from "@/assets/js/Constant";
import CallWillPage from "@/business/customerServiceMission/view/CallWillPage";
export default {
    components: {
        CallWillPage
    },
    props: {},
    data() {
        return {
            resultFilters: [],
            opNameFilters: [],
            form: {},
            activityId: "",
            callRecordList: [
                {
                    activityId: 0,
                    activityName: "string",
                    callTime: "2018-08-28T06:15:31.564Z",
                    failedReason: "string",
                    id: 0,
                    opId: 0,
                    opName: "string",
                    orderCycleCount: 0,
                    payPoint: 0,
                    payPrice: 0,
                    payType: 0,
                    productId: 0,
                    productName: "string",
                    remark: "string",
                    result: 0,
                    userId: "string",
                    userName: "string",
                    userPhone: "string"
                }
            ],
            pageInfo: {
                currentSize: 10,
                sizeArray: [10, 20, 50, 100],
                number: 1,
                totalElements: 0
            },
            isLoading: false,
            currentCallRecord: {}
        };
    },
    computed: {},
    created() {
        this.activityId = this.$route.query.activityId;
        this.resultFilters = Constant.resultList;
        this.getCallRecordListFromWeb();
    },
    methods: {
        /**营销结果筛选 */
        resultFilter(value, row) {
            return row.result == value;
        },
        /**客服姓名筛选 */
        opNameFilter(value, row, column) {
            const property = column["property"];
            return row[property] === value;
        },
        /**查询某营销活动的拨打记录 */
        getCallRecordListFromWeb() {
            this.isLoading = true;
            let that = this;
            this.$callRecordRequest.callRecordList({
                activityId: this.activityId,
                userId: this.form.userId,
                userPhone: this.form.userPhone,
                userName: this.form.userName,
                failedReason: this.form.failedReason,
                // 意向
                openTime: this.form.openTime,
                page: this.pageInfo.number - 1,
                size: this.pageInfo.currentSize,
                onResult(isSuccess, data) {
                    if (isSuccess) {
                        that.handlePageInfo(data);
                        that.callRecordList = data.result;
                    } else {
                    }
                    that.isLoading = false;
                }
            });
        },
        /**
         * 将page 数据赋值
         */
        handlePageInfo(data) {
            this.pageInfo.totalElements = data.page.totalElements;
            console.log(this.pageInfo);
        },
        /**
         * 改变了每页数据
         */
        handleSizeChange(val) {
            this.pageInfo.currentSize = val;
            this.getCallRecordListFromWeb();
        },
        /**
         * 处理当前的改变
         */
        handleCurrentChange(val) {
            this.pageInfo.number = val;
            this.getCallRecordListFromWeb();
        },
        /**
         * 重置 pageInfo
         */
        resetPageInfo() {
            this.pageInfo.currentSize = this.pageInfo.sizeArray[0];
            this.pageInfo.number = 1;
        },
        /**表单查询拨打记录列表 */
        searchCallRecord() {
            this.resetPageInfo();
            this.getCallRecordListFromWeb();
        },
        /**点击呼叫按钮 */
        callPage(callRecord) {
            this.currentCallRecord = callRecord;
            this.$store.commit("SET_SHOW_CALLPAGE", true);
        }
    },
    watch: {},
    filters: {
        resultFilter(result) {
            return Constant.resultMap[result];
        }
    }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
</style>



