import Vue from "vue";
import Vuex from "vuex";
import permission from "./modules/permission";
import tagsView from "./modules/tagsView";
import user from "./modules/user";
import app from "./modules/app";
import product from "./modules/product";
import baseProduct from "./modules/baseproduct";
import teamManage from "./modules/teamManage";
import activity from "./modules/activity";
import order from "./modules/order";
import orderAndRefund from "./modules/orderAndRefund";
import recordAndCount from "./modules/recordAndCount";
import customerMission from "./modules/customerMission";
import reviewAndOrder from "./modules/reviewAndOrder";
Vue.use(Vuex);

const store = new Vuex.Store({
    modules: {
        permission,
        tagsView,
        user,
        app,
        product,
        baseProduct,
        teamManage,
        activity,
        order,
        orderAndRefund,
        recordAndCount,
        customerMission,
        reviewAndOrder
    }
});

export default store;


