<template>
    <el-dialog title="新增任务" :visible.sync="isCurrentShow" width="61.8%" :before-close="handleClose" :close-on-click-modal="false">
        <el-form ref="activity" :model="activity" :rules="rules" label-width="30%" size="mini" label-position="right">
            <el-form-item label="任务名称：" prop="name">
                <el-input placeholder="请输入：" v-model="activity.name">
                </el-input>
            </el-form-item>
            <el-form-item label="活动类型：" prop="type">
                <el-radio-group v-model="activity.type">
                    <el-radio v-for="activityType in activityTypeList" :key="activityType.value" :label="activityType.value">{{activityType.text}}</el-radio>
                </el-radio-group>
            </el-form-item>
            <el-form-item label="支付方式：" prop="payType">
                <el-select v-model="activity.payType" placeholder="请选择正确的支付方式" @change="getProductListByPayType">
                    <el-option v-for="payType in payTypeList" :label="payType.text" :value="payType.value" :key="payType.value"></el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="绑定产品关系：">
                <el-checkbox-group v-model="products">
                    <el-checkbox-button v-for="product in productList" :key="product.id" :label="product.id">{{product.title}}<br/>积分价格：{{product.payPoint+product.payPrice}}</el-checkbox-button>
                </el-checkbox-group>
            </el-form-item>
            <el-form-item label="是否自动开通：" prop="autoOpen">
                <el-switch v-model="activity.autoOpen" active-value="1" inactive-value="0">
                </el-switch>
            </el-form-item>
            <el-form-item label="IVR编号：" prop="ivr">
                <el-input v-model="activity.ivr" placeholder="请输入"></el-input>
            </el-form-item>
            <el-form-item label="SP类型：" prop="spCode">
                <el-select v-model="activity.spCode" placeholder="请选择">
                    <el-option v-for="payType in payTypeList " :label="payType.text " :value="payType.value " :key="payType.value "></el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="最高积分： " prop="maxPoint">
                <el-input placeholder="请输入： " v-model="activity.maxPoint ">
                </el-input>
            </el-form-item>
            <el-form-item label="最低积分： " prop="minPoint">
                <el-input placeholder="请输入： " v-model="activity.minPoint ">
                </el-input>
            </el-form-item>
            <el-form-item label="普通活动短信模板(开通成功短信)：" prop="smsTemplate">
                <el-input type="textarea" :line=2 placeholder="请输入备注：" v-model="activity.smsTemplate ">
                </el-input>
            </el-form-item>
            <el-form-item label="外呼脚本：" prop="script">
                <el-input type="textarea" :line=2 placeholder="请输入备注：" v-model="activity.script ">
                </el-input>
            </el-form-item>
        </el-form>
        <div style="float:right; ">
            <el-button type="primary" size="mini" @click="addActivity">保存</el-button>
            <el-button size="mini" @click="handleClose">返回</el-button>
        </div>
        <div style="clear:both;"></div>
    </el-dialog>
</template>
<script>
import Constant from "@/assets/js/Constant";
export default {
    name: "AddProduct",
    data() {
        return {
            payTypeList: [],
            activityTypeList: [],
            activity: {},
            productList: [],
            products: [],
            rules: {
                name: [
                    { required: true, trigger: "blur" },
                    {
                        validator: function(rule, value, callback) {
                            if (!value) {
                                return callback(new Error("请输入产品名称"));
                            }
                            setTimeout(() => {
                                if (value.length > 10) {
                                    callback(new Error("名称最多10个字"));
                                } else {
                                    callback();
                                }
                            }, 1000);
                        },
                        trigger: "blur"
                    }
                ],
                type: [
                    {
                        required: true,
                        trigger: "change"
                    }
                ],
                payType: [
                    {
                        required: true,
                        message: "请选择",
                        trigger: "change"
                    }
                ],
                productList: [
                    {
                        required: true
                    }
                ],
                autoOpen: [{ required: true, trigger: "change" }],
                ivr: [{ required: true, trigger: "blur" }],
                spCode: [{ required: false, trigger: "blur" }],
                // 厂商
                maxPoint: [{ required: true, trigger: "blur" }],
                minPoint: [{ required: true, trigger: "blur" }],
                smsTemplate: [{ required: false, trigger: "blur" }],
                script: [{ required: true, trigger: "blur" }]
            }
        };
    },
    computed: {
        isCurrentShow() {
            return this.$store.state.activity.showAddActivity;
        }
    },
    created() {
        this.payTypeList = Constant.payTypeList;
        this.activityTypeList = Constant.activityTypeList;
    },
    methods: {
        handleClose() {
            this.$refs["activity"].resetFields();
            this.$store.commit("SET_SHOW_ADDACTIVITY", false);
        },
        /**获取可以绑定的产品 */
        getProductListByPayType() {
            this.products = [];
            let that = this;
            this.$productRequest.getProductListByPayType({
                payType: this.activity.payType,
                onResult(isSuccess, data) {
                    if (isSuccess) {
                        if (data.status == 200 && data.result) {
                            that.productList = data.result;
                        } else if (data.result == 200 && !data.result) {
                            that.$message({
                                type: "warning",
                                message: "没有对应产品"
                            });
                        } else {
                            that.$message({
                                type: "warning",
                                message: "服务器异常"
                            });
                        }
                    } else {
                        that.$message({
                            type: "error",
                            message: "当前网络异常"
                        });
                    }
                }
            });
        },
        /**新增任务 */
        addActivity() {
            var that = this;
            this.$refs["activity"].validate(valid => {
                if (valid) {
                    this.$activityRequest.addActivity({
                        activity: this.activity,
                        products: this.products,
                        onResult(isSuccess, data) {
                            if (isSuccess) {
                                if (data.status == 200) {
                                    that.handleClose();
                                    that.$emit("refreshActivities");
                                    that.$message({
                                        type: "success",
                                        message: "任务添加成功！"
                                    });
                                } else if (
                                    data.status == 500 &&
                                    data.code != 1000
                                ) {
                                    that.$message({
                                        type: "error",
                                        message: data.msg
                                    });
                                }
                            } else {
                                that.$message({
                                    type: "error",
                                    message: "当前网络异常"
                                });
                            }
                        }
                    });
                } else {
                    that.$message({
                        type: "error",
                        message: "请确保活动信息正确"
                    });
                    return false;
                }
            });
        }
    }
};
</script>
<style scoped>
</style>




