<template>
  <div class="main-container gray-bg">
    <el-form ref="form" size="mini" :model="form" label-position="right" label-width="100px">
      <el-row :gutter="10">
        <el-col :span="8">
          <el-form-item label="活动名称：">
            <el-select v-model="form.activityName" placeholder="请选择活动名称">
              <el-option v-for="activity in activityList" :label="activity.name" :value="activity.name" :key="activity.id"></el-option>
            </el-select>
          </el-form-item>
        </el-col>
        <el-col :span="8">
          <el-form-item label="产品名称：">
            <el-select v-model="form.productName" placeholder="请选择产品">
              <el-option v-for="product in productList" :label="product.title" :value="product.title" :key="product.productCode"></el-option>
            </el-select>
          </el-form-item>
        </el-col>
        <el-col :span="8">
          <el-form-item label="状态：">
            <el-select v-model="form.status" placeholder="请选择">
              <el-option v-for="status in orderStatusFilters" :label="status.text" :value="status.value" :key="status.value"></el-option>
            </el-select>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row :gutter="10">
        <el-col :span="8">
          <el-form-item label="订单号：">
            <el-input v-model="form.orderNo" placeholder="请输入"></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="8">
          <el-form-item label="业务号：">
            <el-input v-model="form.userId" placeholder="请输入"></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="8">
          <el-form-item label="用户姓名：">
            <el-input v-model="form.userName" placeholder="请输入"></el-input>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row :gutter="10">
        <el-col :span="8">
          <el-form-item label="手机号：">
            <el-input v-model="form.phone" placeholder="请输入"></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="8">
          <el-form-item label="设备号：">
            <el-input v-model="form.deviceId" placeholder="请输入"></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="8">
          <el-form-item label="支付方式：">
            <el-select v-model="form.payType" placeholder="请选择">
              <el-option label="全部" value="" aria-checked></el-option>
              <el-option v-for="payType in payTypeFilters" :label="payType.text" :value="payType.value" :key="payType.value"></el-option>
            </el-select>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row :gutter="10">
        <el-col :span="8">
          <el-form-item label="开通时间：">
            <el-date-picker v-model="form.openTime" type="datetimerange" range-separator="-" start-placeholder="开始日期" end-placeholder="结束日期">
            </el-date-picker>
          </el-form-item>
        </el-col>
        <el-col :span="8">
          <el-form-item label="退单时间：">
            <el-date-picker v-model="form.unorderTime" type="datetimerange" range-separator="-" start-placeholder="开始日期" end-placeholder="结束日期">
            </el-date-picker>
          </el-form-item>
        </el-col>
        <el-col :span="8">
          <!-- <el-button>导出订单</el-button> -->
          <el-button type="primary" size="mini" style="float:right;" @click="searchOrderRecordList">查询</el-button>
        </el-col>
      </el-row>
    </el-form>
    <el-table :data="orderRecordList" v-loading="isLoading" style="width: 100%">
      <el-table-column prop="orderNo" label="订单号" align="center" show-overflow-tooltip>
      </el-table-column>
      <el-table-column prop="userId" label="业务号" align="center" show-overflow-tooltip>
      </el-table-column>
      <el-table-column prop="userName" label="姓名" align="center" show-overflow-tooltip>
      </el-table-column>
      <el-table-column prop="productName" label="产品名称" align="center" show-overflow-tooltip>
      </el-table-column>
      <el-table-column label="产品价格" align="center" show-overflow-tooltip>
        <template slot-scope="scope">
          <span>{{scope.row.payPrice + scope.row.payPoint}}</span>
        </template>
      </el-table-column>
      <!--支付方式筛选-->
      <el-table-column prop="payType" label="支付方式" width="90" align="center" show-overflow-tooltip>
        <template slot-scope="scope">
          <span>{{scope.row.payType | payTypeFilter}}</span>
        </template>
      </el-table-column>
      <el-table-column prop="openTime" label="开通时间" align="center" show-overflow-tooltip>
      </el-table-column>
      <el-table-column prop="unorderTime" label="退单时间" align="center" show-overflow-tooltip>
      </el-table-column>
      <!--状态筛选-->
      <el-table-column prop="status" label="状态" align="center" show-overflow-tooltip>
        <template slot-scope="scope">
          {{scope.row.status | orderStatusFilter}}
        </template>
      </el-table-column>
      <el-table-column label="操作" width="200" align="center">
        <template slot-scope="scope">
          <el-button @click="viewDetail(scope.row)" style="display:inline-block;vertical-align:middle;" type="text">详情</el-button>
          <el-button v-if="scope.row.status!=2&&scope.row.status!=3" style="display:inline-block;vertical-align:middle;" type="text">开通<br/>设置</el-button>
          <el-button v-if="scope.row.status==1" style="display:inline-block;vertical-align:middle;color:red;" type="text">投诉<br/>退单</el-button>
        </template>
      </el-table-column>
    </el-table>
    <div style="float:right;">
      <el-pagination class="layout-pagination" @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="pageInfo.number" :page-sizes="pageInfo.sizeArray" :page-size="pageInfo.currentSize" layout="total, sizes, prev, pager, next, jumper" :total="pageInfo.totalElements">
      </el-pagination>
    </div>
    <div style="clear:both;"></div>
    <order-detai :order="order" />
  </div>
</template>

<script>
import Constant from "@/assets/js/Constant";
import OrderDetai from "./Detail";
export default {
    components: { OrderDetai },
    props: {},
    data() {
        return {
            activityTypeList: [], //活动类型
            activityList: [], // 活动列表
            productList: [], // 产品列表
            order: {},
            payTypeFilters: [],
            unorderSourceFilters: [], //投诉来源
            orderStatusFilters: [],
            form: {},
            orderRecordList: [
                {
                    activityId: 0,
                    activityName: "string",
                    address: "string",
                    autoOpen: 0,
                    createTime: "2018-08-23T06:21:44.726Z",
                    crmId: "string",
                    deviceId: "string",
                    id: 1,
                    opId: 0,
                    opName: "string",
                    orderCycleCount: 0,
                    orderNo: "string",
                    payPoint: 0,
                    payPrice: 0,
                    payType: 3,
                    phone: "string",
                    point: 0,
                    productId: 0,
                    productName: "string",
                    spCode: "string",
                    status: 0,
                    unorderFlag: 0,
                    unorderSource: "string",
                    unorderTime: "2018-08-23T06:21:44.726Z",
                    userId: "string",
                    userName: "string",
                    userType: 0
                }
            ],
            pageInfo: {
                currentSize: 10,
                sizeArray: [10, 20, 50, 100],
                number: 1,
                totalElements: 0
            },
            isLoading: false
        };
    },
    computed: {},
    created() {
        this.activityTypeList = Constant.activityTypeList;
        this.payTypeFilters = Constant.payTypeList;
        this.orderStatusFilters = Constant.orderStatusList;
        this.unorderSourceFilters = Constant.unorderSourceList;
        this.getOrderRecordListFromWeb(); // 获取订单列表
    },
    methods: {
        /**查询订单列表 */
        getOrderRecordListFromWeb() {
            let that = this;
            this.isLoading = false;
            if (this.form.unorderTime) {
                this.form.unorderTimeStart = this.timeFormat(
                    this.form.unorderTime[0]
                );
                this.form.unorderTimeEnd = this.timeFormat(
                    this.form.unorderTime[1]
                );
            }
            if (this.form.openTime) {
                this.form.openTimeStart = this.timeFormat(
                    this.form.unorderTime[0]
                );
                this.form.openTimeEnd = this.timeFormat(
                    this.form.unorderTime[1]
                );
            }
            this.$orderRequest.orderList({
                orderVo: this.form,
                page: this.pageInfo.number - 1,
                size: this.pageInfo.currentSize,
                onResult(isSuccess, data) {
                    if (isSuccess) {
                        that.orderRecordList = data.result;
                        that.handlePageInfo(data);
                    } else {
                    }
                    that.isLoading = false;
                }
            });
        },
        /**
         * 将page 数据赋值
         */
        handlePageInfo(data) {
            this.pageInfo.totalElements = data.page.totalElements;
            console.log(this.pageInfo);
        },
        /**
         * 改变了每页数据
         */
        handleSizeChange(val) {
            console.log(`每页 ${val} 条`);
            this.pageInfo.currentSize = val;
            this.getOrderRecordListFromWeb();
        },
        /**
         * 处理当前的改变
         */
        handleCurrentChange(val) {
            console.log(`当前页: ${val}`);
            this.pageInfo.number = val;
            this.getOrderRecordListFromWeb();
        },
        /**
         * 重置 pageInfo
         */
        resetPageInfo() {
            this.pageInfo.currentSize = this.pageInfo.sizeArray[0];
            this.pageInfo.number = 1;
        },
        /**表单查询订单列表 */
        searchOrderRecordList() {
            this.resetPageInfo();
            this.getOrderRecordListFromWeb();
        },
        /**查看订单详情 */
        viewDetail(order) {
            this.order = order;
            this.$store.commit("SET_SHOW_DETAILDLG", true);
        },
        formatDate(val) {
            if (val) {
                const dateMat = new Date(val);
                const year = dateMat.getFullYear();
                const month = dateMat.getMonth() + 1;
                const day = dateMat.getDate();
                const hh = dateMat.getHours();
                const mm = dateMat.getMinutes();
                const ss = dateMat.getSeconds();
                const timeFormat =
                    year +
                    "-" +
                    month +
                    "-" +
                    day +
                    " " +
                    hh +
                    ":" +
                    mm +
                    ":" +
                    ss;
                return timeFormat;
            }
        }
    },
    filters: {
        payTypeFilter(payType) {
            return Constant.payTypeMap[payType];
        },
        orderStatusFilter(status) {
            return Constant.orderStatusMap[status];
        }
    },

    watch: {}
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
</style>



