import Vue from 'vue'
import axios from 'axios'

import Constant from '@/assets/js/Constant'

function logout() {

}

function exit(code) {

}

/**
 * 统一处理成功回应
 */
function handleSuccessResponse(holder, data) {
  holder.onResult(true, data);
}

/**
 * 统一处理失败回应
 */
function handleFailResponse(holder, xhr, textStatus, errorThrown) {
  var data = {
    xhr: xhr,
    textStatus: textStatus,
    error: errorThrown
  }
  holder.onResult(false, data);
}

export var RequestHelper = {
  /**
   * 查询小组信息列表
   */
  getTeamList(holder) {
    let api = Constant.serverUrl + Constant.api.teamManage.getTeamList;
    let that = this;
    axios.get(api).then(success => {
      handleSuccessResponse(holder, success.data);
    }).catch(error => {
      handleFailResponse(holder, error);
    });
  },
  /**
   * 查询小组成员信息
   */
  getTeamMembers(holder) {
    let api = Constant.serverUrl + Constant.api.teamManage.getTeamMembers +
      "?page=" + holder.page + "&size=" + holder.size + "&teamId=" + holder.teamId;
    let that = this;
    axios.get(api).then(success => {
      handleSuccessResponse(holder, success.data);
    }).catch(error => {
      handleFailResponse(holder, error);
    });
  },
  /**
   * 获取坐席待回呼记录
   */
  getCallBackByOpId(holder) {
    let api = Constant.serverUrl + Constant.api.teamManage.getCallBackByOpId +
      "?opId=" + holder.opId;
    let that = this;
    axios.get(api).then(success => {
      handleSuccessResponse(holder, success.data);
    }).catch(error => {
      handleFailResponse(holder, error);
    });
  },
  /**
   * 获取待分组的坐席
   */
  getUnAssignedOp(holder) {
    let api = Constant.serverUrl + Constant.api.teamManage.getUnAssignedOp;
    let that = this;
    axios.get(api).then(success => {
      handleSuccessResponse(holder, success.data);
    }).catch(error => {
      handleFailResponse(holder, error);
    });
  },
  /**获取组长人选 */
  getLeaders(holder) {
    let api = Constant.serverUrl + Constant.api.teamManage.getLeaders;
    let that = this;
    axios.get(api).then(success => {
      handleSuccessResponse(holder, success.data);
    }).catch(error => {
      handleFailResponse(holder, error);
    });
  },
  /**新增小组 */
  addTeam(holder) {
    let api = Constant.serverUrl + Constant.api.teamManage.addTeam;
    let that = this;
    axios.post(api, {
      userTeam: holder.userTeam,
      members: holder.members
    }).then(success => {
      handleSuccessResponse(holder, success.data);
    }).catch(error => {
      handleFailResponse(holder, error);
    });
  }
};



function install(Vue) {
  Vue.prototype.$teamManageRequest = RequestHelper;
};

export default install;


