<template>
    <div class="main-container gray-bg">
        <el-form ref="form" size="mini" :model="form" label-position="right" label-width="100px">
            <el-row :gutter="10">
                <el-col :span="8">
                    <el-form-item label="业务号：">
                        <el-input v-model="form.userId" placeholder="请输入"></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="8">
                    <el-form-item label="产品名称：">
                        <el-input v-model="form.productName" placeholder="请输入"></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="8">
                    <el-form-item label="产品ID：">
                        <el-input v-model="form.productCode" placeholder="请输入"></el-input>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row :gutter="10">
                <el-col :span="8">
                    <el-form-item label="用户姓名：">
                        <el-input v-model="form.userName" placeholder="请输入"></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="8">
                    <el-form-item label="手机号：">
                        <el-input v-model="form.orderPhone" placeholder="请输入"></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="8">
                    <el-form-item label="支付方式：">
                        <el-select v-model="form.payType" placeholder="请选择">
                            <el-option v-for="payType in payTypeList" :label="payType.text" :value="payType.value" :key="payType.value"></el-option>
                        </el-select>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row :gutter="10">
                <el-col :span="8">
                    <el-form-item label="申请日期：">
                        <el-date-picker v-model="form.createTime" type="datetimerange" range-separator="-" start-placeholder="开始日期" end-placeholder="结束日期">
                        </el-date-picker>
                    </el-form-item>
                </el-col>
                <el-col :span="8">
                    <el-form-item label="订购日期：">
                        <el-date-picker v-model="form.openTime" type="datetimerange" range-separator="-" start-placeholder="开始日期" end-placeholder="结束日期">
                        </el-date-picker>
                    </el-form-item>
                </el-col>
                <el-col :span="8">
                    <el-form-item label="开通状态：">
                        <el-select v-model="form.status" placeholder="请选择">
                            <el-option v-for="status in openStatus" :label="status.text" :value="status.value" :key="status.value"></el-option>
                        </el-select>
                    </el-form-item>
                </el-col>
            </el-row>
        </el-form>

        <div>
            <!-- <el-button style="margin-left:1%;" type="small">导出记录</el-button> -->
            <el-button @click="applyOpen" style="background-color:#009933; color:white;" size="small">申请开通</el-button>
            <el-button type="primary" size="mini" @click="searchOrderApplyList" style="float:right;">&nbsp;&nbsp;查询&nbsp;&nbsp;</el-button>
        </div> <br/>

        <el-table :data="orderApplyList" v-loading="isLoading" style="width: 100%">
            <el-table-column label="业务号" align="center" show-overflow-tooltip>
                <template slot-scope="scope">
                    <span>{{ scope.row.userId }}</span>
                </template>
            </el-table-column>
            <el-table-column label="用户姓名" align="center" show-overflow-tooltip>
                <template slot-scope="scope">
                    <span>{{ scope.row.userName}}</span>
                </template>
            </el-table-column>
            <el-table-column label="开通状态" width="100" align="center">
                <template slot-scope="scope">
                    <span>{{ scope.row.status | orderStatusFilter}}</span>
                </template>
            </el-table-column>
            <el-table-column label="产品名称" align="center" show-overflow-tooltip>
                <template slot-scope="scope">
                    <span>{{ scope.row.productName }}</span>
                </template>
            </el-table-column>

            <el-table-column label="产品ID" align="center" show-overflow-tooltip>
                <template slot-scope="scope">
                    <span>{{ scope.row.productCode }}</span>
                </template>
            </el-table-column>
            <el-table-column label="产品单价" align="center" show-overflow-tooltip>
                <template slot-scope="scope">
                    <span>{{ scope.row.point }}</span>
                </template>
            </el-table-column>
            <el-table-column label="开通月数" align="center" show-overflow-tooltip>
                <template slot-scope="scope">
                    <span>{{ scope.row.cycleCount }}</span>
                </template>
            </el-table-column>
            <el-table-column label="总需积分" align="center" show-overflow-tooltip>
                <template slot-scope="scope">
                    <span>{{ scope.row.point }}</span>
                </template>
            </el-table-column>
            <el-table-column label="申请日期" width="100" sortable align="center" show-overflow-tooltip>
                <template slot-scope="scope">
                    <span>{{ scope.row.createTime }}</span>
                </template>
            </el-table-column>
            <el-table-column label="开通日期" width="100" sortable align="center" show-overflow-tooltip>
                <template slot-scope="scope">
                    <span>{{ scope.row.openTime }}</span>
                </template>
            </el-table-column>
            <el-table-column label="提交客服" align="center" show-overflow-tooltip>
                <template slot-scope="scope">
                    <span>{{ scope.row.opName }}</span>
                </template>
            </el-table-column>
            <el-table-column label="备注" align="center" show-overflow-tooltip>
                <template slot-scope="scope">
                    <el-button v-if="scope.row.remark!=''" @click="viewRemark(scope.row.remark)" type="text">查看</el-button>
                    <span v-else>--</span>
                </template>
            </el-table-column>
            <el-table-column label="操作" align="center" show-overflow-tooltip>
                <template slot-scope="scope">
                    <el-button type="text" @click="viewDetail(scope.row)">详情</el-button>
                    <el-button type="text" @click="resetOrderApply(scope.row)">重置</el-button>
                </template>
            </el-table-column>
        </el-table>
        <div style="float:right;">
            <el-pagination class="layout-pagination" @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="pageInfo.number" :page-sizes="pageInfo.sizeArray" :page-size="pageInfo.currentSize" layout="total, sizes, prev, pager, next, jumper" :total="pageInfo.totalElements">
            </el-pagination>
        </div>
        <div style="clear:both;"></div>
        <detail :form="detailObject" />
        <apply-open :activityList="activityList" />
    </div>
</template>

<script>
import Constant from "@/assets/js/Constant";
import Detail from "@/business/userOrderApply/view/Detail";
import ApplyOpen from "@/business/userOrderApply/view/ApplyOpen";
export default {
    components: {
        Detail,
        ApplyOpen
    },
    props: {},
    data() {
        return {
            payTypeList: [], // 支付方式
            openStatus: [], // 开通状态
            detailObject: {},
            form: {},
            orderApplyList: [
                {
                    createTime: "2018-08-23T08:18:59.744Z",
                    cycleCount: 0,
                    deviceId: "string",
                    id: 0,
                    opId: 0,
                    opName: "string",
                    openTime: "2018-08-23T08:18:59.744Z",
                    orderPhone: "string",
                    payType: 0,
                    point: 0,
                    productCode: "string",
                    productName: "string",
                    remark: "string",
                    status: 1,
                    userId: "string",
                    userName: "string",
                    userPhone: "string"
                }
            ],
            pageInfo: {
                currentSize: 10,
                sizeArray: [10, 20, 50, 100],
                number: 1,
                totalElements: 0
            },
            isLoading: false,
            activityList: [] // 当前在线的营销活动
        };
    },

    created() {
        this.openStatus = Constant.orderStatusList; // 给开通状态列表赋值
        this.payTypeList = Constant.payTypeList; // 给支付方式赋值
        this.getOrderApplyListFromWeb();
    },

    methods: {
        /**表单查询 */
        searchOrderApplyList() {},
        /**详情 */
        viewDetail(row) {
            this.detailObject = row;
            this.$store.commit("SET_SHOW_ORDERAPPLYDETAIL", true);
        },
        /**查看备注 */
        viewRemark(remark) {
            this.$alert(
                "<div style='margin-bottom:10px;'><i class='el-icon-info' style='color:#108EE9;font-size:30px; display:inline-block; vertical-align:middle;'></i><span style='margin-left:12px;font-size:20px;display:inline-block; vertical-align:middle;'>备注</span></div><div style='text-align:center;'>" +
                    remark +
                    "</div>",
                {
                    dangerouslyUseHTMLString: true
                }
            );
        },
        /**重置订购申请 */
        resetOrderApply(orderApply) {
            let that = this;
            this.$orderApplyRequest.resetOrderApply({
                recordId: orderApply.id,
                onResult(isSuccess, data) {
                    if (isSuccess) {
                        that.$message({
                            type: "success",
                            message: data.msg
                        });
                    } else {
                        that.$message({
                            type: "success",
                            message: data.msg
                        });
                    }
                }
            });
        },
        /**
         * 获取订购申请列表
         */
        getOrderApplyListFromWeb() {
            //this.isLoading = true;
            let that = this;
            this.$orderApplyRequest.orderApplyList({
                page: this.pageInfo.number - 1,
                size: this.pageInfo.currentSize,
                userId: this.form.userId,
                productName: this.form.productName,
                productCode: this.form.productCode,
                userName: this.form.userName,
                phone: this.form.phone,
                payType: this.form.payType,
                createTime: this.form.createTime,
                openTime: this.form.openTime,
                onResult(isSuccess, data) {
                    if (isSuccess) {
                        that.handleGetOrderApplyListSuccessResponse(data);
                    } else {
                    }
                    that.isLoading = false;
                }
            });
        },
        handleGetOrderApplyListSuccessResponse(data) {
            this.handlePageInfo(data);
            this.orderApplyList = data.result;
        },
        /**
         * 将page 数据赋值
         */
        handlePageInfo(data) {
            this.pageInfo.totalElements = data.page.totalElements;
            console.log(this.pageInfo);
        },
        /**
         * 改变了每页数据
         */
        handleSizeChange(val) {
            this.pageInfo.currentSize = val;
            this.getBaseProductListFromWeb();
        },
        /**
         * 处理当前的改变
         */
        handleCurrentChange(val) {
            this.pageInfo.number = val;
            this.getBaseProductListFromWeb();
        },
        /**
         * 重置 pageInfo
         */
        resetPageInfo() {
            this.pageInfo.currentSize = this.pageInfo.sizeArray[0];
            this.pageInfo.number = 1;
        },
        /**点击订购申请 */
        applyOpen() {
            let that = this;
            this.$activityRequest.validList({
                onResult(isSuccess, data) {
                    if (isSuccess) {
                        if (data.status == 200) {
                            that.activityList = data.result;
                        }
                    } else {
                    }
                    that.isLoading = false;
                }
            });
            this.$store.commit("SET_SHOW_APPLYOPENDLG", true);
        }
    },
    filters: {
        orderStatusFilter(status) {
            return Constant.orderStatusMap[status];
        }
    },
    watch: {}
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
</style>



