<template>
    <el-dialog title="申请开通" :visible.sync="isCurrentShow" width="61.8%" :before-close="handleClose" :close-on-click-modal="false">
        <el-form size="mini" label-width="25%">
            <el-form-item label="业务号：">
                <el-input v-model="userId" placeholder="请输入"></el-input>
                <el-button style="margin-left:15%;" @click="getUser">在线获取信息</el-button>
            </el-form-item>
        </el-form>
        <hr/>
        <el-form ref="order" size="mini" :model="subscriber" label-position="right" label-width="30%">
            <el-form-item label="*业务号：">
                {{subscriber.userId}}
            </el-form-item>
            <el-form-item label="*设备号：">
                {{subscriber.deviceId}}
            </el-form-item>
            <el-form-item label="*联系方式：">
                {{subscriber.phone}}
            </el-form-item>
            <el-form-item label="*用户姓名：">
                {{subscriber.userName}}
            </el-form-item>
            <el-form-item label="*可用积分：">
                <span style="color:red;">{{subscriber.point}}</span>
            </el-form-item>
            <el-form-item label="*营销类型：">
                <el-radio-group v-model="marketingType">
                    <el-radio label="1">不加入任何活动</el-radio>
                    <el-radio label="2">加入目前在线活动</el-radio>
                </el-radio-group>
            </el-form-item>
            <el-form-item v-if="marketingType == 2" label="*选择活动：">
                <el-select v-model="activity" placeholder="请选择包月产品" style="width:50%;">
                    <el-option v-for="activity in activityList" :label="activity.name" :value="activity" :key="activity.id"></el-option>
                </el-select>
            </el-form-item>
            <el-form-item v-if="marketingType == 1" label="*选择产品类型：">
                <el-radio-group v-model="productType" @change="searchProductList">
                    <el-radio label="1">包月产品</el-radio>
                    <el-radio label="2">按次产品</el-radio>
                </el-radio-group>
            </el-form-item>
            <el-form-item v-if="marketingType == 1" label="*选择产品：">
                <el-select v-if="productType==1" v-model="subscriber.productCode" placeholder="请选择包月产品" style="width:50%;">
                    <el-option v-for="payType in payTypeList" :label="payType.text" :value="payType.value" :key="payType.value"></el-option>
                </el-select>
                <el-select v-if="productType==2" v-model="subscriber.productCode" placeholder="请选择按次产品" style="width:50%;">
                    <el-option v-for="payType in payTypeList" :label="payType.text" :value="payType.value" :key="payType.value"></el-option>
                </el-select>
            </el-form-item>
            <el-form-item v-if="marketingType == 1 && productType==1" label="*选择开通时限：">
                <el-input-number v-model="cycleCount" :min="1" :max="10" style="width:50%;"></el-input-number>
            </el-form-item>
            <el-form-item label="*手机号码：">
                <el-input v-model="orderPhone" placeholder="请输入"></el-input>
            </el-form-item>
            <el-form-item v-if="marketingType == 1" label="*支付方式：">
                {{product.payType | payTypeFilter}}
            </el-form-item>
            <el-form-item>
                <el-button style="float:right; margin-right:15%; background-color:#009933; color:white; border:none;">确认订购</el-button>
            </el-form-item>
        </el-form>
    </el-dialog>
</template>
<script>
import Constant from "@/assets/js/Constant";
export default {
    props: {
        activityList: [] // 活动列表
    },
    data() {
        return {
            payTypeList: [],
            userId: "",
            marketingType: "2", //营销类型 1:不加入活动; 2:加入目前在线活动
            productType: "1", // 产品类型 1:包月; 2:按次
            cycleCount: "", // 开通月数
            productList: [], // 产品列表
            activity: {}, // 选择的活动
            product: {}, // 选择的产品
            orderPhone: "", // 预留电话

            subscriber: {
                address: "string",
                crmId: "string",
                deviceId: "string",
                phone: "string",
                point: 0,
                userId: "string",
                userName: "string",
                userType: 0
            }
        };
    },
    computed: {
        isCurrentShow() {
            return this.$store.state.orderAndRefund.showApplyOpenDlg;
        }
    },
    created() {
        this.payTypeList = Constant.payTypeList;
    },
    methods: {
        /**查找产品列表 */
        searchProductList() {
            let that = this;
            this.$productRequest.getProductList({
                activityId: this.activity.id,
                onResult(isSuccess, data) {
                    if (isSuccess) {
                        that.productList = data.result;
                    } else {
                    }
                }
            });
        },
        /**关闭弹窗 */
        handleClose() {
            this.$refs["order"].resetFields();
            this.$store.commit("SET_SHOW_APPLYOPENDLG", false);
        },
        /**在线获取信息 */
        getUser() {
            let that = this;
            this.$userManageRequest.searchSubscriber({
                userId: that.userId,
                onResult(isSuccess, data) {
                    if (isSuccess) {
                        that.subscriber = data.result;
                    } else {
                    }
                }
            });
        }
    },
    filters: {
        payTypeFilter(payType) {
            return Constant.payTypeMap[payType];
        }
    },
    watch: {}
};
</script>
<style scoped>
.el-input {
    width: 50%;
}

.el-form-item {
    color: #929292;
}
</style>





