<template>
    <div class="main-container gray-bg">
        <el-form :inline="true" size="mini" width="100%" class="demo-form-inline">
            <div style="float:right;">
                <el-form-item>
                    <el-input v-model="activityName" placeholder="请输入任务名称："></el-input>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" @click="searchActivityList">查询</el-button>
                </el-form-item>
            </div>
        </el-form>
        <el-table :data="activityList" v-loading="isLoading" style="width: 100%">
            <el-table-column label="名称" align="center" show-overflow-tooltip>
                <template slot-scope="scope">
                    <span>{{ scope.row.activity.name}}</span>
                </template>
            </el-table-column>
            <el-table-column label="活动类型" align="center" show-overflow-tooltip>
                <template slot-scope="scope">
                    <span>{{ scope.row.activity.type | activityTypeFilter}}</span>
                </template>
            </el-table-column>
            <el-table-column label="开始时间" align="center" show-overflow-tooltip>
                <template slot-scope="scope">
                    <span>{{ scope.row.activity.validTime}}</span>
                </template>
            </el-table-column>
            <el-table-column label="预订购数" align="center" show-overflow-tooltip>
                <template slot-scope="scope">
                    <span>{{ scope.row.calledCount}}</span>
                </template>
            </el-table-column>
            <el-table-column label="支付方式" align="center" show-overflow-tooltip>
                <template slot-scope="scope">
                    <span>{{scope.row.activity.payType | payTypeFilter}}</span>
                </template>
            </el-table-column>
            <!-- <el-table-column label="状态" align="center" show-overflow-tooltip>
                <template slot-scope="scope">
                    <span v-if="scope.row.activity.status==1">在线</span>
                    <span v-if="scope.row.activity.status==0">下线</span>
                </template>
            </el-table-column> -->
            <el-table-column label="操作" align="center" show-overflow-tooltip>
                <template slot-scope="scope">
                    <el-button type="text" @click="ReviewAndOrderDo">审核与订购</el-button>
                    <!-- 网龄活动有导出用户 -->
                    <el-button v-if="scope.row.activity.type==1" type="text">导出用户列表</el-button>
                </template>
            </el-table-column>
        </el-table>
        <div style="float:right;">
            <el-pagination class="layout-pagination" @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="pageInfo.number" :page-sizes="pageInfo.sizeArray" :page-size="pageInfo.currentSize" layout="total, sizes, prev, pager, next, jumper" :total="pageInfo.totalElements">
            </el-pagination>
        </div>
        <div style="clear:both;"></div>
        <review-and-order />
    </div>
</template>
<script>
import Constant from "@/assets/js/Constant";
import ReviewAndOrder from "./ReviewAndOrder";
export default {
    components: {
        ReviewAndOrder
    },
    data() {
        return {
            activityName: "", // 营销活动名称
            activityList: [
                {
                    activity: {
                        autoOpen: 0,
                        createTime: "2018-08-28T07:33:02.724Z",
                        expireTime: "2018-08-28T07:33:02.724Z",
                        id: 0,
                        ivr: "string",
                        maxPoint: 0,
                        minPoint: 0,
                        name: "string",
                        payType: 0,
                        script: "string",
                        smsParameter: "string",
                        smsTemplate: "string",
                        spCode: "string",
                        status: 0,
                        type: 0,
                        validTime: "2018-08-28T07:33:02.724Z"
                    },
                    calledCount: 0,
                    orderCount: 0,
                    orderedCount: 0
                }
            ],
            pageInfo: {
                currentSize: 10,
                sizeArray: [10, 20, 50, 100],
                number: 1,
                totalElements: 0
            },
            isLoading: false
        };
    },
    created() {
        this.getActivityListFromWeb();
    },
    methods: {
        /**查询活动列表 */
        getActivityListFromWeb() {
            //this.isLoading = true;
            let that = this;
            this.$activityRequest.getActivityList({
                activityName: this.activityName,
                page: this.pageInfo.number,
                size: this.pageInfo.currentSize,
                onResult(isSuccess, data) {
                    if (isSuccess) {
                        that.handlePageInfo(data);
                        that.activityList = data.result;
                    } else {
                    }
                    that.isLoading = false;
                }
            });
        },
        /**
         * 将page 数据赋值
         */
        handlePageInfo(data) {
            this.pageInfo.totalElements = data.page.totalElements;
            console.log(this.pageInfo);
        },
        /**
         * 改变了每页数据
         */
        handleSizeChange(val) {
            console.log(`每页 ${val} 条`);
            this.pageInfo.currentSize = val;
            this.getActivityListFromWeb();
        },
        /**
         * 处理当前的改变
         */
        handleCurrentChange(val) {
            console.log(`当前页: ${val}`);
            this.pageInfo.number = val;
            this.getActivityListFromWeb();
        },
        /**
         * 重置 pageInfo
         */
        resetPageInfo() {
            this.pageInfo.currentSize = this.pageInfo.sizeArray[0];
            this.pageInfo.number = 1;
        },
        /**表单查询活动列表 */
        searchActivityList() {
            this.resetPageInfo();
            this.getActivityListFromWeb();
        },
        /**审核与订购操作 */
        ReviewAndOrderDo() {
            this.$store.commit("SET_SHOW_REVIEWANDORDER", true);
        }
    },
    filters: {
        payTypeFilter(payType) {
            return Constant.payTypeMap[payType];
        },
        activityTypeFilter(activityType) {
            return Constant.activityTypeMap[activityType];
        }
    }
};
</script>



