<template>
    <div class="middle-box text-center loginscreen animated fadeInDown">
        <div>
            <form class="m-t">
                <div class="form-group">
                    <input type="text" class="form-control" v-model="name" placeholder="Username">
                </div>
                <div class="form-group">
                    <input type="password" class="form-control" v-model="password" placeholder="Password">
                </div>
                <button class="btn btn-primary block full-width m-b" v-on:click="onClickLogin">Login</button>
            </form>
        </div>
    </div>
</template>

<script>
export default {
    data() {
        return {
            name: "",
            password: ""
        };
    },
    methods: {
        onClickLogin() {
            this.$router.push({
                name: "MainLayout"
            });
            this.saveNameInStore();
        },

        saveNameInStore() {
            this.$store.commit("SET_OPNAME", this.name);
        }
    }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
</style>



