<template>
    <div class="main-container gray-bg">
        <!-- 查询表单 -->
        <el-form :inline="true" size="mini" :model="formInline" class="demo-form-inline">
            <el-row :gutter="4">
                <el-col :span=7>
                    <el-form-item label="产品名称">
                        <el-input v-model="formInline.title" placeholder="输入产品名称"></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span=7>
                    <el-form-item label="产品ID">
                        <el-input v-model="formInline.productCode" placeholder="输入产品ID"></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span=7>
                    <el-form-item label="支付类型">
                        <el-select v-model="formInline.payType">
                            <el-option label="全部" value="" aria-checked></el-option>
                            <el-option v-for="payType in payTypeList" :label="payType.text" :value="payType.value" :key="payType.value"></el-option>
                        </el-select>
                    </el-form-item>
                </el-col>
                <el-col :span=3>
                    <el-form-item>
                        <el-button type="primary" @click="selectProduct">查询</el-button>
                    </el-form-item>
                </el-col>
            </el-row>
        </el-form>
        <!-- 新增产品按钮 -->
        <el-input id="productCode" placeholder="请输入产品ID" v-model="addProduct.productCode" size="small" style="width:30%; margin-bottom:20px;">
            <el-button slot="append" @click="handleAddProduct">新增产品</el-button>
        </el-input>
        <!-- 产品列表 -->
        <el-table v-loading="isLoading" :data="productList" style="width: 100%">
            <el-table-column label="产品名称" align="center" show-overflow-tooltip>
                <template slot-scope="scope">
                    <span>{{ scope.row.title}}</span>
                </template>
            </el-table-column>
            <el-table-column label="产品类型" align="center" show-overflow-tooltip>
                <template slot-scope="scope">
                    <span>{{ scope.row.type | formatType}}</span>
                </template>
            </el-table-column>
            <el-table-column label="产品ID" align="center" show-overflow-tooltip>
                <template slot-scope="scope">
                    <span>{{ scope.row.productCode }}</span>
                </template>
            </el-table-column>
            <el-table-column label="支付方式" align="center" show-overflow-tooltip>
                <template slot-scope="scope">
                    <span>{{ scope.row.payType | formatPayType}}</span>
                </template>
            </el-table-column>
            <el-table-column label="总价值" align="center" show-overflow-tooltip>
                <template slot-scope="scope">
                    <span>{{ scope.row.payPrice+scope.row.payPoint }}</span>
                </template>
            </el-table-column>
            <el-table-column label="最低积分" align="center" show-overflow-tooltip>
                <template slot-scope="scope">
                    <span>{{ scope.row.minPoint }}</span>
                </template>
            </el-table-column>
            <el-table-column label="出账(元)" align="center" show-overflow-tooltip>
                <template slot-scope="scope">
                    <span>{{ scope.row.payPrice/100 }}</span>
                </template>
            </el-table-column>
            <el-table-column label="筛选上限" align="center" show-overflow-tooltip>
                <template slot-scope="scope">
                    <span>{{ scope.row.maxPoint }}</span>
                </template>
            </el-table-column>
            <el-table-column label="月数" align="center" show-overflow-tooltip>
                <template slot-scope="scope">
                    <span>{{ scope.row.orderCycleCount }}</span>
                </template>
            </el-table-column>
            <el-table-column label="状态" align="center" show-overflow-tooltip>
                <template slot-scope="scope">
                    <span v-if="scope.row.status==1">已上线</span>
                    <span v-else>已下线</span>
                </template>
            </el-table-column>
            <el-table-column label="排斥关系" align="center" show-overflow-tooltip>
                <template slot-scope="scope">
                    <span>{{scope.row.exclusionPID}}</span>
                </template>
            </el-table-column>
            <el-table-column label="上/下线" align="center">
                <template slot-scope="scope">
                    <el-switch v-model="scope.row.status" :active-value="1" :inactive-value="0" @change="statusChange(scope.row)"></el-switch>
                </template>
            </el-table-column>
            <el-table-column label="操作" align="center" show-overflow-tooltip>
                <template slot-scope="scope">
                    <el-button size="mini" type="text" @click="modifyProduct(scope.row)">修改</el-button>
                </template>
            </el-table-column>
        </el-table>
        <div style="float:right;">
            <el-pagination class="layout-pagination" @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="pageInfo.number" :page-sizes="pageInfo.sizeArray" :page-size="pageInfo.currentSize" layout="total, sizes, prev, pager, next, jumper" :total="pageInfo.totalElements">
            </el-pagination>
        </div>
        <div style="clear:both;"></div>
        <!-- 添加产品弹窗 -->
        <add-product :product="addProduct" @saveProduct="saveProduct"></add-product>
        <!-- 修改产品弹窗 -->
        <modify-product :product="modProduct" @updateProduct="updateProduct"></modify-product>
    </div>
</template>
<script>
import Constant from "@/assets/js/Constant";
import AddProduct from "@/business/product/AddProduct";
import ModifyProduct from "@/business/product/ModifyProduct";
export default {
    components: {
        AddProduct,
        ModifyProduct
    },
    props: {},
    data() {
        return {
            modProduct: {},
            addProduct: {},
            payTypeList: [],
            formInline: {
                title: "",
                productCode: "",
                payType: ""
            },
            isLoading: false,
            pageInfo: {
                currentSize: 10,
                sizeArray: [10, 20, 50, 100],
                number: 1,
                totalElements: 0
            },
            productList: [
                {
                    desc: "string",
                    exclusionPID: "string",
                    id: 0,
                    maxPoint: 0,
                    minPoint: 0,
                    name: "string",
                    orderCount: 0,
                    orderCycleCount: 0,
                    origPrice: 0,
                    payPoint: 1,
                    payPrice: 1,
                    payType: 2,
                    pointProductId: "string",
                    productCode: "123123123123",
                    status: 0,
                    title: "首映看大片-1804灵活",
                    type: 1
                }
            ]
        };
    },
    filters: {
        // 类型显示转换
        formatType(type) {
            return Constant.productTypeMap[type];
        },
        formatPayType(payType) {
            return Constant.payTypeMap[payType];
        }
    },
    created() {
        this.payTypeList = Constant.payTypeList;
        this.getProductListFromWeb();
    },
    computed: {},
    methods: {
        /**
         * 获取产品列表
         */
        getProductListFromWeb() {
            this.isLoading = true;
            let that = this;
            this.$productRequest.getProductList({
                name: this.formInline.title,
                productCode: this.formInline.productCode,
                payType: this.formInline.payType,
                page: this.pageInfo.number - 1,
                size: this.pageInfo.currentSize,
                onResult(isSuccess, data) {
                    if (isSuccess) {
                        that.handleGetProductListSuccessResponse(data);
                    } else {
                    }
                    that.isLoading = false;
                }
            });
        },
        handleGetProductListSuccessResponse(data) {
            this.handlePageInfo(data);
            this.productList = data.result;
        },
        /**
         * 将page 数据赋值
         */
        handlePageInfo(data) {
            this.pageInfo.totalElements = data.page.totalElements;
        },
        /**
         * 重置 pageInfo
         */
        resetPageInfo() {
            this.pageInfo.currentSize = this.pageInfo.sizeArray[0];
            this.pageInfo.number = 1;
        },
        handleSizeChange(val) {
            this.pageInfo.currentSize = val;
            // console.log(`每页 ${val} 条`);
            this.getProductListFromWeb();
        },
        handleCurrentChange(val) {
            this.pageInfo.number = val;
            // console.log(`当前页: ${val}`);
            this.getProductListFromWeb();
        },
        selectProduct() {
            this.resetPageInfo();
            this.getProductListFromWeb();
        },
        /**
         * 点击添加按钮
         */
        handleAddProduct() {
            if (!this.addProduct.productCode) {
                $("#productCode").focus();
                this.$message({
                    type: "warning",
                    message: "请输入产品id"
                });
                return;
            } else {
                if (!/^[1][0-9]{10,}$/.test(this.addProduct.productCode)) {
                    this.$message({
                        type: "error",
                        message: "请输入正确的产品id"
                    });
                    return;
                }
            }
            // 先通过productCode在线查询产品信息
            let that = this;
            this.$baseProductRequest.searchBaseProduct({
                productId: this.addProduct.productCode,
                onResult(isSuccess, data) {
                    if (isSuccess) {
                        if (data.status == 200 && data.result) {
                            // 如果查询到，赋值给addProduct
                            that.addProduct = data.result;
                            that.$store.commit("SET_SHOW_ADDPRODUCT", true);
                        } else {
                            // 如果没有查询到，提示输入正确的产品id
                            that.$message({
                                type: "error",
                                message: "请输入正确的产品id"
                            });
                        }
                    } else {
                        that.$message({
                            type: "error",
                            message: "当前网络异常"
                        });
                    }
                }
            });
        },
        /**
         * 点击修改按钮
         */
        modifyProduct(product) {
            let that = this;
            this.$store.commit("SET_SHOW_MODIFYPRODUCT", true);
            this.$productRequest.getProductById({
                productId: product.id,
                onResult(isSuccess, data) {
                    if (isSuccess) {
                        if (data.status == 200) {
                            that.modProduct = data.result;
                        } else if (code != 1000) {
                            that.$message({
                                type: "error",
                                message: data.msg
                            });
                        }
                    } else {
                    }
                }
            });
        },
        /**修改弹窗提交 */
        updateProduct(product) {
            let that = this;
            this.$productRequest.updateProduct({
                productId: product.id,
                product: product,
                onResult(isSuccess, data) {
                    if (isSuccess) {
                        if (data.status == 200) {
                            that.getProductListFromWeb();
                            that.$store.commit("SET_SHOW_MODIFYPRODUCT", false);
                            that.$message({
                                type: "success",
                                message: "产品修改成功！！！"
                            });
                        } else {
                            that.$message({
                                type: "error",
                                message: data.msg
                            });
                        }
                    } else {
                    }
                }
            });
        },
        /**修改产品状态 */
        statusChange(product) {
            let that = this;
            this.$productRequest.updateOnlineStatus({
                product: product,
                onResult(isSuccess, data) {
                    if (isSuccess) {
                        if (data.status == 200) {
                            that.$message({
                                type: "success",
                                message: "产品状态修改成功！！！"
                            });
                        } else {
                            product.status = product.status == 0 ? 1 : 0;
                        }
                    } else {
                        product.status = product.status == 0 ? 1 : 0;
                        that.$message({
                            type: "error",
                            message: "当前网络异常"
                        });
                    }
                }
            });
        },
        /**添加产品提交 */
        saveProduct(product) {
            let that = this;
            this.$productRequest.addProduct({
                product: product,
                onResult(isSuccess, data) {
                    if (isSuccess) {
                        if (data.status == 200) {
                            that.$store.commit("SET_SHOW_ADDPRODUCT", false);
                            that.$message({
                                type: "success",
                                message: "产品添加成功！"
                            });
                        } else if (data.status == 500 && data.code !== 1000) {
                            that.$store.commit("SET_SHOW_ADDPRODUCT", false);
                            that.$message({
                                type: "warning",
                                message: data.msg
                            });
                        } else {
                            that.$message({
                                type: "error",
                                message: "服务器异常"
                            });
                        }
                    } else {
                        that.$message({
                            type: "error",
                            message: "当前网络异常"
                        });
                    }
                }
            });
        }
    }
};
</script>
<style>
/*
.el-table th {
    background: #f3f3f3 !important;
}
 .el-form {
    background: #f3f3f3 !important;
    padding-top: 15px;
} */
</style>





