<template>
    <el-row :gutter="20">
        <el-col :span="16">
            <echart-bar /><br/>
            <echart-line />
        </el-col>
        <el-col :span="8">
            <order-list /><br/>
            <call-list />
        </el-col>
    </el-row>
</template>
<script>
import EchartBar from "./EchartBar.1";
import EchartLine from "./EchartLine";
import OrderList from "./OrderList";
import CallList from "./CallList";
export default {
    components: {
        EchartBar,
        EchartLine,
        OrderList,
        CallList
    },
    data() {
        return {};
    }
};
</script>


