<template>
    <el-dialog title="新增产品" :visible.sync="isCurrentShow" width="70%" :before-close="handleClose" :close-on-click-modal="false">
        <el-form ref="baseProduct" :model="baseProduct" :rules="rules" label-width="180px" size="mini" label-position="right">
            <el-row :gutter="20" type="flex" class="row-bg">
                <el-col :span="12">
                    <el-form-item label="产品ID：" class="item" prop="code">
                        <el-input v-model="baseProduct.code" disabled placeholder="请输入"></el-input>
                    </el-form-item>
                    <el-form-item label="产品类型：" prop="type">
                        <el-select v-model="baseProduct.type" placeholder="请输入产品类型">
                            <el-option v-for="type in productTypeList" :key="type.value" :label="type.text" :value="type.value"></el-option>
                        </el-select>
                    </el-form-item>

                    <el-form-item label="积分价值：" prop="point" class="item">
                        <el-input v-model="baseProduct.point" placeholder="请输入"></el-input>
                    </el-form-item>
                    <el-form-item label="租借期：" class="item" prop="rentalPeriod">
                        <el-input v-model="baseProduct.rentalPeriod" placeholder="请输入"></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="12">
                    <el-form-item label="产品名称：" prop="name">
                        <el-input v-model="baseProduct.name" placeholder="请输入"></el-input>
                    </el-form-item>
                    <el-form-item label="支付方式：" prop="payType">
                        <el-select v-model="baseProduct.payType" placeholder="请选择支付方式">
                            <el-option v-for="payType in payTypeList" :label="payType.text" :value="payType.value" :key="payType.value"></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="价格（分）：" prop="origPrice">
                        <el-input v-model="baseProduct.origPrice" placeholder="请输入"></el-input>
                    </el-form-item>
                    <el-form-item label="上/下线：" class="item">
                        <el-switch v-model="baseProduct.status" :active-value="1" :inactive-value="0" active-text="上线" inactive-text="下线"></el-switch>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-form-item label="互斥产品：">
                <el-input v-model="baseProduct.exclusionPID" placeholder="请输入"></el-input>
            </el-form-item>
            <el-form-item label="产品描述：">
                <el-input type="textarea" :rows="2" placeholder="请输入备注：" v-model="baseProduct.desc">
                </el-input>
            </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
            <el-button type="primary" size="mini" @click="addBaseProduct">保存</el-button>
        </span>
    </el-dialog>
</template>
<script>
import Constant from "@/assets/js/Constant";
export default {
    name: "AddProduct",
    props: {
        baseProduct: {
            code: "123444444444",
            desc: "假的",
            exclusionPID: "测试",
            id: 0,
            name: "浏览器的",
            origPrice: 0,
            payType: 0,
            point: 0,
            rentalPeriod: 0,
            status: 0,
            type: 0
        }
    },
    data() {
        return {
            payTypeList: [],
            productTypeList: [],
            isLoading: false,
            rules: {
                code: [
                    {
                        required: true,
                        trigger: "blur",
                        pattern: /^[1][0-9]{10,19}$/,
                        message: "请输入正确的产品ID"
                    }
                ],
                name: [
                    {
                        required: true,
                        trigger: "blur",
                        min: "4",
                        max: "17",
                        message: "请输入正确的产品名称"
                    }
                ],
                type: [
                    {
                        required: true,
                        message: "请选择合理的产品类型",
                        trigger: "change"
                    }
                ],
                payType: [
                    {
                        required: true,
                        message: "请选择有效的支付方式",
                        trigger: "change"
                    }
                ],
                point: [
                    {
                        required: false,
                        message: "请输入最低积分",
                        trigger: "blur"
                    },
                    { pattern: /^[0-9]{0,}$/, message: "请输入有效数字" }
                ],
                origPrice: [
                    {
                        required: false,
                        message: "请输入积分上限",
                        trigger: "blur"
                    },
                    { pattern: /^[0-9]{0,}$/, message: "请输入有效数字" }
                ],
                rentalPeriod: [
                    { required: false, trigger: "blur" },
                    { pattern: /^[0-9]{0,}$/, message: "请输入有效数字" }
                ]
            }
        };
    },
    computed: {
        isCurrentShow() {
            return this.$store.state.baseProduct.showAddBaseProduct;
        }
    },
    created() {
        this.payTypeList = Constant.payTypeList;
        this.productTypeList = Constant.productTypeList;
    },
    methods: {
        handleClose() {
            this.$store.commit("SET_SHOW_ADDBASEPRODUCT", false);
        },
        /**添加基础产品 */
        addBaseProduct() {
            var that = this;
            this.$refs["baseProduct"].validate(valid => {
                if (valid) {
                    this.$baseProductRequest.saveBaseProduct({
                        baseProduct: this.baseProduct,
                        onResult(isSuccess, data) {
                            if (isSuccess) {
                                if (data.status == 200) {
                                    that.handleClose();
                                    that.$message({
                                        type: "success",
                                        message: "产品添加成功！"
                                    });
                                } else {
                                    that.handleClose();
                                    that.$message({
                                        type: "error",
                                        message: data.msg
                                    });
                                }
                            } else {
                            }
                        }
                    });
                } else {
                    that.$message({
                        type: "error",
                        message: "请确保产品信息正确"
                    });
                    return false;
                }
            });
        }
    }
};
</script>
<style>
#tishi {
    float: left;
    color: rgb(50, 142, 234);
}

/* .el-input__inner:focus {
    border-color: rgb(63, 164, 237);
    box-shadow: none;
    transition-duration: 0.5s;
}
.el-input__inner:hover {
    border-color: rgb(63, 164, 237);
} */
</style>




