import Vue from 'vue'
import axios from 'axios'
import Constant from '@/assets/js/Constant'
import Qs from 'qs'

function logout() {

}

function exit(code) {

}

/**
 * 统一处理成功回应
 */
function handleSuccessResponse(holder, data) {
  var response = {
    body: data
  }
  holder.onResult(true, response);
}

/**
 * 统一处理失败回应
 */
function handleFailResponse(holder, xhr, textStatus, errorThrown) {
  var data = {
    xhr: xhr,
    textStatus: textStatus,
    error: errorThrown
  }
  holder.onResult(false, data);
}

export var RequestHelper = {
  /**
   * 获取当前操作员所属cp
   */
  getCategoryTree(holder) {
    let api = `${Constant.serverUrl + Constant.api.category.getCategoryTree}?id=${holder.id}`;
    let that = this;
    axios.get(api).then(success => {
      handleSuccessResponse(holder, success.data);
    }).catch(error => {
      handleFailResponse(holder, error);
    });
  },
  /**
   * 获取内容包
   */
  getPackages(holder) {
    let categoryId;
    let info;
    let status;
    let beginTime;
    let endTime;
    let publishStatus;
    let sort;
    console.log(holder);
    if (holder.sort) {
      sort = holder.sort;
    } else {
      sort = "";
    }
    if (!holder.categoryId || holder.categoryId == "" || holder.categoryId.length === 0) {
      categoryId = "1";
    } else {
      categoryId = holder.categoryId;
    }
    if (holder.info) {
      info = holder.info;
    } else {
      info = "";
    }
    if (holder.status) {
      status = holder.status;
    } else {
      status = "";
    }
    if (holder.beginTime) {
      beginTime = holder.beginTime;
    } else {
      beginTime = "";
    }
    if (holder.endTime) {
      endTime = holder.endTime;
    } else {
      endTime ="";
    }
    if (holder.publishStatus) {
      publishStatus = holder.publishStatus;
    } else {
      publishStatus = "";
    }
    let api = `${Constant.serverUrl + Constant.api.category.getPackages}?sort=${sort}&size=${holder.size}&page=${holder.page}&categoryId=${categoryId}&Info=${info}&status=${status}&beginTime=${beginTime}&endTime=${endTime}&publishStatus=${publishStatus}`;
    let that = this;
    axios.get(api).then(success => {
      handleSuccessResponse(holder, success.data);
    }).catch(error => {
      handleFailResponse(holder, error);
    });
  },
  /**
   * 获取标记
   */
  getFlags(holder) {
    let api = `${Constant.api.category.getFlags}`;
    let that = this;
    axios.get(api).then(success => {
      handleSuccessResponse(holder, success.data);
    }).catch(error => {
      handleFailResponse(holder, error);
    });
  },
  /**
   * 获取内容列表
   */
  searchContentFromWeb(holder) {
    let contentType;
    if (holder.contentType) {
      contentType = holder.contentType;
    } else {
      contentType = "";
    }
    let enableStatus;
    if (holder.enableStatus) {
      enableStatus = holder.enableStatus;
    } else {
      enableStatus = "";
    }
    let cpCode;
    if (holder.cpCode) {
      cpCode = holder.cpCode;
    } else {
      cpCode = "";
    }

    let api = `${Constant.serverUrl + Constant.api.content.searchContent}?page=${holder.page}&size=${holder.size}&contentType=${contentType}&enableStatus=${enableStatus}&cpCode=${cpCode}`.trim();
    let that = this;
    axios.get(api).then(success => {
      handleSuccessResponse(holder, success.data);
    }).catch(error => {
      handleFailResponse(holder, error);
    });
  },
  /**
   * 获取内容包
   */
  searchPackage(holder) {
    let categoryId;
    if (holder.categoryId) {
      categoryId = holder.categoryId;
    } else {
      categoryId = "";
    }
    let auditStatus;
    if (holder.auditStatus) {
      auditStatus = holder.auditStatus;
    } else {
      auditStatus = "";
    }
  

    let api = `${Constant.serverUrl + Constant.api.category.searchPackage}?page=${holder.page}&size=${holder.size}&categoryId=${categoryId}&auditStatus=${auditStatus}`;
    let that = this;
    axios.get(api).then(success => {
      handleSuccessResponse(holder, success.data);
    }).catch(error => {
      handleFailResponse(holder, error);
    });
  },
  /**
   * 获取内容类型列表
   */
  getContentTypes(holder) {
    let api = `${Constant.api.getContentType}`;
    let that = this;
    axios.get(api).then(success => {
      handleSuccessResponse(holder, success.data);
    }).catch(error => {
      handleFailResponse(holder, error);
    });
  },
  /**
   * 获取地区列表
   */
  getAreas(holder) {
    let api = `${Constant.api.getCopyRightArea}`;
    let that = this;
    axios.get(api).then(success => {
      handleSuccessResponse(holder, success.data);
    }).catch(error => {
      handleFailResponse(holder, error);
    });
  }
};



function install(Vue) {
  Vue.prototype.$categoryRequest = RequestHelper;
};

export default install;


