<template>
    <div class="main-container gray-bg">
        <el-form :inline="true" size="mini" :model="formInline" class="demo-form-inline">
            <el-col :span="12">
                <el-form-item label="支付类型：">
                    <el-select v-model="formInline.payType">
                        <el-option label="全部" value=""></el-option>
                        <el-option v-for="payType in payTypeList" :label="payType.text" :value="payType.value" :key="payType.value"></el-option>
                    </el-select>
                </el-form-item>
            </el-col>
            <el-col :span="8">
                <el-form-item label="产品名称：">
                    <el-input v-model="formInline.productName" placeholder="请输入"></el-input>
                </el-form-item>
            </el-col>
            <el-col :span="4">
                <el-form-item>
                    <el-button type="primary" @click="searchActivityList">&nbsp;&nbsp;查询&nbsp;&nbsp;</el-button>
                </el-form-item>
            </el-col>
        </el-form>
        <div style="clear:left;">
            <el-button size="small" @click="refresh">刷新营销数</el-button>
            <el-button @click="addActivity" size="small" style="background-color:108EE9;">新增任务</el-button>
        </div>
        <br/>
        <el-table :data="activityList" style="width: 100%">
            <el-table-column prop="activity.id" label="ID" align="center">
            </el-table-column>
            <el-table-column prop="activity.name" label="名称" align="center">
            </el-table-column>
            <el-table-column prop="activity.createTime" label="创建时间" align="center" show-overflow-tooltip>
            </el-table-column>
            <el-table-column label="支付方式" align="center">
                <template slot-scope="scope">
                    <span>{{scope.row.activity.payType | payTypeFilter}}</span>
                </template>
            </el-table-column>
            <el-table-column prop="productCount" label="产品数" align="center">
            </el-table-column>
            <el-table-column label="活动类型" align="center">
                <template slot-scope="scope">
                    <span>{{scope.row.activity.type | activityTypeFilter}}</span>
                </template>
            </el-table-column>
            <el-table-column prop="subscriberCount" label="营销用户数" width="90" align="center">
            </el-table-column>
            <!-- <el-table-column prop="activity.minPoint" label="最低积分" align="center">
            </el-table-column>
            <el-table-column prop="activity.maxPoint" label="最高积分" align="center">
            </el-table-column> -->
            <el-table-column label="进度详情" align="center">
                <template slot-scope="scope">
                    <el-button type="text" @click="viewDetail(scope.row.activity)">详情</el-button>
                </template>
            </el-table-column>
            <el-table-column label="上/下线" align="center">
                <template slot-scope="scope">
                    <el-switch v-model="scope.row.activity.status" inactive-color="#ccc" :active-value="1" :inactive-value="0" @change="statusChange(scope.row.activity)">
                    </el-switch>
                </template>
            </el-table-column>
            <el-table-column label="自动开通/手动开通" width="140" align="center">
                <template slot-scope="scope">
                    <el-switch v-model="scope.row.activity.autoOpen" active-color="#669900" inactive-color="#ccc" :active-value="1" :inactive-value="0" @change="autoOpenSwitch(scope.row.activity)">
                    </el-switch>
                </template>
            </el-table-column>
            <el-table-column label="操作" align="center" width="140">
                <template slot-scope="scope">
                    <span v-if="scope.row.activity.status == 0">
                        <el-button type="text" style="color:black;">- -</el-button>
                    </span>
                    <span v-else>
                        <el-button type="text" @click="modifyMission(scope.row.activity)">修改</el-button>
                        <el-button type="text">导入用户</el-button>
                    </span>
                </template>
            </el-table-column>
        </el-table>
        <div style="float:right; ">
            <el-pagination class="layout-pagination " @size-change="handleSizeChange " @current-change="handleCurrentChange " :current-page="pageInfo.number " :page-sizes="pageInfo.sizeArray " :page-size="pageInfo.currentSize " layout="total, sizes, prev, pager, next, jumper " :total="pageInfo.totalElements ">
            </el-pagination>
        </div>
        <div style="clear:both; "></div>
        <add-mission @refreshActivities="getActivityListFromWeb" />
        <modify-mission :activity="modActivity" :productArray="products" @refreshActivities="getActivityListFromWeb" />
        <mission-detail :activity="viewActivity" />
    </div>
</template>
<script>
import Constant from "@/assets/js/Constant";
import AddMission from "./AddMission";
import ModifyMission from "./ModifyMission";
import MissionDetail from "./MissionDetail";
export default {
    components: {
        AddMission,
        ModifyMission,
        MissionDetail
    },
    data() {
        return {
            payTypeList: [],
            formInline: {
                payType: "",
                productName: ""
            },
            activityList: [],
            pageInfo: {
                currentSize: 10,
                sizeArray: [10, 20, 50, 100],
                number: 1,
                totalElements: 0
            },
            isLoading: false,
            modActivity: {}, // 要修改的任务
            products: [], // 要修改任务所绑定的产品
            viewActivity: {} // 要查看的任务
        };
    },
    created() {
        this.payTypeList = Constant.payTypeList;
        this.getActivityListFromWeb();
    },
    methods: {
        /**刷新营销数 */
        refresh() {
            this.getActivityListFromWeb();
        },
        /**查询活动列表 */
        getActivityListFromWeb() {
            this.isLoading = true;
            let that = this;
            this.$activityRequest.getActivityList({
                payType: this.formInline.payType,
                productName: this.formInline.productName,
                page: this.pageInfo.number - 1,
                size: this.pageInfo.currentSize,
                onResult(isSuccess, data) {
                    if (isSuccess) {
                        if (data.status == 200) {
                            that.handlePageInfo(data);
                            that.activityList = data.result;
                        } else {
                            that.$message({
                                type: "error",
                                message: "服务器异常"
                            });
                        }
                    } else {
                        that.$message({
                            type: "error",
                            message: "当前网络异常"
                        });
                    }
                    that.isLoading = false;
                }
            });
        },
        /**
         * 将page 数据赋值
         */
        handlePageInfo(data) {
            this.pageInfo.totalElements = data.page.totalElements;
        },
        /**
         * 改变了每页数据
         */
        handleSizeChange(val) {
            this.pageInfo.currentSize = val;
            this.getActivityListFromWeb();
        },
        /**
         * 处理当前的改变
         */
        handleCurrentChange(val) {
            this.pageInfo.number = val;
            this.getActivityListFromWeb();
        },
        /**
         * 重置 pageInfo
         */
        resetPageInfo() {
            this.pageInfo.currentSize = this.pageInfo.sizeArray[0];
            this.pageInfo.number = 1;
        },
        /**表单查询 */
        searchActivityList() {
            this.resetPageInfo();
            this.getActivityListFromWeb();
        },
        /**新增任务 */
        addActivity() {
            this.$store.commit("SET_SHOW_ADDACTIVITY", true);
        },
        /**修改任务 */
        modifyMission(activity) {
            let that = this;
            this.$activityRequest.getActivity({
                activityId: activity.id,
                onResult(isSuccess, data) {
                    if (isSuccess) {
                        if (data.status == 200) {
                            that.modActivity = data.result.activity;
                            that.products = data.result.products;
                            that.$store.commit("SET_SHOW_MODIFYACTIVITY", true);
                        } else {
                            that.$message({
                                type: "error",
                                message: "服务器异常"
                            });
                        }
                    } else {
                        that.$message({
                            type: "error",
                            message: "当前网络异常"
                        });
                    }
                }
            });
        },
        /**查看任务详情 */
        viewDetail(activity) {
            this.viewActivity = activity;
            this.$store.commit("SET_SHOW_ACTIVITYDETAIL", true);
        },
        /**更改活动上下线状态 */
        statusChange(activity) {
            let that = this;
            this.$activityRequest.updateOnlineStatus({
                activityId: activity.id,
                onlineStatus: activity.status,
                onResult(isSuccess, data) {
                    if (isSuccess) {
                        if (data.status == 200) {
                            that.$message({
                                type: "success",
                                message: "状态更新成功"
                            });
                        } else {
                            activity.status = activity.status == 0 ? 1 : 0;
                            that.$message({
                                type: "error",
                                message: "服务器异常"
                            });
                        }
                    } else {
                        activity.status = activity.status == 0 ? 1 : 0;
                        that.$message({
                            type: "error",
                            message: "当前网络异常"
                        });
                    }
                }
            });
        },
        /**是否自动开通 */
        autoOpenSwitch(activity) {
            let that = this;
            this.$activityRequest.updateAutoOpen({
                activityId: activity.id,
                autoOpen: activity.autoOpen,
                onResult(isSuccess, data) {
                    if (isSuccess) {
                        if (data.status == 200) {
                            that.$message({
                                type: "success",
                                message: "自动开通更新成功"
                            });
                        } else {
                            activity.autoOpen = activity.autoOpen == 0 ? 1 : 0;
                            that.$message({
                                type: "error",
                                message: "服务器异常"
                            });
                        }
                    } else {
                        activity.autoOpen = activity.autoOpen == 0 ? 1 : 0;
                        that.$message({
                            type: "error",
                            message: "当前网络异常"
                        });
                    }
                }
            });
        }
    },
    filters: {
        payTypeFilter(payType) {
            return Constant.payTypeMap[payType];
        },
        activityTypeFilter(activityType) {
            return Constant.activityTypeMap[activityType];
        }
    }
};
</script>
<style>
</style>



