<template>
    <div>
        <h3>
            <span style="color:red;font-weight:700;margin-right:6px;">●</span>列表查询
        </h3>
        <el-form :inline="true" size="mini" :model="formInline" class="demo-form-inline">
            <el-row :gutter="20">
                <el-col :span="10">
                    <el-form-item label="产品ID:">
                        <el-input v-model="formInline.code" placeholder="请输入"></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="10">
                    <el-form-item label="产品名称:">
                        <el-input v-model="formInline.name" placeholder="请输入"></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="4">
                    <el-form-item>
                        <el-button type="primary" style="margin-top:-5px;" @click="searchBaseProduct">查询</el-button>
                    </el-form-item>
                </el-col>
            </el-row>
        </el-form>

        <el-table :data="baseProductList" v-loading="isLoading" style="width: 100%">
            <el-table-column label="产品ID" align="center" show-overflow-tooltip>
                <template slot-scope="scope">
                    <span>{{ scope.row.code }}</span>
                </template>
            </el-table-column>
            <el-table-column label="产品名称" align="center" show-overflow-tooltip="">
                <template slot-scope="scope">
                    <span>{{ scope.row.name}}</span>
                </template>
            </el-table-column>
            <el-table-column label="价值积分" align="center">
                <template slot-scope="scope">
                    <span>{{ scope.row.point}}</span>
                </template>
            </el-table-column>
            <!--支付方式筛选-->
            <el-table-column label="支付方式" align="center" width="90">
                <template slot-scope="scope">
                    <span>{{scope.row.payType | payTypeFilter}}</span>
                </template>
            </el-table-column>
            <!-- 属性 -->
            <!--属性筛选-->
            <el-table-column label="属性" align="center">
                <template slot-scope="scope">
                    <span v-if="scope.row.type==0">包月</span>
                    <span v-if="scope.row.type==1">按次</span>
                </template>
            </el-table-column>
            <!--状态筛选-->
            <el-table-column label="状态" align="center">
                <template slot-scope="scope">
                    <span v-if="scope.row.status==1">已上线</span>
                    <span v-if="scope.row.status==0">已下线</span>
                </template>
            </el-table-column>
            <el-table-column label="租借期" align="center">
                <template slot-scope="scope">
                    <span>{{ scope.row.rentalPeriod }}</span>
                </template>
            </el-table-column>
            <el-table-column label="互斥产品" align="center">
                <template slot-scope="scope">
                    <el-button type="text" @click="viewMutexProduct(scope.row.exclusionPID)">查看</el-button>
                </template>
            </el-table-column>
            <el-table-column label="描述" align="center">
                <template slot-scope="scope">
                    <el-button type="text" @click="viewDesc(scope.row.desc)">查看</el-button>
                </template>
            </el-table-column>
            <!-- 产品描述/产品介绍 -->
            <el-table-column label="产品介绍" align="center">
                <template slot-scope="scope">
                    <el-button type="text" @click="viewDesc(scope.row.desc)">查看</el-button>
                </template>
            </el-table-column>
            <el-table-column label="上/下线" align="center">
                <template slot-scope="scope">
                    <el-switch v-model="scope.row.status" :active-value="1" :inactive-value="0" @change="statusChange(scope.row)">
                    </el-switch>
                </template>
            </el-table-column>
            <!-- <el-table-column label="纯积分/纯出账" align="center">
        <template slot-scope="scope">
          <el-switch v-model="scope.row.payType" :active-value="1" :inactive-value="0" @change="typeChange(scope.row)">
          </el-switch>
        </template>
      </el-table-column> -->
        </el-table>
        <div style="float:right;">
            <el-pagination class="layout-pagination" @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="pageInfo.number" :page-sizes="pageInfo.sizeArray" :page-size="pageInfo.currentSize" layout="total, sizes, prev, pager, next, jumper" :total="pageInfo.totalElements">
            </el-pagination>
        </div>
        <div style="clear:both;"></div>
    </div>
</template>

<script>
import Constant from "@/assets/js/Constant";
export default {
    components: {},
    props: {},
    data() {
        return {
            formInline: {
                code: "",
                name: ""
            },
            baseProductList: [
                {
                    code: "122233333333",
                    desc: "string",
                    exclusionPID: "string",
                    id: 0,
                    name: "string",
                    origPrice: 0,
                    payType: 0,
                    point: 0,
                    rentalPeriod: 0,
                    status: 0,
                    type: 0
                }
            ],
            pageInfo: {
                currentSize: 10,
                sizeArray: [10, 20, 50, 100],
                number: 1,
                totalElements: 0
            },
            isLoading: false
        };
    },

    created() {
        this.getBaseProductListFromWeb();
    },

    methods: {
        /**
         * 获取基础产品列表
         */
        getBaseProductListFromWeb() {
            this.isLoading = true;
            let that = this;
            this.$baseProductRequest.getBaseProductList({
                page: this.pageInfo.number - 1,
                size: this.pageInfo.currentSize,
                name: this.formInline.name,
                code: this.formInline.code,
                onResult(isSuccess, data) {
                    if (isSuccess) {
                        that.handleGetBaseProductListSuccessResponse(data);
                    } else {
                    }
                    that.isLoading = false;
                }
            });
        },
        handleGetBaseProductListSuccessResponse(data) {
            this.handlePageInfo(data);
            this.baseProductList = data.result;
        },
        /**
         * 将page 数据赋值
         */
        handlePageInfo(data) {
            this.pageInfo.totalElements = data.page.totalElements;
            console.log(this.pageInfo);
        },
        /**
         * 改变了每页数据
         */
        handleSizeChange(val) {
            console.log(`每页 ${val} 条`);
            this.pageInfo.currentSize = val;
            this.getBaseProductListFromWeb();
        },
        /**
         * 处理当前的改变
         */
        handleCurrentChange(val) {
            console.log(`当前页: ${val}`);
            this.pageInfo.number = val;
            this.getBaseProductListFromWeb();
        },
        /**
         * 重置 pageInfo
         */
        resetPageInfo() {
            this.pageInfo.currentSize = this.pageInfo.sizeArray[0];
            this.pageInfo.number = 1;
        },
        /**
         * 点击按条件查找
         */
        searchBaseProduct() {
            this.resetPageInfo();
            this.getBaseProductListFromWeb();
        },
        /**
         * 修改基础产品状态
         */
        statusChange(baseProduct) {
            let that = this;
            this.$baseProductRequest.updateOnlineStatus({
                productId: baseProduct.id,
                onlineStatus: baseProduct.status,
                onResult(isSuccess, data) {
                    if (isSuccess) {
                        if (data.status == 200) {
                            that.$message({
                                type: "success",
                                message: "状态修改成功"
                            });
                        } else {
                            baseProduct.status =
                                baseProduct.status == 0 ? 1 : 0;
                            that.$message({
                                type: "error",
                                message: "服务器异常"
                            });
                        }
                    } else {
                        baseProduct.status = baseProduct.status == 0 ? 1 : 0;
                        that.$message({
                            type: "error",
                            message: "当前网络异常"
                        });
                    }
                }
            });
        },
        /**
         * 修改基础产品类型
         */
        typeChange(baseProduct) {
            let that = this;
            console.log("修改产品类型！！！！！！！");
        },
        /**产看互斥产品 */
        viewMutexProduct(exclusionPID) {
            this.$alert(
                "<div style='text-align:center;'>" + exclusionPID + "</div>",
                {
                    dangerouslyUseHTMLString: true
                }
            );
        },
        /**查看描述 */
        viewDesc(desc) {
            this.$alert("<div style='text-align:center;'>" + desc + "</div>", {
                dangerouslyUseHTMLString: true
            });
        }
    },
    filters: {
        payTypeFilter(payType) {
            return Constant.payTypeMap[payType];
        }
    },

    watch: {}
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
</style>



