import router from './router'
import store from '@/store/store'
import {
    Message
} from 'element-ui'

// permissiom judge
function hasPermission(roles, permissionRoles) {
    if (roles.indexOf('admin') >= 0) return true // admin权限 直接通过
    if (!permissionRoles) return true
    return roles.some(role => permissionRoles.indexOf(role) >= 0)
}

const whiteList = ['/login'] // 不重定向白名单

router.beforeEach((to, from, next) => {
    // NProgress.start() // 开启Progress
    if (true) { // 判断是否有token 目前先置为 true !!!!
        if (to.path === '/login') {
            // next({ path: '/' })
            next();
            // NProgress.done() // router在hash模式下 手动改变hash 重定向回来 不会触发afterEach 暂时hack方案 ps：history模式下无问题，可删除该行！
        } else {
            if (store.state.user.roles.length === 0) { // 判断当前用户是否已拉取完user_info信息
                store.dispatch('GetUserInfo').then(res => { // 拉取user_info
                    const roles = res.data.role
                    store.dispatch('GenerateRoutes', {
                        roles
                    }).then(() => { // 生成可访问的路由表
                        router.addRoutes(store.state.permission.addRouters) // 动态添加可访问路由表
                        next({ ...to,
                            replace: true
                        }) // hack方法 确保addRoutes已完成 ,replace: true so the navigation will not leave a history record
                        // router.replace(to);
                    })
                }).catch((e) => {
                    console.log(e);
                    store.dispatch('FedLogOut').then(() => {
                        Message.error('验证失败,请重新登录')
                        next({
                            path: '/login'
                        })
                    })
                })
            } else {
                // 没有动态改变权限的需求可直接next() 删除下方权限判断 ↓
                if (hasPermission(store.state.user.roles, to.meta.role)) {
                    next() //
                } else {
                    next({
                        path: '/401',
                        query: {
                            noGoBack: true
                        }
                    })
                    // NProgress.done() // router在hash模式下 手动改变hash 重定向回来 不会触发afterEach 暂时hack方案 ps：history模式下无问题，可删除该行！
                }
                // 可删 ↑
            }
        }
    } else {
        if (whiteList.indexOf(to.path) !== -1) { // 在免登录白名单，直接进入
            next()
        } else {
            next('/login') // 否则全部重定向到登录页
            // NProgress.done() // router在hash模式下 手动改变hash 重定向回来 不会触发afterEach 暂时hack方案 ps：history模式下无问题，可删除该行！
        }
    }
})

router.afterEach(() => {
    // NProgress.done() // 结束Progress
})

