<template>
    <el-dialog :visible.sync="isCurrentShow" width="70%" :before-close="handleClose" :close-on-click-modal="false">
        <div style="margin-top:-30px;margin-bottom:-50px;">
            <i class="el-icon-warning" style="color:red;font-size:36px; display:inline-block; vertical-align:middle;"></i>
            <span style="margin-left:12px;font-size:20px;display:inline-block; vertical-align:middle;">详情</span>
        </div>
        <el-form ref="order" size="mini" :model="order" label-position="left" label-width="120px" style="padding:10%;">
            <el-col :span="12">
                <el-form-item label="订单号：">
                    {{order.orderNo}}
                </el-form-item>
                <el-form-item label="姓名：">
                    {{order.userName}}
                </el-form-item>
                <el-form-item label="支付方式：">
                    {{order.payType | payTypeFilter}}
                </el-form-item>
                <el-form-item label="产品价格：">
                    {{order.price}}
                </el-form-item>
                <el-form-item label="状态：">
                    {{order.status}}
                </el-form-item>
                <el-form-item label="开通失败原因：">
                    {{order.failedReason}}
                </el-form-item>
                <el-form-item label="设备号：">
                    {{order.deviceId}}
                </el-form-item>
                <el-form-item label="手机号码：">
                    {{order.phone}}
                </el-form-item>
            </el-col>
            <el-col :span="12">
                <el-form-item label="业务号：">
                    {{order.userId}}
                </el-form-item>
                <el-form-item label="产品名称：">
                    {{order.productName}}
                </el-form-item>
                <el-form-item label="投诉来源：">
                    {{order.unorderSource}}
                </el-form-item>
                <el-form-item label="开通时间：">
                    {{order.createTime}}
                </el-form-item>
                <el-form-item label="退单时间：">
                    {{order.unorderTime}}
                </el-form-item>
                <el-form-item label="厂商：">
                    {{order.spCode}}
                </el-form-item>
                <el-form-item label="电话：">
                    {{order.phone}}
                </el-form-item>
            </el-col>
        </el-form>
        <div style="clear:both;"></div>
    </el-dialog>
</template>
<script>
import Constant from "@/assets/js/Constant";
export default {
    props: {
        order: { type: Object }
    },
    data() {
        return {
            form: {}
        };
    },
    computed: {
        isCurrentShow() {
            return this.$store.state.order.showDetailDlg;
        }
    },
    methods: {
        handleClose() {
            this.$store.commit("SET_SHOW_DETAILDLG", false);
        }
    },
    filters: {
        payTypeFilter(payType) {
            return Constant.payTypeMap[payType];
        }
    }
};
</script>




