<template>
    <div style="width:100%; border:solid 1px #CCC">
        <div style="margin-top: 20px">
            <div style="border-bottom:solid 1px #ccc">
                <h3 style="display:inline-block;vertical-alige:middle; margin-left:3%;">订单排行榜</h3>
                <el-select size="mini" v-model="currentActivity" style="margin:0 14px;width:50%;">
                    <el-option v-for="activity in activityList" :key="activity.id" :value="activity">
                        {{activity.name}}
                    </el-option>
                </el-select>
                <el-button type="text" style="line-height:1px;">更多</el-button>
            </div>
            <el-radio-group v-model="checkedTeam" size="mini" style="margin-top:10px;">
                <el-radio-button :label="0" checked>总排行</el-radio-button>
                <el-radio-button v-for="team in teams" :key="team.teamName" :label="team.teamName"></el-radio-button>
            </el-radio-group>
            <el-table :data="currentTop5" style="width: 100%">
                <el-table-column prop="rank" label="排名" align="center" width="50">
                </el-table-column>
                <el-table-column prop="opName" label="姓名" align="center" width="50">
                </el-table-column>
                <el-table-column prop="orderCount" label="订单量" align="center" width="70">
                </el-table-column>
                <el-table-column prop="name" label="占本任务成功数" align="center" width="90">
                </el-table-column>
                <el-table-column prop="address" label="与上任务环比" align="center" width="70">
                </el-table-column>
            </el-table>
        </div>
    </div>
</template>
<script>
export default {
    data() {
        return {
            activityList: [],
            currentActivity: {},
            checkedTeam: "",
            currentTop5: [
                { rank: 1, opName: "张三", orderCount: 199 },
                { rank: 2, opName: "李四", orderCount: 121 },
                { rank: 3, opName: "王五", orderCount: 137 },
                { rank: 4, opName: "马六", orderCount: 158 },
                { rank: 5, opName: "小七", orderCount: 101 }
            ],
            teams: [
                {
                    teamName: "张三组",
                    members: [
                        { opId: 3001, opName: "张三1" },
                        { opId: 3002, opName: "张三2" }
                    ]
                },
                {
                    teamName: "李四组",
                    members: [
                        { opId: 4001, opName: "李四1" },
                        { opId: 4002, opName: "李四2" }
                    ]
                }
            ]
        };
    }
};
</script>



