<template>
  <div class="menu-wrapper">
    <template v-for="item in routes" v-if="!item.hidden&&item.children">

      <div v-if="item.children.length===1 && !item.children[0].children" :key="item.children[0].name" v-on:click="onClickSideBarItem(item, true)">
        <el-menu-item :index="item.path+'/'+item.children[0].path" class='submenu-title-noDropdown'>
          <svg-icon v-if="item.children[0].meta&&item.children[0].meta.icon" :icon-class="item.children[0].meta.icon"></svg-icon>
          <span v-if="item.children[0].meta&&item.children[0].meta.title">{{generateTitle(item.children[0].meta.title)}}</span>
        </el-menu-item>
      </div>

      <el-submenu v-else :index="item.name||item.path" :key="item.name">
        <template slot="title">
          <svg-icon v-if="item.meta&&item.meta.icon" :icon-class="item.meta.icon"></svg-icon>
          <span v-if="item.meta&&item.meta.title">{{generateTitle(item.meta.title)}}</span>
        </template>

        <template v-for="child in item.children" v-if="!child.hidden">
          <sidebar-item class="nest-menu" v-if="child.children&&child.children.length>0" :routes="[child]" :key="child.path"></sidebar-item>

          <div v-else :key="child.name" v-on:click="onClickSideBarItem(item, false, child)">
            <el-menu-item :index="item.path+'/'+child.path">
              <svg-icon v-if="child.meta&&child.meta.icon" :icon-class="child.meta.icon"></svg-icon>
              <span v-if="child.meta&&child.meta.title">{{generateTitle(child.meta.title)}}</span>
            </el-menu-item>
          </div>
        </template>
      </el-submenu>

    </template>
  </div>
</template>

<script>
import { generateTitle } from "@/utils/i18n";
import Constant from "@/assets/js/Constant";

export default {
    name: "SidebarItem",
    props: {
        routes: {
            type: Array
        }
    },
    methods: {
        generateTitle,
        /**
         * 点击菜单
         */
        onClickSideBarItem(item, isChildClick, child) {
            console.log("onClickSideBarItem");
            console.log(item);
            console.log(child);
            if (!item) {
                console.log("item 为false return");
                return;
            }
            let linkType;
            if (isChildClick) {
                linkType = item.meta.linkType;
            } else {
                linkType = child.meta.linkType;
            }
            console.log(linkType);

            if (linkType && this.$common.isNotBlank(linkType)) {
                this.handleByLinkType(item, linkType, isChildClick, child);
            } else {
                // linkType 为空 直接走跳转
                console.log("linkType 为空 return");
                this.handleGo2App(item, isChildClick, child);
            }
        },
        /**
         * 根据linktype进行不同的跳转
         */
        handleByLinkType(item, linkType, isChildClick, child) {
            console.log("handleByLinkType linkType:" + linkType);
            switch (linkType) {
                case Constant.linkType.app:
                    this.handleGo2App(item, isChildClick, child);
                    break;
                case Constant.linkType.link:
                    this.handleGo2Link(item, isChildClick, child);
                    break;
                default:
                    this.handleGo2App(item, isChildClick, child);
                    break;
            }
        },
        /**
         * app 内部访问
         */
        handleGo2App(item, isChildClick, child) {
            let path;
            if (isChildClick) {
                path = item.path + "/" + item.children[0].path;
            } else {
                path = item.path + "/" + child.path;
            }
            console.log("path:" + path);
            this.$router.push({ path: path });
        },
        /**
         * 外部链接访问
         */
        handleGo2Link(item, isChildClick, child) {
            let path;
            if (isChildClick) {
                path = item.children[0].path;
            } else {
                path = child.path;
            }
            window.open(path);
        }
    },
    watch: {
        routes() {}
    }
};
</script>




