<template>
    <div style="background-color:white; padding:20px 10px;">
        <div>
            <el-table :data="productList" style="width: 100%">
                <el-table-column width="50" lable="">
                    <template slot-scope="scope">
                        <el-radio :label="scope.row.id" v-model="templateRadio" @change.native="getTemplateRow(scope.$index,scope.row)">&nbsp;</el-radio>
                    </template>
                </el-table-column>
                <el-table-column label="产品名称" align="center" show-overflow-tooltip>
                    <template slot-scope="scope">
                        <span>{{ scope.row.title }}</span>
                    </template>
                </el-table-column>
                <el-table-column label="支付方式" align="center" show-overflow-tooltip>
                    <template slot-scope="scope">
                        <span>{{ scope.row.payType | payTypeFilter}}</span>
                    </template>
                </el-table-column>
                <el-table-column label="积分兑换（分）" align="center" show-overflow-tooltip>
                    <template slot-scope="scope">
                        <span>{{ scope.row.payPoint}}</span>
                    </template>
                </el-table-column>
                <el-table-column label="出账支付（元）" align="center" show-overflow-tooltip>
                    <template slot-scope="scope">
                        <span>{{ scope.row.payPrice }}</span>
                    </template>
                </el-table-column>
            </el-table>
        </div>
        <br>
        <div>
            <div v-if="subscriber" style="width:60%;float:left; margin-bottom:20px;">
                <table style="border:solid 1px rgb(233,233,233);width:100%;">
                    <tr>
                        <td class="colum">姓名</td>
                        <td>{{subscriber.userName}}</td>
                        <td class="colum">业务号</td>
                        <td>{{subscriber.userId}}</td>
                    </tr>
                    <tr>
                        <td class="colum">设备号</td>
                        <td>{{subscriber.deviceId}}</td>
                        <td class="colum">积分</td>
                        <td>{{subscriber.point}}</td>
                    </tr>
                    <tr>
                        <td class="colum">手机/电话</td>
                        <td>{{subscriber.phone}}
                            <el-button type="text" id="phone" @click="togglePhone">显示</el-button>
                        </td>
                        <td class="colum">地址</td>
                        <td>{{subscriber.address}}
                            <el-button type="text" id="address" @click="toggleAddress">显示</el-button>
                        </td>
                    </tr>
                </table>
            </div>
            <div v-if="!subscriber" style="width:60%;float:left; margin-bottom:20px;">
                <h2 style="color:red;">请先获取营销对象！！！！</h2>
            </div>
            <div id="result" style="width:35%;float:right; height:500px;">
                <el-form :model="order" ref="resultForm" :rules="rules" label-position="left" size="mini">
                    <el-form-item label="结果：">
                        <el-radio-group v-model="order.result">
                            <el-radio label="12" style="color:#66CC00;">营销成功</el-radio>
                            <el-radio label="1" style="color:red;">营销失败</el-radio>
                        </el-radio-group>
                        <!-- 营销成功 -->
                        <div v-if="order.result==12">
                            <div style="clear:left;margin-bottom:30px;">
                                <el-form-item label="短信通知手机：" prop="userPhone">
                                    <el-input v-model="order.userPhone" placeholder="请输入手机号"></el-input>
                                </el-form-item>
                                <!-- 如果是包月产品 -->
                                <el-form-item v-if="checkedProduct && checkedProduct.type==0" label="订购月数：">
                                    <el-input-number v-model="order.cycleCount" :min="1" :max="maxCount" label="开通月数"></el-input-number>
                                </el-form-item>
                            </div>
                        </div>
                        <div v-if="order.result==1">
                            <el-radio-group v-model="order.falseType">
                                <div style="margin-bottom:30px;">
                                    <el-radio label="1" style="color:#555; margin-right:20px;">营销失败</el-radio>
                                    <el-select v-if="order.falseType==1" v-model="order.failedReason" placeholder="失败类型" style="width:40%;margin-right:20px;">
                                        <el-option v-for="failReason in failReasonList" :label="failReason.text" :value="failReason.value" :key="failReason.value"></el-option>
                                    </el-select>
                                    <el-select v-if="order.falseType != 1" v-model="order.failedReason" disabled placeholder="失败类型" style="width:40%;margin-right:20px;">
                                    </el-select>
                                </div>
                                <div style="margin-bottom:30px;">
                                    <el-radio label="6" style="color:#555; margin-right:20px;">有意向&nbsp;&nbsp;&nbsp;&nbsp;</el-radio>
                                    <el-select v-if="order.falseType==6" v-model="order.willingType" placeholder="意向类型" style="width:40%;margin-right:20px;">
                                        <el-option label="预约" value="1"></el-option>
                                        <el-option label="待考虑" value="2"></el-option>
                                        <el-option label="高意向" value="3"></el-option>
                                    </el-select>
                                    <el-select v-if="order.falseType != 6" v-model="order.willingType" disabled placeholder="意向类型" style="width:40%;margin-right:20px;">
                                    </el-select>
                                </div>
                                <div v-if="order.falseType==6 && order.willingType==1" style="float:right; margin-top:-20px;">
                                    <el-form-item label="预约时间：">
                                        <el-date-picker v-model="order.yuYueTime" @change="formatDate" style="width:85%;" type="datetime" placeholder="选择日期时间">
                                        </el-date-picker>
                                    </el-form-item>
                                </div>
                                <div>
                                    <el-radio label="2" style="color:#555;">无人应答</el-radio>
                                </div>
                            </el-radio-group>
                        </div>
                    </el-form-item>
                    <el-form-item>
                        <el-button v-if="subscriber && order.result" style="float:right;" type="primary" @click="saveRecord()">保存记录</el-button>
                        <el-button v-if="!order.result || !subscriber" disabled style="float:right;" type="primary" @click="saveRecord()">保存记录</el-button>
                    </el-form-item>
                </el-form>
            </div>
        </div><br>
        <div id="dial" style="width:60%;float:left;">
            呼叫号码：
            <el-input size="mini" v-model="subscriber.phone" style="width:40%;margin-bottom:10px;"></el-input>
            <br/> 电话外呼：
            <el-button size="small" type="primary">外呼</el-button>········
            <el-button size="small">接入IVR</el-button>········
            <el-button size="small">获取用户按键</el-button>········
            <el-button size="small">挂机</el-button>
        </div><br>
        <div style="float:left;width:60%; margin-top:30px;">
            <table id="table2" style="border:solid 1px rgb(233,233,233);">
                <tr>
                    <td class="colum">工作台状态</td>
                    <td class="colum1"></td>
                </tr>
                <tr>
                    <td class="colum">用户响应</td>
                    <td class="colum1"></td>
                </tr>
                <tr>
                    <td class="colum">录音编号</td>
                    <td class="colum1"></td>
                </tr>
                <tr>
                    <td class="colum">脚本</td>
                    <td class="colum1">{{activity.script}}</td>
                </tr>
            </table>
        </div>
        <div style="clear:both;"></div>
    </div>
</template>

<script>
import { Loading } from "element-ui";
import Constant from "@/assets/js/Constant";
export default {
    components: {},
    props: {
        activity: { type: Object }
    },
    data() {
        return {
            templateRadio: 1,
            failReasonList: [],
            order: {
                result: "", // 营销结果 12:营销成功; 1:营销失败
                failedReason: "", //失败原因
                userPhone: "", // 营销成功预留手机号
                cycleCount: "", // 订购月数
                falseType: "", //失败的类型 1:营销失败 6:有意向 2:无人应答
                willingType: "", //意向类型
                yuYueTime: "", // 预约时间
                appoDate: ""
            },
            maxCount: "", // 订购上限
            activityId: "", // 活动id
            checkedProduct: null, // 产品列表中选中的产品
            productList: [
                {
                    desc: "string",
                    exclusionPID: "string",
                    id: 0,
                    maxPoint: 0,
                    minPoint: 0,
                    name: "string",
                    orderCount: 0,
                    orderCycleCount: 0,
                    origPrice: 0,
                    payPoint: 2,
                    payPrice: 1,
                    payType: 2,
                    pointProductId: "string",
                    productCode: "string",
                    status: 0,
                    title: "首映看大片-1804灵活",
                    type: 0
                }
            ],
            subscriber: {},
            rules: {
                userPhone: [
                    {
                        required: false,
                        pattern: /^1[34578]\d{9}$/,
                        message: "请输入正确的手机号码",
                        trigger: "blur"
                    }
                ]
            }
        };
    },
    created() {
        this.failReasonList = Constant.failReasonList;
        this.getProductsByActivityIdFromWeb();
    },
    computed: {},
    methods: {
        /**行选中 */
        getTemplateRow(index, row) {
            this.checkedProduct = row;
        },
        /**获取营销活动对应的产品列表 */
        getProductsByActivityIdFromWeb() {
            let that = this;
            this.$activityRequest.getProductsByActivityId({
                activityId: this.activity.id,
                onResult(isSuccess, data) {
                    if (isSuccess) {
                        that.productList = data.result;
                    } else {
                    }
                }
            });
        },
        /**获取下一个营销用户 */
        nextUser() {
            let that = this;
            let loadingInstance = Loading.service({
                fullscreen: true,
                text: "正在获取下一个营销对象,请稍后...",
                spinner: "el-icon-loading",
                background: "rgba(0,0,0,0.8)"
            });
            this.$activityRequest.nextUser({
                activityId: this.activity.id,
                opId: 1,
                //opId: window.parent.userInfo.userId,
                onResult(isSuccess, data) {
                    // 判断是否还有营销对象
                    //this.$emit("isLoadingChange", false);
                    if (isSuccess) {
                        if (data.status == 200 && data.result) {
                            that.subscriber = data.result;
                        } else if (data.status == 200 && !data.result) {
                            that.$message({
                                type: "warning",
                                message: "您在该活动暂时没有营销对象"
                            });
                            //that.$store.commit("SET_SHOW_CALLPAGE", false);
                            that.subscriber = null;
                        } else {
                            that.$message({
                                type: "error",
                                message: "服务器异常"
                            });
                        }
                    } else {
                        that.$message({
                            type: "error",
                            message: "当前网络异常"
                        });
                    }
                    loadingInstance.close();
                }
            });
        },
        /**保存拨打记录 */
        saveRecord() {
            var that = this;
            var callRecord = {
                activityName: this.activity.name,
                activityId: this.activity.id,
                userId: this.subscriber.userId,
                userName: this.subscriber.userName,
                opId: 1,
                // opId: document.parent.userInfo.userId,
                opName: "坐席一号"
            };
            if (!this.order.result) {
                return;
            }
            if (this.order.result == 12) {
                callRecord.result = 12;
                if (this.checkedProduct == null) {
                    this.$message({
                        type: "error",
                        message: "请选择有效的产品！"
                    });
                    return;
                } else {
                    callRecord.productId = this.checkedProduct.id;
                    callRecord.productName = this.checkedProduct.name;
                    callRecord.orderCycleCount = this.checkedProduct.orderCycleCount;
                    callRecord.payType = this.checkedProduct.payType;
                    callRecord.payPoint = this.checkedProduct.payPoint;
                    callRecord.payPrice = this.checkedProduct.payPrice;
                    callRecord.orderPhone = this.order.userPhone;
                }
            } else if (this.order.result == 1) {
                if (this.order.falseType == "") {
                    this.$message({
                        type: "error",
                        message: "请选择失败类型！"
                    });
                    return;
                } else if (this.order.falseType == 1) {
                    // 营销失败
                    callRecord.result = 1;
                    if (this.order.failedReason == "") {
                        this.$message({
                            type: "error",
                            message: "请选择失败原因！"
                        });
                        return;
                    } else {
                        callRecord.failedReason = this.order.failedReason;
                    }
                } else if (this.order.falseType == 6) {
                    // 有意向
                    callRecord.result = 6;
                    if (this.order.willingType == "") {
                        this.$message({
                            type: "error",
                            message: "请选择意向类型！"
                        });
                        return;
                    } else if (this.order.willingType == 1) {
                        if (this.order.yuYueTime == "") {
                            this.$message({
                                type: "error",
                                message: "请选择预约时间！"
                            });
                            return;
                        } else {
                            callRecord.willType = this.order.willingType;
                            callRecord.appoDate = this.order.appoDate;
                        }
                    } else {
                        callRecord.willType = this.order.willingType;
                    }
                } else {
                    callRecord.result = 2;
                }
            }
            this.$refs["resultForm"].validate(valid => {
                if (valid) {
                    let loadingInstance = Loading.service({
                        fullscreen: true,
                        text: "正在保存记录,请稍后...",
                        spinner: "el-icon-loading",
                        background: "rgba(0,0,0,0.8)"
                    });
                    this.$callRecordRequest.addCallRecord({
                        callRecord: callRecord,
                        onResult(isSuccess, data) {
                            if (isSuccess) {
                                if (data.status == 200) {
                                    that.$emit("refreshMyActivity");
                                    that.$message({
                                        type: "success",
                                        message: "拨打记录保存成功！"
                                    });
                                    that.nextUser();
                                } else {
                                    that.$message({
                                        type: "error",
                                        message: "记录失败！"
                                    });
                                }
                            } else {
                                that.$message({
                                    type: "error",
                                    message: "当前网络异常"
                                });
                            }
                            loadingInstance.close();
                        }
                    });
                } else {
                    that.$message({
                        type: "error",
                        message: "请确记录信息正确"
                    });
                }
            });
        },
        togglePhone() {
            var content = $("#phone").text();
            if (content == "显示") {
                $("#phone").text("隐藏");
            } else {
                $("#phone").text("显示");
            }
        },
        toggleAddress() {
            var content = $("#address").text();
            if (content == "显示") {
                $("#address").text("隐藏");
            } else {
                $("#address").text("显示");
            }
        },
        formatDate(val) {
            if (val) {
                const dateMat = new Date(val);
                const year = dateMat.getFullYear();
                const month = dateMat.getMonth() + 1;
                const day = dateMat.getDate();
                const hh = dateMat.getHours();
                const mm = dateMat.getMinutes();
                const ss = dateMat.getSeconds();
                const timeFormat =
                    year +
                    "-" +
                    month +
                    "-" +
                    day +
                    " " +
                    hh +
                    ":" +
                    mm +
                    ":" +
                    ss;
                this.order.appoDate = timeFormat;
            }
            console.log(this.order.appoDate);
        }
    },
    mounted() {
        this.nextUser();
    },
    filters: {
        payTypeFilter(payType) {
            return Constant.payTypeMap[payType];
        }
    },
    watch: {
        activity(nawActivity, old) {
            this.getProductsByActivityIdFromWeb();
            this.nextUser();
        },
        checkedProduct(current, old) {
            // 如果是包月产品
            if (current.type === 0) {
                this.order.cycleCount = Math.floor(
                    this.subscriber.point / current.payPoint
                );
                this.maxCount = Math.floor(
                    this.subscriber.point / current.payPoint
                );
            }
        }
    }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
td {
    text-align: center;
    border: 1px solid #cccccc;
    height: auto;
}
.colum {
    padding: 6px;
    font: 1.2em sans-serif;
    font-weight: 500;
    color: black;
    width: 20%;
}
#dial > .el-button {
    margin-left: -5px;
}
.colum1 {
    width: 80%;
}
</style>



