<template>
    <div>
        <el-form ref="form" size="mini" :model="form" label-position="right" label-width="120px">
            <el-row :gutter="10">
                <el-col :span="8">
                    <el-form-item label="任务类型：">
                        <el-select v-model="form.userType" placeholder="所有">
                            <el-option label="区域一" value="shanghai"></el-option>
                            <el-option label="区域二" value="beijing"></el-option>
                        </el-select>
                    </el-form-item>
                </el-col>
                <el-col :span="8">
                    <el-form-item label="用户类型：">
                        <el-select v-model="form.userType" placeholder="所有">
                            <el-option label="区域一" value="shanghai"></el-option>
                            <el-option label="区域二" value="beijing"></el-option>
                        </el-select>
                    </el-form-item>
                </el-col>
                <el-col :span="8">
                    <el-form-item label="开始结束时间：">
                        <el-date-picker v-model="form.time" type="daterange" range-separator="-" start-placeholder="开始日期" end-placeholder="结束日期">
                        </el-date-picker>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row :gutter="10">
                <el-col :span="8">
                    <el-form-item label="客服：">
                        <el-select v-model="form.userType" placeholder="请选择">
                            <el-option label="区域一" value="shanghai"></el-option>
                            <el-option label="区域二" value="beijing"></el-option>
                        </el-select>
                    </el-form-item>
                </el-col>
                <el-col :span="8">
                    <el-form-item></el-form-item>
                </el-col>
                <el-col :span="8">
                    <el-form-item>
                        <el-button type="primary">&nbsp;查询&nbsp;</el-button>
                    </el-form-item>
                </el-col>
            </el-row>
        </el-form>
        <div style="float:right; margin-bottom:10px;">
            <el-button size="mini" style=" ">清理统计空间</el-button>
            <el-button size="mini" style=" ">&nbsp;主动刷新&nbsp;</el-button>
        </div>
        <div id="info" style="clear:both;height:68px;background-color:#E6F3FC; line-height:34px; ">
            <el-col :span="1">
                <i class="el-icon-info" style="color:#108EE9;margin-left:10px;font-size:20px;line-height:68px;"></i>
            </el-col>
             <el-col :span="23">
                 <el-row>
                    <el-col :span="2"><span style="font-weight:600;">【查询总计】</span></el-col>
                    <el-col :span="3">收入/元：<span>1234</span></el-col>
                    <el-col :span="4">实收/元：<span>1234</span></el-col>
                    <el-col :span="3">退费：<span>1234</span></el-col>
                    <el-col :span="3">总营销：<span>1234</span></el-col>
                    <el-col :span="3">未领取<span>1234</span></el-col>
                    <el-col :span="3">领取：<span>1234</span></el-col>
                    <el-col :span="3">拨打：<span>1234</span></el-col>
                </el-row>
                <el-row>
                    <el-col :span="3">预订购A：<span>1234</span></el-col>
                    <el-col :span="3">开通成功B：<span>1234</span></el-col>
                    <el-col :span="3">开通失败C：<span>1234</span></el-col>
                    <el-col :span="3">待开D：<span>1234</span></el-col>
                    <el-col :span="3">A-B-C-D：<span>1234</span></el-col>
                    <el-col :span="3">实开B-E：<span>1234</span></el-col>
                    <el-col :span="2">退单：<span>1234</span></el-col>
                    <el-col :span="2">回呼：<span>1234</span></el-col>
                    <el-col :span="2">打回：<span>1234</span></el-col>
                </el-row>
             </el-col>
        </div>
        <el-table :data="tableData " v-loading="isLoading " style="width: 100% " tooltip-effect="dark ">
            <el-table-column label="任务ID " align="center " show-overflow-tooltip>
                <template slot-scope="scope ">
                    <span>{{ scope.row.userId }}</span>
                </template>
            </el-table-column>
            <el-table-column label="状态 " align="center " show-overflow-tooltip>
                <template slot-scope="scope ">
                    <span>{{ scope.row.deviceId}}</span>
                </template>
            </el-table-column>
            <el-table-column label="任务名称 " align="center " show-overflow-tooltip>
                <template slot-scope="scope ">
                    <span>{{ scope.row.userName}}</span>
                </template>
            </el-table-column>
            <el-table-column label="价格 " align="center " show-overflow-tooltip>
                <template slot-scope="scope ">
                    <span>{{ scope.row.phone}}</span>
                </template>
            </el-table-column>

            <el-table-column label="支付方式 " align="center " show-overflow-tooltip>
                <template slot-scope="scope ">
                    <span>{{ scope.row.phone}}</span>
                </template>
            </el-table-column>
            <el-table-column label="收入/元 " align="center " show-overflow-tooltip>
                <template slot-scope="scope ">
                    <span>{{ scope.row.point }}</span>
                </template>
            </el-table-column>
            <el-table-column label="退费 " align="center " show-overflow-tooltip>
                <template slot-scope="scope ">
                    <span>{{ scope.row.smsContent }}</span>
                </template>
            </el-table-column>
            <el-table-column label="实收/元 " align="center " show-overflow-tooltip>
                <template slot-scope="scope ">
                    <span>{{ scope.row.result }}</span>
                </template>
            </el-table-column>
            <el-table-column label="总营销 " align="center " show-overflow-tooltip>
                <template slot-scope="scope ">
                    <span>{{ scope.row.result }}</span>
                </template>
            </el-table-column>
            <el-table-column label="领取 " align="center " show-overflow-tooltip>
                <template slot-scope="scope ">
                    <span>{{ scope.row.result }}</span>
                </template>
            </el-table-column>
            <el-table-column label="拨打 " align="center " show-overflow-tooltip>
                <template slot-scope="scope ">
                    <span>{{ scope.row.result }}</span>
                </template>
            </el-table-column>
            <el-table-column label="接通量 " align="center " show-overflow-tooltip>
                <template slot-scope="scope ">
                    <span>{{ scope.row.result }}</span>
                </template>
            </el-table-column>
            <el-table-column label="预订购A " align="center " show-overflow-tooltip>
                <template slot-scope="scope ">
                    <span>{{ scope.row.result }}</span>
                </template>
            </el-table-column>
            <el-table-column label="开通成功B " align="center " show-overflow-tooltip>
                <template slot-scope="scope ">
                    <span>{{ scope.row.result }}</span>
                </template>
            </el-table-column>
            <el-table-column label="开通失败C " align="center " show-overflow-tooltip>
                <template slot-scope="scope ">
                    <span>{{ scope.row.result }}</span>
                </template>
            </el-table-column>
            <el-table-column label="待开D " align="center " show-overflow-tooltip>
                <template slot-scope="scope ">
                    <span>{{ scope.row.result }}</span>
                </template>
            </el-table-column>
            <el-table-column label="退单E " align="center " show-overflow-tooltip>
                <template slot-scope="scope ">
                    <span>{{ scope.row.result }}</span>
                </template>
            </el-table-column>
            <el-table-column label="A-B-C-D " align="center " show-overflow-tooltip>
                <template slot-scope="scope ">
                    <span>{{ scope.row.result }}</span>
                </template>
            </el-table-column>
            <el-table-column label="实开B-E " align="center " show-overflow-tooltip>
                <template slot-scope="scope ">
                    <span>{{ scope.row.result }}</span>
                </template>
            </el-table-column>
            <el-table-column label="回呼 " align="center " show-overflow-tooltip>
                <template slot-scope="scope ">
                    <span>{{ scope.row.result }}</span>
                </template>
            </el-table-column>
            <el-table-column label="打回 " align="center " show-overflow-tooltip>
                <template slot-scope="scope ">
                    <span>{{ scope.row.result }}</span>
                </template>
            </el-table-column>
            <el-table-column label="绩效 " align="center " show-overflow-tooltip>
                <template slot-scope="scope ">
                    <el-button type="text ">查看</el-button>
                </template>
            </el-table-column>
        </el-table>
        <div style="float:right; ">
            <el-pagination class="layout-pagination " @size-change="handleSizeChange " @current-change="handleCurrentChange " :current-page="pageInfo.number " :page-sizes="pageInfo.sizeArray " :page-size="pageInfo.currentSize " layout="total, sizes, prev, pager, next, jumper " :total="pageInfo.totalElements ">
            </el-pagination>
        </div>
        <div style="clear:both;"></div>
    </div>
</template>

<script>
export default {
    components: {},
    props: {},
    data() {
        return {
            form: {},
            tableData: [],
            pageInfo: {
                currentSize: 10,
                sizeArray: [10, 20, 50, 100],
                number: 1,
                totalElements: 0
            },
            isLoading: false
        };
    },

    created() {},

    methods: {
        /**
         * 获取基础产品列表
         */
        getBaseProductListFromWeb() {
            this.isLoading = true;
            let that = this;
            this.$baseProductRequest.getBaseProductList({
                page: this.pageInfo.number - 1,
                size: this.pageInfo.currentSize,
                baseProduct: this.formInline,
                onResult(isSuccess, data) {
                    if (isSuccess) {
                        that.handleGetBaseProductListSuccessResponse(data);
                    } else {
                    }
                    that.isLoading = false;
                }
            });
        },
        handleGetBaseProductListSuccessResponse(data) {
            this.handlePageInfo(data);
            this.baseProductList = data.body;
        },
        /**
         * 将page 数据赋值
         */
        handlePageInfo(data) {
            this.pageInfo.totalElements = data.page.totalElements;
            console.log(this.pageInfo);
        },
        /**
         * 改变了每页数据
         */
        handleSizeChange(val) {
            console.log(`每页 ${val} 条`);
            this.pageInfo.currentSize = val;
            this.getBaseProductListFromWeb();
        },
        /**
         * 处理当前的改变
         */
        handleCurrentChange(val) {
            console.log(`当前页: ${val}`);
            this.pageInfo.number = val;
            this.getBaseProductListFromWeb();
        },
        /**
         * 重置 pageInfo
         */
        resetPageInfo() {
            this.pageInfo.currentSize = this.pageInfo.sizeArray[0];
            this.pageInfo.number = 1;
        }
    },

    watch: {}
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
#info span{
    color:#108EE9;
}
</style>



