// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import router from './router'
import Vuex from 'vuex'
import store from '@/store/store.js'
import ElementUI from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css'
import echarts from "echarts"

import App from './App'
import {
  LANGUAGE
} from '@/assets/js/Language'
import VueI18n from 'vue-i18n'
import RequestHelper from '@/assets/js/RequestHelper'
import ContentRequestHelper from '@/business/content/util/RequestHelper.js'
import CategoryRequestHelper from "@/business/category/util/RequestHelper.js"
import ProductRequestHelper from "@/business/product/util/RequestHelper.js";
import CallRecordRequestHelper from "@/business/callRecord/util/RequestHelper.js";
import BaseProductRequestHelper from "@/business/baseproduct/util/RequestHelper.js";
import UserRecordRequestHelper from "@/business/userManage/util/RequestHelper.js";
import ActivityRequestHelper from "@/business/customerServiceMission/util/RequestHelper.js";
import OrderRequestHelper from "@/business/orderRecord/util/RequestHelper.js";
import OrderApplyRequestHelper from "@/business/userOrderApply/util/RequestHelper.js";
import BlackListRequestHelper from "@/business/blacklist/util/RequestHelper.js";
import TeamManageRequestHelper from "@/business/teamManage/util/RequestHelper.js";
import Common from '@/assets/js/Common'
import './permission'
import './icons'
import VueVideoPlayer from 'vue-video-player'
// require videojs style
import 'video.js/dist/video-js.css'
// videojs
import videojs from 'video.js'
window.videojs = videojs
// hls plugin for videojs6
require('videojs-contrib-hls/dist/videojs-contrib-hls.js')
require('vue-video-player/src/custom-theme.css');


// import './styles/index.scss' // global css

Vue.use(ElementUI, {
  i18n: (key, value) => i18n.t(key, value)
})


Vue.config.productionTip = false

Vue.use(VueI18n);
Vue.use(LANGUAGE);
Vue.use(RequestHelper);
Vue.use(Common);
Vue.use(ContentRequestHelper);
Vue.use(CategoryRequestHelper);
Vue.use(ProductRequestHelper);
Vue.use(CallRecordRequestHelper);
Vue.use(BaseProductRequestHelper);
Vue.use(UserRecordRequestHelper);
Vue.use(ActivityRequestHelper);
Vue.use(OrderRequestHelper);
Vue.use(OrderApplyRequestHelper);
Vue.use(BlackListRequestHelper);
Vue.use(TeamManageRequestHelper);

Vue.use(VueVideoPlayer);

const i18n = new VueI18n({
  locale: 'zh_CN', // 默认值
  messages: LANGUAGE,
})


/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  i18n,
  template: '<App/>',
  components: {
    App
  }
})


