import Vue from 'vue'
import CONFIG from '@/assets/js/Constant'
import store from '@/store/store.js'

var COMMON = {
  TYPE_VIDEO_BG: "background_img",

  CONTENT_TYPE: [{
      key: "movie",
      value: "电影"
    },
    {
      key: "drama",
      value: "电视剧"
    },
    {
      key: "variety",
      value: "综艺"
    },
    {
      key: "animation",
      value: "动漫"
    },
    {
      key: "music",
      value: "音乐"
    },
    {
      key: "sports",
      value: "体育"
    },
    {
      key: "documatry",
      value: "纪实"
    },
    {
      key: "game",
      value: "游戏"
    },
  ],

  /**
   * 获取当前年份
   */
  getCurrentYear() {
    return new Date().getFullYear();
  },

  /**
   * 通过语言获取图片
   * @pictureinfo json字符串 图片
   * @defaultImg 默认图片
   */
  initPictureByLang(contentItem) {
    if (contentItem == null || contentItem.pictureInfo == undefined || this.isBlank(contentItem.pictureInfo)) {
      return;
    }
    let pictureList = JSON.parse(contentItem.pictureInfo);
    if (pictureList == null || pictureList.length == 0) {
      return;
    }
    for (let pictureItem of pictureList) {
      if (this.isNotBlank(CONFIG.LANG) && this.isNotBlank(pictureItem.type)) {
        if ("poster_" + CONFIG.LANG.toLowerCase() == pictureItem.type.toLowerCase() &&
          this.isNotBlank(pictureItem.filePath)) {
          contentItem.contentIcon = pictureItem.filePath;
          contentItem.itemIcon = pictureItem.filePath;
        }
      }
    }
    // 如果图片参数为空 直接取一个不同的
    if (this.isBlank(contentItem.contentIcon) && this.isBlank(contentItem.itemIcon)) {
      for (let pictureItem of pictureList) {
        if (this.isNotBlank(CONFIG.LANG) && this.isNotBlank(pictureItem.type)) {
          if ("poster_" + CONFIG.LANG.toLowerCase() != pictureItem.type.toLowerCase() &&
            this.isNotBlank(pictureItem.filePath)) {
            contentItem.contentIcon = pictureItem.filePath;
            contentItem.itemIcon = pictureItem.filePath;
            return;
          }
        }
      }
    }
  },

  //标题国际化
  initTitleByLang(contentItem) {
    if (contentItem == null || this.isBlank(contentItem.i18nName)) {
      return;
    }
    let i18nNameList = JSON.parse(contentItem.i18nName);
    if (i18nNameList == null || i18nNameList.length == 0) {
      return;
    }
    for (let i18nItem of i18nNameList) {
      if (CONFIG.LANG == i18nItem.i18n && !this.isBlank(i18nItem.name)) {
        contentItem.contentName = i18nItem.name;
        contentItem.title = i18nItem.name;
        contentItem.contentTitle = i18nItem.name;
        break;
      }
    }
  },

  //副标题国际化
  transferSubtitleByLang(contentItem) {
    if (contentItem == null || this.isBlank(contentItem.i18nSubTitle)) {
      return;
    }

    let i18nNameList = JSON.parse(contentItem.i18nSubTitle);

    if (i18nNameList == null || i18nNameList.length == 0) {
      return;
    }

    for (let i18nItem of i18nNameList) {
      if (CONFIG.LANG == i18nItem.i18n && !this.isBlank(i18nItem.name)) {
        contentItem.subTitle = i18nItem.name;
        break;
      }
    }
  },

  //分数需要除以2(统一用contentLevel,有些地方有level)
  transferScore(contentItem) {
    if (contentItem.level != null && contentItem.level != "") {
      contentItem.contentLevel = contentItem.level;
    }
    if (contentItem.contentLevel != null && contentItem.contentLevel != "") {
      contentItem.contentLevel = parseInt(contentItem.contentLevel) / 2;
    }
  },

  //内容类型国际化
  getContentTypeByInternational(contentType) {
    //当前的实际type
    let currentType = contentType;

    //获取国际化type对照数组
    let cITypes = CONFIG.CONTENTTYPES;

    if (cITypes != null && cITypes.length > 0) {
      for (let type of cITypes) {
        if (currentType == type.value && CONFIG.LANG.toLowerCase() == type.i18n.toLowerCase()) {
          return type.label;
        }
      }
    }
    return this.transferContentType(currentType);
  },

  //内容类型国际化(防止localStorage里面不存在国际化类型数组)
  transferContentType(stringType) {
    if (CONFIG.LANG == "zh_CN") {
      for (let type of this.CONTENT_TYPE) {
        if (type.key == stringType) {
          return type.value;
        }
      }
    }
    return stringType;
  },

  /**
   * 获取销售头像
   */
  getSaleImg() {
    let saleImg = CONFIG.CONFIGINFO.result.saleImg;
    if (this.isBlank(saleImg)) {
      return CONFIG.IMG_BASEURL + "xxx.jpg?type=icon_57_57";
    } else {
      return CONFIG.IMG_BASEURL + saleImg;
    }
  },
  /**
   * 内容详情页面获取视频背景
   */
  getVideoBgFromPictureInfo(contentDetail) {
    if (contentDetail == null) {
      return "";
    }
    if (this.isBlank(contentDetail.pictureInfo)) {
      return "";
    }
    let pictureList = JSON.parse(contentDetail.pictureInfo);
    if (pictureList == null || pictureList.length == 0) {
      return "";
    }
    for (let item of pictureList) {
      if (this.TYPE_VIDEO_BG == item.type) {
        if (this.isBlank(item.filePath)) {
          return "";
        } else {
          return CONFIG.IMG_BASEURL + item.filePath;
        }
      }
    }
  },
  /**
   * banner默认图
   */
  handleContentBanner(contentItem) {
    if (contentItem.contentIcon == null || contentItem.contentIcon == "") {
      contentItem.contentIcon = "xxx.jpg?type=img_1920_520";
    }
    if (contentItem.itemIcon == null || contentItem.contentIcon == "") {
      contentItem.itemIcon = "xxx.jpg?type=img_1920_520";
    }
  },
  /**
   *
   */
  handleContentIconSuperLargeH(contentItem) {
    if (contentItem.contentIcon == null || contentItem.contentIcon == "") {
      contentItem.contentIcon = "xxx.jpg?type=img_1244_500";
    }
    if (contentItem.itemIcon == null || contentItem.contentIcon == "") {
      contentItem.itemIcon = "xxx.jpg?type=img_1244_500";
    }
  },

  /**
   * 处理大型横向图 比如banner
   */
  handleContentIconLargeH(contentItem) {
    if (contentItem.contentIcon == null || contentItem.contentIcon == "") {
      contentItem.contentIcon = "xxx.jpg?type=img_1040_343";
    }
    if (contentItem.itemIcon == null || contentItem.contentIcon == "") {
      contentItem.itemIcon = "xxx.jpg?type=img_1040_343";
    }
    //内容包列表需要
    if (contentItem.icon == null || contentItem.icon == "") {
      contentItem.icon = "xxx.jpg?type=img_1040_343";
    }
  },

  handleContentIconMidH(contentItem) {
    if (contentItem.contentIcon == null || contentItem.contentIcon == "") {
      contentItem.contentIcon = "xxx.jpg?type=img_290_146";
    }
    if (contentItem.itemIcon == null || contentItem.contentIcon == "") {
      contentItem.itemIcon = "xxx.jpg?type=img_290_146";
    }
  },

  /**
   * 处理横向小图
   */
  handleContentIconSmallH(contentItem) {
    if (contentItem.contentIcon == null || contentItem.contentIcon == "") {
      contentItem.contentIcon = "xxx.jpg?type=img_125_77";
    }
    if (contentItem.itemIcon == null || contentItem.contentIcon == "") {
      contentItem.itemIcon = "xxx.jpg?type=img_125_77";
    }
  },

  /**
   * 处理默认的销售图片
   */
  handleContentIconForIcon(contentItem) {
    if (contentItem.contentIcon == null || contentItem.contentIcon == "") {
      contentItem.contentIcon = "xxx.jpg?type=icon_57_57";
    }
    if (contentItem.itemIcon == null || contentItem.contentIcon == "") {
      contentItem.itemIcon = "xxx.jpg?type=icon_57_57";
    }
  },

  /**
   * 处理竖向图
   */
  handleContentNormalV(contentItem) {
    if (contentItem.contentIcon == null || contentItem.contentIcon == "") {
      contentItem.contentIcon = "xxx.jpg?type=img_280_370";
    }
    if (contentItem.itemIcon == null || contentItem.contentIcon == "") {
      contentItem.itemIcon = "xxx.jpg?type=icon_280_370";
    }
  },

  handlecontentIcon(contentItem) {
    if (contentItem.contentIcon == null || contentItem.contentIcon == "") {
      contentItem.contentIcon = "xxx.jpg";
    }
    if (contentItem.itemIcon == null || contentItem.contentIcon == "") {
      contentItem.itemIcon = "xxx.jpg";
    }
  },

  /**
   * 将时间格式化
   */
  getCreateTime(longTime) {
    var date = new Date(longTime);
    var time =
      date.getFullYear() + "-" + (date.getMonth() + 1) + "-" + date.getDate();
    return time;
  },
  trim(str) { //删除左右两端的空格
    return str.replace(/(^\s*)|(\s*$)/g, "");
  },
  ltrim(str) { //删除左边的空格
    return str.replace(/(^\s*)/g, "");
  },
  rtrim(str) { //删除右边的空格
    return str.replace(/(\s*$)/g, "");
  },
  isBlank(str) { //是否为空
    if (str == '' || typeof (str) == 'undefined' || str == null) {
      return true;
    }
    return false;
  },
  isNotBlank(str) { //是否为非空
    var result = this.isBlank(str);
    return !result;
  },
  isMobile(str) { //是否为手机格式
    var pattern = new RegExp('^\\+?\\d{1,31}$');
    return pattern.test(str);
  },
  minLength(str, len) {
    if (str.length < len) {
      return false;
    }
    return true;
  },
  maxLength(str, len) {
    if (str.length > len) {
      return false;
    }
    return true;
  },

  /**
   * 生成指定随机数
   */
  randomNum(minNum, maxNum) {
    switch (arguments.length) {
      case 1:
        return parseInt(Math.random() * minNum + 1, 10);
        break;
      case 2:
        return parseInt(Math.random() * (maxNum - minNum + 1) + minNum, 10);
        break;
      default:
        return 0;
        break;
    }
  },

  /**
   * 生成指定位数的随机数
   */
  getRandomNum(length) {
    let random = "";
    for (let i = 0; i < length; i++) {
      let randomNum = this.randomNum(0, 9);
      random = random + randomNum + "";
    }
    return random;
  },
  /**
   * 拷贝数组
   */
  copyArray(desArray, copyArray) {
    let jsonArray = JSON.stringify(desArray);
    copyArray = JSON.parse(jsonArray);
    return copyArray;
  },
  /**
   * 数组当中遍历图片
   */
  formateImgInArray(array) {
    if (!array || array.length === 0) {
      return;
    }
    for (let item of array) {
      if (!this.isBlank(item.picPath)) {
        this.$set(item, "item.picPath", store.user.imageServerUrl + item.picPath);
      }
    }
    return array;
  },

  /**
   * @currentRole 当前角色
   * @authRoles 可使用的角色 数组
   */
  authComponent(currentRole, authRoles, element, dst) {
    if (!authRoles || authRoles.length === 0) {
      console.log("authRoles数组为空");
      return;
    }
    let index = authRoles.indexOf(currentRole);
    if (index < 0) {
      console.log("没有找到当前内容");
      return;
    }
    // 若当前角色存在于可使用角色 执行dst
    dst(element);
  },

  /**
   * 组件是否合法
   */
  authComponentValid(authRoles) {
    let currentRoles = store.state.user.roles;
    if (!currentRoles || currentRoles.length === 0) {
      console.log("未获取到当前角色");
      return false;
    }
    if (!authRoles || authRoles.length === 0) {
      console.log("authRoles数组为空 直接合法");
      return true;
    }
    // 遍历角色是否合法
    for (let currentRole of currentRoles) {
      let index = authRoles.indexOf(currentRole);
      if (index >= 0) {
        console.log("找到当前的内容");
        return true;
      }  
    }
    
    // 若当前角色存在于可使用角色 执行dst
    return false;
  },

  
  /**
   * 让组件不可点击
   */
  authComponentDisabled(currentRole, authRoles, element) {
    console.log("~~~~~~authComponentDisabled~~~~~~");
    this.authComponent(currentRole, authRoles, element, (element) => {
      console.log("!!!!test!!!!!");
      console.log(element);
      element.addClass("is-disabled");
    });
  },

  /**
   * 让组件不可见
   */
  authComponentHidden(currentRole, authRoles, element) {
    this.authComponent(currentRole, authRoles, element, (element) => {
      element.css("display", "none");
    });
  }
};

function install(Vue) {
  Vue.prototype.$common = COMMON;
};

export default install;


